<template>
  <div id='bd-agent-admin'>
    <!-- TODO:自定义智能机器人欢迎语、客服账号欢迎语 -->
    <!-- TODO: 增加搜索功能 -->
    <!-- FIXME: 当从工作组页面切换回来之后，数据显示第一页，然后分页显示其他页数 -->
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
    <el-button size="mini" type="primary" style="float: left;" @click="addAgentDialog()">添加客服账号</el-button>
    <el-input size="mini" v-model="realName" placeholder="客服真名" style="width: 120px; margin-left: 10px;" @input="handleQueryChange" clearable></el-input>
    <el-button size="mini" type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="handleQuery">查询</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="agents.totalElements">
    </el-pagination>
    <el-table stripe v-loading="loading" size="mini" height="calc(100% - 80px)" :data="agents.content" style="width: 100%">
      <el-table-column type="index" width="35"></el-table-column>
      <el-table-column prop="subDomain" label="企业号" width="100"></el-table-column>
      <el-table-column prop="uid" label="唯一ID(uId)" width="125"></el-table-column>
      <el-table-column prop="username" label="用户名(用于登录)" width="155">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" height="15px" width="15px"/>
          <span>{{ username(scope.row, scope.row.username) }}</span>
          <span v-if="isSelf(scope.row.username)">
            <el-tag size="mini" type="danger">当前账号</el-tag>
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="realName" label="真名(后台展示)"></el-table-column>
      <el-table-column prop="nickname" label="昵称(对话页面展示)"></el-table-column>
      <el-table-column prop="maxThreadCount" label="最大接待数">
        <template slot-scope="scope">
          <span v-if="scope.row.robot">不限</span>
          <span v-else>{{ scope.row.maxThreadCount }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="robot" label="机器人">
        <template slot-scope="scope">
          <span v-if="scope.row.robot">是</span>
          <span v-else>否</span>
        </template>
      </el-table-column>
      <el-table-column prop="enabled" label="在职">
        <template slot-scope="scope">
          <span v-if="scope.row.enabled">是</span>
          <span v-else>否</span>
        </template>
      </el-table-column>
      <el-table-column prop="acceptStatus" label="接待状态" v-if="showAuthority.debug">
        <template slot-scope="scope">
          <span>{{ scope.row.acceptStatus }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="connectionStatus" label="在线状态" v-if="showAuthority.debug">
        <template slot-scope="scope">
          <span>{{ scope.row.connectionStatus }}</span>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="workGroups" label="工作组">
        <template slot-scope="scope">
          <span v-if="scope.row.robot">全部工作组</span>
          <span v-else>
            <el-tag v-for="workGroup in scope.row.workGroups" :key="workGroup.id" size="mini" style="margin-right:5px;">{{ workGroup.nickname }}</el-tag>
          </span>
        </template>
      </el-table-column> -->
      <!-- <el-table-column prop="roles" label="角色">
        <template slot-scope="scope">
          <el-tag v-for="role in scope.row.roles" :key="role.id" size="mini" style="margin-right:5px;">{{ role.name }}</el-tag>
        </template>
      </el-table-column> -->
      <el-table-column fixed="right" label="操作" width="350">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="handleView(scope.$index, scope.row)">模拟对话</el-button>
          <el-button v-if="!scope.row.robot" size="mini" type="primary" @click="handleCode(scope.$index, scope.row)">获取代码</el-button>
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="showDeleteButton(scope.row)" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="agents.totalElements">
    </el-pagination>
    <el-dialog
      title="添加客服账号"
      :visible.sync="dialogVisible"
      width="35%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="登录用户名" prop="username">
          <el-input v-model="form.username" placeholder="用于登录客服后台" :disabled="isSelf(form.username)"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="form.password" auto-complete="off" placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item label="头像" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="真名" prop="realName">
          <el-input v-model="form.realName" placeholder="仅在管理后台显示"></el-input>
        </el-form-item>
        <el-form-item label="昵称" prop="name">
          <el-input v-model="form.nickname" placeholder="访客端显示"></el-input>
        </el-form-item>
        <el-form-item label="最大接待数" prop="maxThreadCount">
          <el-input-number v-model="form.maxThreadCount" @change="handleMaxThreadCountChange" :min="1" :max="1000" label="描述文字"></el-input-number>
        </el-form-item>
        <!-- <el-form-item label="所属工作组" prop="workGroups">
          <el-tag v-for="workGroup in form.workGroups" :key="workGroup.id" size="mini" style="margin-right:5px;" closable :disable-transitions="false" @close="handleRemoveTag(workGroup)">{{ workGroup.nickname }}</el-tag>
          <el-autocomplete size="mini"
            v-if="searchWorkGroupVisible"
            v-model="searchWorkGroup"
            :fetch-suggestions="querySearchWorkGroupAsync"
            ref="saveWorkGroupInput"
            placeholder="请输入内容"
            @select="handleSelectWorkGroup">
          </el-autocomplete>
          <el-button v-else size="mini" @click="showSearchWorkGroup">添加工作组</el-button>
        </el-form-item>
        <el-form-item label="角色" prop="role">
          <el-radio v-model="form.role" v-for="role in roles" :key="role.id" :label="role.value" :disabled="roleRadioDisabled(role.value)">{{ role.name }}</el-radio>
        </el-form-item> -->
        <el-form-item label="在职" prop="enabled">
          <el-radio v-model="form.enabled" :label="true">是</el-radio>
          <el-radio v-model="form.enabled" :label="false">否</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" size="mini" @click="addAgent()">{{ dialogTitle }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import * as https from '@/apis/http'
import bus from '@/util/eventbus'

export default {
  name: 'bd-agent-admin',
  data () {
    return {
      realName: '',
      searchWorkGroupVisible: false,
      searchWorkGroup: '',
      loading: false,
      pageSize: 20,
      currentPage: 1,
      isEdit: false,
      dialogVisible: false,
      allWorkGroups: [],
      form: {
        id: 0,
        username: '',
        password: '',
        email: '',
        avatar: constants.DEFAULT_AVATAR,
        realName: '',
        nickname: '',
        maxThreadCount: 10,
        workGroups: [],
        role: '',
        robot: false,
        enabled: true
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 5, message: '长度最少5个字符', trigger: 'blur' }
          // { validator: validateUsername, trigger: 'blur' }
        ],
        // password: [
        //   { required: true, message: '请输入密码', trigger: 'blur' }
        // ],
        email: [
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'change' }
        ],
        realName: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        nickname: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ],
        workGroups: [
          { type: 'array', required: true, message: '请至少选择一个工作组', trigger: 'change' }
        ],
        role: [
          { required: true, message: '请选择角色', trigger: 'change' }
        ]
      },
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL,
      isIp: constants.IS_IP
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'accessToken', 'agents', 'workGroups', 'roles', 'showAuthority' ]),
    dialogTitle () {
      return this.isEdit ? '更新' : '创建'
    },
    mainHost () {
      return document.domain.split('.').slice(-2).join('.') + (location.port === '8081' ? ':8000' : '')
    }
  },
  methods: {
    ...mapActions([ 'createAgent', 'updateAgent', 'deleteAgent', 'getAgents', 'filterAgents', 'getAllWorkGroups' ]),
    username (user, usernameAtSubDomain) {
      if (this.isSelf(user.username)) {
        return usernameAtSubDomain
      } else if (user.robot) {
        return '机器人'
      } else if (user.super) {
        return usernameAtSubDomain
      } else if (user.agent || user.workGroupAdmin) {
        return usernameAtSubDomain.split('@')[0]
      } else {
        return usernameAtSubDomain
      }
    },
    roleRadioDisabled (value) {
      return this.form.robot || value === constants.ROLE_ROBOT
    },
    isSelf (username) {
      return username === this.userInfo.username
    },
    showDeleteButton (row) {
      return row.username !== this.userInfo.username && !row.robot
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809171923191')
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    querySearchWorkGroupAsync (queryString, cb) {
      console.log(queryString)
      var allWorkGroups = this.allWorkGroups
      var results = queryString ? allWorkGroups.filter(this.createStateFilter(queryString)) : allWorkGroups

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelectWorkGroup (item) {
      console.log(item)
      let contains = this.form.workGroups.some(wg => {
        return wg.id === item.object.id
      })
      if (!contains) {
        this.form.workGroups.push(item.object)
      }
      //
      this.searchWorkGroupVisible = false
      this.searchWorkGroup = ''
    },
    showSearchWorkGroup () {
      this.searchWorkGroupVisible = true
      this.$nextTick(_ => {
        this.$refs.saveWorkGroupInput.$refs.input.focus()
      })
    },
    handleRemoveTag (workGroup) {
      console.log(workGroup)
      this.form.workGroups.splice(this.form.workGroups.indexOf(workGroup), 1)
    },
    handleMaxThreadCountChange (value) {
      // console.log(value)
    },
    handleSizeChange (val) {
      // console.log(val)
      this.pageSize = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getAgents(payload)
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.currentPage = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getAgents(payload)
    },
    addAgentDialog () {
      this.isEdit = false
      //
      this.form.id = ''
      this.form.username = ''
      this.form.password = ''
      this.form.email = ''
      this.form.avatar = constants.DEFAULT_AVATAR
      this.form.realName = ''
      this.form.nickname = ''
      this.form.maxThreadCount = 10
      this.form.workGroups = []
      this.form.role = ''
      this.form.robot = false
      this.form.enabled = true
      //
      this.dialogVisible = true
    },
    transformWorkGroups () {
      // if (this.isEdit) {
      let workGroups = this.form.workGroups
      this.form.workGroups = []
      for (let i = 0; i < workGroups.length; i++) {
        const workGroup = workGroups[i]
        this.form.workGroups.push(workGroup.wid)
      }
      // }
    },
    addAgent () {
      this.transformWorkGroups()
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          if (this.isEdit) {
            this.updateAgent(this.form)
          } else {
            this.createAgent(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleQueryChange (value) {
      // console.log(value)
      if (value.trim().length === 0) {
        this.loading = true
        let payload = { page: 0, size: 20 }
        this.getAgents(payload)
      }
    },
    handleQuery () {
      // console.log(this.realName)
      if (this.realName === '') {
        let payload = { page: 0, size: 20 }
        this.getAgents(payload)
      } else {
        let payload = {page: 0, size: 20, realName: this.realName}
        this.filterAgents(payload)
      }
    },
    handleView (index, row) {
      // console.log(row)
      if (this.isIp) {
        window.open(constants.CHAT_BASE_URL + '?uid=' + this.userInfo.uid + '&wid=&type=appointed&aid=' + row.uid + '&ph=ph')
      } else {
        window.open(constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/chat?uid=' + this.userInfo.uid + '&wid=&type=appointed&aid=' + row.uid + '&ph=ph')
      }
    },
    handleCode (index, row) {
      // console.log(row)
      bus.$emit(constants.EVENT_BUS_AGENT_CODE, row)
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.isEdit = true
      this.form.id = row.id
      if (row.username !== this.userInfo.username) {
        this.form.username = row.username.split('@')[0]
      } else {
        this.form.username = row.username
      }
      this.form.password = ''
      this.form.email = row.email
      this.form.avatar = row.avatar
      this.form.realName = row.realName
      this.form.nickname = row.nickname
      this.form.maxThreadCount = row.maxThreadCount
      this.form.email = row.email
      this.form.mobile = row.mobile
      this.form.robot = row.robot
      this.form.enabled = row.enabled
      // this.form.workGroups = row.workGroups
      // checkbox专用
      // this.form.workGroups = []
      // for (let i = 0; i < row.workGroups.length; i++) {
      //   const workGroup = row.workGroups[i]
      //   this.form.workGroups.push(workGroup)
      // }
      // TODO: 是否应该使用数组？
      // let roleValue = ''
      // for (let i = 0; i < row.roles.length; i++) {
      //   const role = row.roles[i]
      //   if (role.value !== constants.ROLE_FREE) {
      //     roleValue = role.value
      //   }
      // }
      // if (row.robot) {
      //   roleValue = constants.ROLE_ROBOT
      // }
      // this.form.role = roleValue
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteAgent(row.id)
      }).catch(() => {
        //
      })
    }
  },
  mounted () {
    https.getAllWorkGroups(this.accessToken, response => {
      console.log(response)
      for (let i = 0; i < response.data.length; i++) {
        const element = response.data[i]
        this.allWorkGroups.push({ 'value': element.nickname, 'object': element })
      }
    }, error => {
      console.log(error)
    })
    //
    bus.$on(constants.EVENT_BUS_AGENT_LOAD_FINISHED, message => {
      this.loading = false
    })
  }
}
</script>

<style scoped>
#bd-agent-admin {
  height: 100%;
  text-align: left;
}
</style>



