<template>
    <div id='bd-settings'>
      <el-row :gutter="10">
        <el-col :span="3" id="left-col">
          <el-menu class="el-menu-vertical-demo" style="height: 100%;"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b"
            :router=true>
            <el-menu-item index="/admin/setting/app" v-if="showAuthority.admin">
              <span slot="title" style="margin-left: 5px;">应用管理</span>
            </el-menu-item>
            <el-menu-item index="/admin/setting/agent" v-if="showAuthority.admin">
              <span slot="title" style="margin-left: 5px;">客服管理</span>
            </el-menu-item>
            <!-- <el-menu-item index="/admin/setting/internal" v-if="showAuthority.admin">
              <span slot="title" style="margin-left: 5px;">协同管理</span>
            </el-menu-item> -->
            <!-- <el-menu-item index="/admin/setting/robot" v-if="showAuthority.admin">
              <span slot="title" style="margin-left: 5px;">智能客服</span>
            </el-menu-item> -->
            <el-menu-item index="/admin/setting/cuw" v-if="showAuthority.admin">
              <span slot="title" style="margin-left: 5px;">常用语管理</span>
            </el-menu-item>
            <el-menu-item index="/admin/setting/taboo" v-if="showAuthority.debug">
              <span slot="title" style="margin-left: 5px;">敏感词管理</span>
            </el-menu-item>
            <el-menu-item index="/admin/setting/profile">
              <span slot="title" style="margin-left: 5px;">个人资料</span>
            </el-menu-item>
            <el-menu-item index="/admin/setting/pay" v-if="showAuthority.debug">
              <span slot="title" style="margin-left: 5px;">费用明细</span>
            </el-menu-item>
            <el-menu-item index="/admin/setting/preference" v-if="showAuthority.debug">
              <span slot="title" style="margin-left: 5px;">常用设置</span>
            </el-menu-item>
          </el-menu>
        </el-col>
        <el-col :span="21" id="right-col">
          <transition mode="out-in" enter-active-class="fadeIn" leave-active-class="fadeOut" appear>
            <router-view class="animated"></router-view>
          </transition>
        </el-col>
      </el-row>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'bd-settings',
  computed: {
    ...mapGetters([ 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'changeCurrentMainView' ])
  },
  mounted () {
    this.changeCurrentMainView('setting')
  }

}
</script>

<style scoped>
#bd-settings {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
}
#right-col{
  height: 100%;
}
</style>



