import * as constants from '@/util/constants'
import bus from '@/util/eventbus'
// axios 配置
axios.defaults.timeout = 15000
axios.defaults.headers.post['Content-Type'] = 'application/json'
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest'
// axios.defaults.headers.common['Accept'] = 'application/json'
// axios.defaults.headers.common['Authorization'] = 'bearer ' + localStorage.access_token
// axios.defaults.withCredentials = false

// for 测试 debug
axios.defaults.baseURL = constants.AXIOS_DEFAULT_BASEURL

// http response 拦截器
axios.interceptors.response.use(
  response => {
    return response
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 400:
          // TODO: 修改登录错误提示为：密码错误
          console.log('axios interception error 400')
          bus.$emit(constants.EVENT_BUS_LOGIN_ERROR_400, '400')
          break
        case 401:
          // FIXME: 401报错自动清理本地存储access_token, 然后重新获取access_token
          console.log('axios interception error 401')
          bus.$emit(constants.EVENT_BUS_LOGOUT, '401')
          break
        case 403:
          // TODO: 通过refresh_token获取最新access_token?
          // 403 无权限，跳转到首页
          console.log('axios interception error 403')
          bus.$emit(constants.EVENT_BUS_LOGOUT, '403')
          break
        case 500:
          // TODO: 服务器错误
          console.log('axios interception error 500')
          bus.$emit(constants.EVENT_BUS_SERVER_ERROR_500, '500')
          break
      }
    }
    return 'return axios interception error'
  })

/**
 * 注册页面：验证邮箱唯一性
 * @param {*} email 邮箱
 * @param {*} successcb 成功回调
 * @param {*} failedcb 失败回调
 */
export function emailValidate (email, successcb, failedcb) {
  axios.get('/visitor/api/email/validate', {
    params: {
      'email': email
    }
  }).then(response => {
    successcb(response.data)
  }).catch(error => {
    failedcb(error)
  })
}

