  /**
   * 登录成功后，持久化token
   */
  [types.PERSIST_PASSPORT_TOKEN] (state, { token }) {
    state.passport.token = token
    localStorage.access_token = token.access_token
    localStorage.setItem(types.LOCALSTORAGE_PASSPORT_TOKEN_KEY, JSON.stringify(token))
  },

  /**
   * 退出登录，清空本地登录状态token
   */
  [types.LOGOUT] (state) {
    localStorage.removeItem('access_token')
    localStorage.removeItem(types.LOCALSTORAGE_PASSPORT_TOKEN_KEY)
    state.passport.token.access_token = ''
  },

  /**
   * 添加订阅
   */
  [types.SUBSCRIBE_TOPIC] (state, { topic, subscription }) {
    let contains = state.user.subscribed_topics.some(object => {
      return topic === object.topic
    })
    if (!contains) {
      let object = { 'topic': topic, 'subscription': subscription }
      state.user.subscribed_topics.push(object)
    }
  },

  /**
   * 取消订阅
   */
  [types.UNSUBSCRIBE_TOPIC] (state, { topic }) {
    for (let i = state.user.subscribed_topics.length - 1; i >= 0; i--) {
      const object = state.user.subscribed_topics[i]
      if (object.topic === topic) {
        state.user.subscribed_topics.splice(i, 1)
      }
    }
  },

  /**
   * 清空订阅
   */
  [types.CLEAR_TOPIC] (state) {
    state.user.subscribed_topics = []
  },

  /**
   * 修改当前主view
   */
  [types.CHANGE_CURRENT_MAIN_VIEW] (state, { view }) {
    state.current_main_view = view
  }
