<template>
  <!-- TODO: 增加table loading效果 -->
  <!-- TODO: 支持自定义富文本：文字颜色、字体、字号、图片 -->
  <div id='bd-cuw-admin'>
    <el-row>
      <el-col :span="5" id="left-col">
        <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 15px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
        <el-button size="mini" type="success" style="float: left; margin-bottom: 15px;" @click="addCuw">添加常用语</el-button>
        <!-- <el-button size="mini" type="primary" icon="el-icon-upload2" style="float: left; margin-bottom: 10px;">导入</el-button> -->
        <el-input size="mini" placeholder="搜索类别" v-model="filterText"></el-input>
        <el-tree class="filter-tree"
          ref="cateTree"
          :empty-text="emptyText"
          :data="companyCuwCategories"
          :props="defaultProps"
          :filter-node-method="filterNode"
          @node-click="handleNodeClick">
        </el-tree>
        <el-button size="mini" type="success" @click="addCategory">添加</el-button>
        <el-button size="mini" type="primary" @click="editCategory" :disabled="editButtonDisabled">编辑</el-button>
        <el-button size="mini" type="danger" @click="removeCategory" :disabled="deleteButtonDisabled">删除</el-button>
      </el-col>
      <el-col :span="19" id="right-col">
        <el-table stripe size="mini" height="calc(100% - 80px)"
          :data="companyCuws" style="width: 100%; text-align: left;">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="name" label="标题"></el-table-column>
          <el-table-column prop="content" label="内容"></el-table-column>
          <el-table-column prop="count" label="使用次数" sortable></el-table-column>
          <el-table-column fixed="right" label="操作" width="150">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <el-dialog
      title="添加常用语"
      :visible.sync="addCuwDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="标题" prop="name">
          <el-input v-model="form.name" placeholder="标题"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 20}" v-model="form.content"></el-input>
        </el-form-item>
        <el-form-item label="所属分类" prop="categoryId" v-if="!isEdit">
          <span v-if="companyCuwCategories.length === 1">请首先创建分类</span>
          <el-radio-group v-model="form.categoryId">
            <el-radio v-if="category.id > 0" v-for="category in companyCuwCategories" :key="category.id" :label="category.id">
              {{ category.name }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCuwDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doAddCuw()">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-cuw-admin',
  data () {
    return {
      emptyText: '暂无分组',
      currentCategory: {
        id: 0,
        name: '',
        cuwChildren: []
      },
      //
      pageSize: 20,
      currentPage: 1,
      //
      isEdit: false,
      inputVisible: false,
      inputValue: '',
      addCuwDialogVisible: false,
      setRobotNameDialogVisible: false,
      filterText: '',
      defaultProps: {
        // children: 'cuwChildren',
        label: 'name'
      },
      form: {
        id: 0,
        name: '',
        content: '',
        categoryId: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入标题', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请输入内容', trigger: 'blur' }
        ],
        categoryId: [
          { required: true, message: '请至少选择一个类别', trigger: 'change' }
        ]
      },
      formRobot: {
        robotName: ''
      }
    }
  },
  computed: {
    ...mapGetters([ 'companyCuwCategories', 'companyCuws' ]),
    editButtonDisabled () {
      return false
    },
    deleteButtonDisabled () {
      return false
    }
  },
  watch: {
    filterText (val) {
      this.$refs.cateTree.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'updateCategory', 'deleteCategory',
      'getCompanyCuws', 'currentCompanyCuwCategoryId', 'getChildrenCuws', 'getCuws', 'cuwCreate', 'cuwUpdate', 'cuwDelete' ]),
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809252249331')
    },
    rateTotal (row) {
      return row.rateHelpful + row.rateUseless
    },
    handleNodeClick (data) {
      console.log(data)
      //
      this.currentCategory.id = data.id
      this.currentCategory.name = data.name
      // this.currentCategory.children = data.children
      this.getChildrenCuws(data.id)
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
    },
    addCuw () {
      this.isEdit = false
      this.form.id = 0
      this.form.name = ''
      this.form.content = ''
      this.form.categoryId = ''
      //
      this.addCuwDialogVisible = true
    },
    addCategory () {
      this.$prompt('请输入分组', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        // console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: 0,
            name: value,
            type: constants.CATEGORY_TYPE_CUW_COMPANY
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // 取消
      })
    },
    editCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('请选择要编辑的类别')
      } else {
        this.$prompt('编辑类别', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputValue: this.currentCategory.name
        }).then(({ value }) => {
          // console.log(value)
          if (value !== undefined && value !== null && value.length > 0) {
            let payload = {
              category_id: this.currentCategory.id,
              name: value
            }
            this.updateCategory(payload)
          } else {
            this.$message.error('不能为空')
          }
        }).catch(() => {
          // 取消
        })
      }
    },
    removeCategory () {
      if (this.currentCategory.id === 0) {
        this.$message.error('不允许删除')
        return
      }
      if (this.companyCuws.length > 0) {
        this.$message.error('请首先删除分类内容')
        return
      }
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteCategory(this.currentCategory.id)
        // this.cuwDelete(this.currentCategory.id)
        this.currentCategory.id = 0
      }).catch(() => {
      })
    },
    doAddCuw () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.addCuwDialogVisible = false
          if (this.isEdit) {
            //
            let payload = { id: this.form.id, name: this.form.name, content: this.form.content }
            this.cuwUpdate(payload)
          } else {
            //
            let payload = { categoryId: this.form.categoryId, name: this.form.name, content: this.form.content }
            this.cuwCreate(payload)
          }
        } else {
          return false
        }
      })
    },
    handleEdit (index, row) {
      this.isEdit = true
      // console.log(index, row)
      this.form.id = row.id
      this.form.name = row.name
      this.form.content = row.content
      // FIXME: 直接在编辑对话框修改分组
      // this.form.categoryId = row.category.id
      //
      this.addCuwDialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 删除
        this.cuwDelete(row.id)
      }).catch(() => {
        //
      })
    }
  },
  created () {
    this.getCategories()
    this.getCuws()
  }
}
</script>

<style scoped>
#bd-cuw-admin {
  height: 100%;
  /* padding-top: 5px; */
}
.el-row {
  height: 100%;
  padding-top: 5px;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
}
#right-col{
  height: 100%;
}
.filter-tree {
  margin-top: 10px;
  margin-bottom: 10px;
  height: calc(100% - 130px);
  overflow-y: auto;
}
.el-radio+.el-radio {
  margin-left: 1px;
}
/* .el-radio__label {
  padding-left: 5px !important;
  padding-right: 5px !important;
} */
</style>



