<template>
  <div id='bd-footer'>
    <span v-if="connecting" style="float: left;"><i class="el-icon-loading"></i></span>
    <!-- <img v-if="connecting" :src="connectingImage" class="bd-image" /> -->
    <img v-if="connected" :src="connectedImage" class="bd-image" />
    <img v-if="disconnected" :src="disconnectedImage" class="bd-image" />
    <div class="bd-status">
      <!-- <span v-if="initializing" style="color: blue;">初始化中...</span> -->
      <span v-if="connecting" style="color: blue;">连接中...</span>
      <span v-if="connected" style="color: green;">连接成功</span>
      <span v-if="disconnected" style="color: red;">连接断开</span>
      <!-- TODO: 接待人数, 客服排名, 在线时长 -->
    </div>
    <div class="pull-right">
      <a style="cursor: pointer;" @click="gotoHomeUrl" >{{ title }} &copy; 2013 ~ {{ currentYear }}</a>
      <span v-if="showFeedback" @click="onFeedback" style="cursor: pointer;"><i class="iconfont icon-face bd-help"></i> 反馈</span>
      <span @click="onChat" style="cursor: pointer;"><i class="iconfont icon-chat bd-help"></i> 咨询</span>
      <span @click="onHelp" style="cursor: pointer;"><i class="iconfont icon-help bd-help"></i> 帮助</span>
    </div>

    <el-dialog
      title="意见反馈"
      :visible.sync="feedDialogVisible"
      width="30%"
      :before-close="handleClose"
      center>
      <el-rate v-model="feedRate" show-text style="height: 30px; text-align: center;"></el-rate>
      <el-input type="textarea"
        :autosize="{ minRows: 3, maxRows: 8}"
        placeholder="请输入反馈内容"
        v-model="feedContent">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="feedDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doSaveFeed">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-footer',
  data () {
    return {
      feedDialogVisible: false,
      feedContent: '',
      feedRate: 5,
      homeUrl: constants.HOME_URL,
      chatUrl: constants.CHAT_URL,
      helpUrl: constants.HELP_URL,
      title: constants.DEFAULT_TITLE,
      electron: constants.IS_ELECTRON,
      showFeedback: constants.SHOW_FEEDBACK
    }
  },
  computed: {
    ...mapGetters([ 'connectionStatus', 'showAuthority' ]),
    currentYear () {
      return new Date().getFullYear()
    },
    connecting () {
      return this.connectionStatus === constants.STOMP_CONNECTION_STATUS_CONNECTING
    },
    connected () {
      return this.connectionStatus === constants.STOMP_CONNECTION_STATUS_CONNECTED
    },
    disconnected () {
      return this.connectionStatus === constants.STOMP_CONNECTION_STATUS_DISCONNECTED
    },
    connectingImage () {
      return constants.CONNECTING_IMAGE
    },
    connectedImage () {
      return constants.CONNCTED_IMAGE
    },
    disconnectedImage () {
      return constants.DISCONNECTED_IMAGE
    }
  },
  methods: {
    gotoHomeUrl () {
      if (this.electron) {
        ipcRenderer.send('open-external-url', this.homeUrl)
      } else {
        window.open(this.homeUrl)
      }
    },
    onFeedback () {
      // console.log('onFeedback')
      this.feedDialogVisible = true
    },
    onChat () {
      if (this.electron) {
        ipcRenderer.send('open-external-url', this.chatUrl)
      } else {
        window.open(this.chatUrl)
      }
    },
    onHelp () {
      if (this.electron) {
        ipcRenderer.send('open-external-url', this.helpUrl)
      } else {
        window.open(this.helpUrl)
      }
    },
    doSaveFeed () {
      this.feedDialogVisible = false
      // TODO: 反馈内容提交数据库
      this.$message({ message: '多谢反馈', type: 'success' })
    },
    handleClose (done) {
      done()
    }
  }

}
</script>

<style scoped>
#bd-footer {
  height: 100%;
  font-size: 12px;
}
.bd-status {
  float: left;
  margin-left: 5px;
}
.pull-right {
  float: right;
  color: gray;
}
a:link { text-decoration: none; color: gray}
a:visited { color: gray; }
.bd-image {
  float:left; margin-top: 12px; height: 15px; width: 15px;
}
.bd-help {
  font-size: 12px; margin-left: 10px;
}
</style>



