<template>
  <div id='bd-thread-right'>
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <!-- TODO: 用户信息放在常用语上方，同时可见，不需要切换tab -->
      <el-tab-pane label="用户信息" name="info">
        <bd-info></bd-info>
      </el-tab-pane>
      <!-- TODO: 修改为：常用语&知识库，包含知识库的内容，不需要切换tab -->
      <el-tab-pane label="常用语" name="cuw">
        <bd-cuw></bd-cuw>
      </el-tab-pane>
      <el-tab-pane v-if="showAuthority.debug" label="知识库" name="support">
        <bd-support></bd-support>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdCuw from '@/components/dashboard/main/chat/children/thread/right/BdCuw'
import BdInfo from '@/components/dashboard/main/chat/children/thread/right/BdInfo'
import BdSupport from '@/components/dashboard/main/chat/children/thread/right/BdSupport'

export default {
  name: 'bd-thread-right',
  components: { BdCuw, BdInfo, BdSupport },
  data () {
    return {
      activeName: 'info'
    }
  },
  computed: {
    ...mapGetters([ 'userCurrentThread', 'showAuthority' ]),
    username () {
      return this.userCurrentThread.visitor.username
    }
  },
  methods: {
    ...mapActions([ 'getCuws', 'fingerPrint', 'userTag' ]),
    handleClick (tab, event) {
      if (tab.name === 'cuw') {
        // this.getCuws()
      } else if (tab.name === 'info') {
      }
    }
  },
  mounted () {
    this.getCuws()
  }
}
</script>

<style scoped>
#bd-thread-right {
  margin-left: 20px;
  margin-top: 10px;
  margin-right: 20px;
  height: 100%;
}
.el-tabs {
  height: 100%;
}
.el-tab-pane {
  padding: 0px 10px 5px 10px;
  height: calc(100% - 100px) !important;
}
.el-tabs__content {
  height: 100% !important;
  overflow-y: scroll !important;
}
</style>



