<template>
  <!-- TODO: 增加自定义样式，并可实时查看效果 -->
  <div id='bd-code-admin'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
    <el-form ref="form" :model="form" label-width="100px" style="width: 600px; margin-top: 50px;">
      <el-form-item label="样式">
        <el-radio-group v-model="form.style">
          <el-radio :label="1">独立窗口</el-radio>
          <el-radio :label="2">嵌入式</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- TODO: 可实时预览大小 -->
      <el-form-item label="icon大小" v-if="iconSizeVisible">
        <el-radio-group v-model="form.size">
          <el-radio :label="1">正常</el-radio>
          <el-radio :label="2">中等</el-radio>
          <el-radio :label="3">小</el-radio>
          <el-radio :label="4">最小</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- <el-form-item label="颜色">
        <el-color-picker v-model="form.color"></el-color-picker>
      </el-form-item> -->
      <!-- <el-form-item label="位置">
        <el-radio-group v-model="form.position">
          <el-radio :label="1">右下角</el-radio>
          <el-radio :label="2">右侧</el-radio>
        </el-radio-group>
      </el-form-item> -->
      <el-form-item label="代码">
        <el-input type="textarea" :autosize="{ minRows: 12, maxRows: 20}" :value="styledCode"></el-input>
        <span v-if="this.form.style === 1">需要以超链接的形式放在网页的任何地方</span>
        <span v-if="this.form.style === 2">{{ htmlTip }}</span>
      </el-form-item>
      <el-form-item>
        <el-button class="copyBtn" type="primary" :data-clipboard-text="styledCode" @click="onCopy">复制</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import Clipboard from 'clipboard'

export default {
  name: 'bd-code-admin',
  props: [ 'type', 'object' ],
  data () {
    return {
      form: {
        color: '#409EFF',
        style: 1,
        size: 1,
        position: 1,
        code: ''
      },
      htmlTip: '复制代码，并放在网页</body>之前',
      isIp: constants.IS_IP,
      isDxz: constants.IS_DAXUEZHANG,
      homeUrl: constants.HOME_URL
    }
  },
  computed: {
    ...mapGetters([ 'userInfo' ]),
    mainHost () {
      return document.domain.split('.').slice(-2).join('.') + (location.port === '8081' ? ':8000' : '')
    },
    styledCode () {
      if (this.form.style === 1) {
        if (this.type === 'workGroup') {
          // 工作组独立窗口
          if (this.isIp) {
            return '<a href="' + constants.CHAT_BASE_URL + '?uid=' + this.userInfo.uid + '&wid=' + this.object.wid + '&type=workGroup&aid=&ph=ph" target="_blank">在线客服</a>'
          } else {
            return '<a href="' + constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/chat?uid=' + this.userInfo.uid + '&wid=' + this.object.wid + '&type=workGroup&aid=&ph=ph" target="_blank">在线客服</a>'
          }
        } else {
          // 客服一对一对话: ' + this.object.workGroups[0].wid + '
          if (this.isIp) {
            return '<a href="' + constants.CHAT_BASE_URL + '?uid=' + this.userInfo.uid + '&wid=&type=appointed&aid=' + this.object.uid + '&ph=ph" target="_blank">在线客服</a>'
          } else {
            return '<a href="' + constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/chat?uid=' + this.userInfo.uid + '&wid=&type=appointed&aid=' + this.object.uid + '&ph=ph" target="_blank">在线客服</a>'
          }
        }
      } else {
        if (this.type === 'workGroup') {
          // 工作组嵌入式代码
          return '<!--虾婆·智能客服代码 开始 -->\n' +
            '<div id="byteDesk"></div>\n' +
            '<script type="text/javascript">\n' +
            'window.adminUid = "' + this.userInfo.uid + '";\n' +
            'window.workGroupWid = "' + this.object.wid + '";\n' +
            'window.subDomain = "' + this.userInfo.subDomain + '";\n' +
            'window.type = "workGroup";\n' +
            'window.agentUid = "";\n' +
            '(function () {\n' +
            '  d = document;\n' +
            '  s = d.createElement("script");\n' +
            '  s.src = "' + this.homeUrl + this.jsDirName + this.floatJs + '";\n' +
            '  s.async = 1;\n' +
            '  d.getElementsByTagName("head")[0].appendChild(s);\n' +
            '})();\n' +
            '<' +
            '/script>\n' +
            '<!--./虾婆·智能客服代码 结束 -->'
        } else {
          // 客服一对一对话
          return '<!--虾婆·智能客服代码 开始 -->\n' +
            '<div id="byteDesk"></div>\n' +
            '<script type="text/javascript">\n' +
            'window.adminUid = "' + this.userInfo.uid + '";\n' +
            'window.workGroupWid = "";\n' +
            'window.subDomain = "' + this.userInfo.subDomain + '";\n' +
            'window.type = "appointed";\n' +
            'window.agentUid = "' + this.object.uid + '";\n' +
            '(function () {\n' +
            '  d = document;\n' +
            '  s = d.createElement("script");\n' +
            '  s.src = "' + this.homeUrl + this.jsDirName + this.floatJs + '";\n' +
            '  s.async = 1;\n' +
            '  d.getElementsByTagName("head")[0].appendChild(s);\n' +
            '})();\n' +
            '<' +
            '/script>\n' +
            '<!--./虾婆·智能客服代码 结束 -->'
        }
      }
    },
    iconSizeVisible () {
      return this.form.style === 2
    },
    jsDirName () {
      if (this.isDxz) {
        return '/js/deploy/dxz/'
      } else {
        return '/js/'
      }
    },
    floatJs () {
      if (this.form.size === 1) {
        return 'n.js'
      } else if (this.form.size === 2) {
        return 'me.js'
      } else if (this.form.size === 3) {
        return 'sm.js'
      } else if (this.form.size === 4) {
        return 'mi.js'
      } else {
        return 'n.js'
      }
    }
  },
  methods: {
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809182325351')
    },
    onCopy () {
      console.log('submit!')
      var clipboard = new Clipboard('.copyBtn')
      clipboard.on('success', e => {
        this.$message({
          message: '复制成功',
          type: 'success'
        })
        // 释放内存
        clipboard.destroy()
      })
      clipboard.on('error', e => {
        // 不支持复制
        this.$message.error('该浏览器不支持自动复制')
        // 释放内存
        clipboard.destroy()
      })
    }
  }

}
</script>

<style scoped>
#bd-code-admin {
  height: 100%;
  overflow: auto;
  text-align: left;
}
</style>



