<template>
  <!-- TODO: 咨询前问卷和值班客服组都应该标准化 -->
  <!-- TODO: 增加搜索功能 -->
  <!-- FIXME: 当从客服页面切换回来之后，数据显示第一页，然后分页显示其他页数 -->
  <div id='bd-workGroup-admin'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
    <el-button size="mini" type="primary" style="float: left;" @click="addWorkGroupDialog()">添加技能组</el-button>
    <el-input size="mini" v-model="nickname" placeholder="技能组昵称" style="width: 120px; margin-left: 10px;" @input="handleQueryChange" clearable></el-input>
    <el-button size="mini" type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="handleQuery">查询</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="workGroups.totalElements">
    </el-pagination>
    <el-table stripe v-loading="loading" size="mini" height="calc(100% - 80px)" :data="workGroups.content" style="width: 100%">
      <el-table-column type="index" width="35"></el-table-column>
      <el-table-column type="expand" width="50">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="工作时间段:">
              <el-tag v-for="workTime in props.row.workTimes" :key="workTime.id" size="mini" style="margin-right:5px;">
                {{ workTime.startTime }} - {{ workTime.endTime }}
              </el-tag>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="wid" label="唯一ID(wId)" width="125"></el-table-column>
      <el-table-column prop="nickname" label="对话页面标题">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" height="15px" width="15px"/>
          <span>{{ scope.row.nickname }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="welcomeTip" label="默认欢迎语"></el-table-column>
      <el-table-column prop="acceptTip" label="接入欢迎语"></el-table-column>
      <el-table-column prop="offlineTip" label="离线提示语"></el-table-column>
      <!-- <el-table-column prop="routeType" label="路由类型">
        <template slot-scope="scope">
          <span>{{ routeType(scope.row.routeType) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="defaultRobot" label="默认机器人">
        <template slot-scope="scope">
          <span v-if="scope.row.defaultRobot">是</span>
          <span v-else>否</span>
        </template>
      </el-table-column>
      <el-table-column prop="offlineRobot" label="离线机器人">
        <template slot-scope="scope">
          <span v-if="scope.row.offlineRobot">是</span>
          <span v-else>否</span>
        </template>
      </el-table-column>
      <el-table-column prop="forceRate" label="强制评价">
        <template slot-scope="scope">
          <span v-if="scope.row.forceRate">是</span>
          <span v-else>否</span>
        </template>
      </el-table-column> -->
      <el-table-column prop="onDutyWorkGroup" label="值班工作组">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.onDutyWorkGroup !== null" :key="scope.row.onDutyWorkGroup.id" size="mini" style="margin-right:5px;">
            {{ scope.row.onDutyWorkGroup.nickname }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="users" label="所有成员">
        <template slot-scope="scope">
          <el-tag v-for="user in scope.row.users" :key="user.id" size="mini" style="margin-right:5px;">
            {{ user.realName }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="admin" label="客服组长">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.admin !== null" :key="scope.row.admin.uid" size="mini" style="margin-right:5px;">
            {{ scope.row.admin.realName }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="350px">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="handleView(scope.$index, scope.row)">模拟对话</el-button>
          <el-button size="mini" type="primary" @click="handleCode(scope.$index, scope.row)">获取代码</el-button>
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="workGroups.totalElements">
    </el-pagination>
    <el-dialog
      title="添加技能组"
      :visible.sync="dialogVisible"
      width="40%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="140px">
        <el-form-item label="对话页面标题" prop="nickname">
          <el-input v-model="form.nickname"></el-input>
        </el-form-item>
        <el-form-item label="LOGO" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <!-- <el-form-item label="默认机器人" prop="defaultRobot">
          <el-radio-group v-model="form.defaultRobot">
            <el-radio :label="true" disabled>开启</el-radio>
            <el-radio :label="false">关闭</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="离线机器人" prop="offlineRobot">
          <el-radio-group v-model="form.offlineRobot">
            <el-radio :label="true" disabled>开启</el-radio>
            <el-radio :label="false">关闭</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="强制评价" prop="forceRate">
          <el-radio-group v-model="form.forceRate">
            <el-radio :label="true" disabled>开启</el-radio>
            <el-radio :label="false">关闭</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="路由类型" prop="routeType">
          <el-radio-group v-model="form.routeType">
            <el-radio label="pubsub" disabled>广播</el-radio>
            <el-radio label="robin">轮询</el-radio>
            <el-radio label="recent" disabled>熟客优先</el-radio>
          </el-radio-group>
        </el-form-item> -->
        <el-form-item
          :label="'工作时间段 ' + (index+1)"
          v-for="(workTime, index) in workTimes"
          :key="workTime.id"
          :prop="'workTimes.0.value'">
          <el-time-picker
            is-range
            v-model="workTime.value"
            range-separator="至"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
            placeholder="选择时间范围">
          </el-time-picker>
          <el-button v-if="index > 0" @click="removeWorkTime(workTime.id)">删除</el-button>
          <el-button v-if="index === form.workTimes.length - 1" @click="addWorkTime()">添加</el-button>
        </el-form-item>
        <el-form-item label="进入页面欢迎语" prop="welcomeTip">
          <el-input type="textarea" v-model="form.welcomeTip"></el-input>
        </el-form-item>
        <el-form-item label="接入客服欢迎语" prop="acceptTip">
          <el-input type="textarea" v-model="form.acceptTip"></el-input>
        </el-form-item>
        <el-form-item label="离线提示" prop="offlineTip">
          <el-input type="textarea" v-model="form.offlineTip"></el-input>
        </el-form-item>
        <el-form-item label="值班工作组" prop="onDutyWorkGroupWid">
          <el-radio-group v-model="form.onDutyWorkGroupWid">
            <el-radio v-for="workGroup in filteredWorkGroups" :key="workGroup.id" :label="workGroup.wid">
              {{ workGroup.nickname }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="所有成员" prop="users">
          <el-tag v-for="user in form.users" :key="user.id" size="mini" style="margin-right:5px;" closable :disable-transitions="false" @close="handleRemoveTag(user)">{{ user.realName }}</el-tag>
          <el-autocomplete size="mini"
            v-if="searchUserVisible"
            v-model="searchUser"
            :fetch-suggestions="querySearchUserAsync"
            ref="saveUserInput"
            placeholder="请输入内容"
            @select="handleSelectUser">
          </el-autocomplete>
          <el-button v-else size="mini" @click="showSearchUser">添加成员</el-button>
        </el-form-item>
        <el-form-item label="客服组长" prop="admin">
          <span v-if="form.users.length === 0">请先添加成员</span>
          <el-radio-group v-model="form.admin">
            <el-radio v-for="user in form.users" :key="user.id" :label="user.uid">
              {{ user.realName }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" size="mini" @click="addWorkGroup()">{{ dialogTitle }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import * as https from '@/apis/http'
import bus from '@/util/eventbus'

export default {
  name: 'bd-workGroup-admin',
  data () {
    return {
      nickname: '',
      searchUserVisible: false,
      searchUser: '',
      loading: false,
      pageSize: 20,
      currentPage: 1,
      dialogVisible: false,
      isEdit: false,
      workTimeIndex: 0,
      allWorkGroups: [],
      filteredWorkGroups: [],
      allAgents: [],
      form: {
        id: 0,
        wid: '',
        nickname: '',
        avatar: constants.DEFAULT_WORKGROUP_AVATAR,
        defaultRobot: false,
        offlineRobot: false,
        forceRate: false,
        routeType: 'robin',
        workTimes: [{
          startTime: new Date(2018, 8, 10, 8, 30),
          endTime: new Date(2018, 8, 10, 11, 30)
        }],
        users: [],
        admin: '',
        welcomeTip: '正在为您分配客服，请稍后',
        acceptTip: '您好有什么可以帮您的?',
        offlineTip: '当前客服不在线，请留言',
        onDutyWorkGroupWid: ''
      },
      workTimes: [{
        id: 0,
        value: [new Date(2018, 8, 10, 8, 30), new Date(2018, 8, 10, 11, 30)]
      }],
      rules: {
        nickname: [
          { required: true, message: '请输入昵称', trigger: 'blur' },
          { min: 2, message: '长度最少2个字符', trigger: 'blur' }
        ],
        workGroups: [
          { type: 'array', required: true, message: '请至少选择一个技能组', trigger: 'change' }
        ],
        defaultRobot: [
          { required: true, message: '请选择默认机器人', trigger: 'change' }
        ],
        offlineRobot: [
          { required: true, message: '请选择离线机器人', trigger: 'change' }
        ],
        forceRate: [
          { required: true, message: '请选择强制评价', trigger: 'change' }
        ],
        routeType: [
          { required: true, message: '请选择强制评价', trigger: 'change' }
        ],
        welcomeTip: [
          { required: true, message: '请填写默认欢迎语', trigger: 'blur' }
        ],
        acceptTip: [
          { required: true, message: '请填写接入欢迎语', trigger: 'blur' }
        ],
        offlineTip: [
          { required: true, message: '请填写离线提示语', trigger: 'blur' }
        ],
        users: [
          { type: 'array', required: true, message: '请至少添加一个成员', trigger: 'change' }
        ],
        admin: [
          { required: true, message: '请选择客服组长', trigger: 'change' }
        ]
      },
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL,
      isIp: constants.IS_IP
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'agents', 'contacts', 'workGroups', 'accessToken' ]),
    dialogTitle () {
      return this.isEdit ? '更新' : '创建'
    },
    mainHost () {
      return document.domain.split('.').slice(-2).join('.') + (location.port === '8081' ? ':8000' : '')
    }
  },
  methods: {
    ...mapActions([ 'createWorkgroup', 'updateWorkgroup', 'deleteWorkgroup', 'getWorkGroups', 'getAllWorkGroups', 'filterWorkGroups' ]),
    routeType (type) {
      return type === 'robin' ? '轮询' : (type === 'pubsub' ? '广播' : type)
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201809171923321')
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    querySearchUserAsync (queryString, cb) {
      console.log(queryString)
      var allAgents = this.allAgents
      var results = queryString ? allAgents.filter(this.createStateFilter(queryString)) : allAgents
      //
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        cb(results)
      }, 3000 * Math.random())
    },
    createStateFilter (queryString) {
      return (state) => {
        return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelectUser (item) {
      console.log(item)
      let contains = this.form.users.some(wg => {
        return wg.id === item.object.id
      })
      if (!contains) {
        this.form.users.push(item.object)
      }
      //
      this.searchUserVisible = false
      this.searchUser = ''
    },
    showSearchUser () {
      this.searchUserVisible = true
      this.$nextTick(_ => {
        this.$refs.saveUserInput.$refs.input.focus()
      })
    },
    handleRemoveTag (user) {
      console.log(user)
      this.form.users.splice(this.form.users.indexOf(user), 1)
    },
    handleCommand (command) {
      this.$message('click on item ' + command)
    },
    handleSizeChange (val) {
      // console.log(val)
      this.pageSize = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getWorkGroups(payload)
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.currentPage = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: 20 }
      this.getWorkGroups(payload)
    },
    addWorkTime () {
      this.workTimeIndex++
      this.workTimes.push({
        id: this.workTimeIndex,
        value: [new Date(2018, 8, 10, 13, 30), new Date(2018, 8, 10, 18, 30)]
      })
    },
    removeWorkTime (id) {
      for (let index = 0; index < this.workTimes.length; index++) {
        const workTime = this.workTimes[index]
        if (workTime.id === id) {
          this.workTimes.splice(index, 1)
        }
      }
    },
    transformWorkTimes () {
      let localWorkTimes = []
      for (let i = 0; i < this.workTimes.length; i++) {
        const workTime = this.workTimes[i]
        let startTime = moment(workTime.value[0]).format('HH:mm:ss')
        let endTime = moment(workTime.value[1]).format('HH:mm:ss')
        // console.log(startTime, endTime)
        localWorkTimes.push({'startTime': startTime, 'endTime': endTime})
      }
      this.form.workTimes = localWorkTimes
    },
    transformUsers () {
      let userUids = []
      for (let i = 0; i < this.form.users.length; i++) {
        const element = this.form.users[i]
        userUids.push(element.uid)
      }
      this.form.users = userUids
    },
    addWorkGroupDialog () {
      this.isEdit = false
      // this.form.id = 0
      // this.form.wid = ''
      // this.form.nickname = ''
      // this.form.avatar = ''
      // this.form.defaultRobot = false
      // this.form.offlineRobot = false
      // this.form.forceRate = false
      // this.form.routeType = ''
      // this.form.welcomeTip = ''
      // this.form.acceptTip = ''
      // this.form.offlineTip = ''
      this.form.onDutyWorkGroupWid = ''
      //
      this.workTimeIndex = 0
      this.workTimes = [{
        id: 0,
        value: [new Date(2018, 8, 10, 8, 30), new Date(2018, 8, 10, 11, 30)]
      }]
      this.form.users = []
      this.form.admin = ''
      //
      this.filteredWorkGroups = [{ id: 0, wid: '', nickname: '不设置' }]
      for (let i = 0; i < this.allWorkGroups.length; i++) {
        const element = this.allWorkGroups[i]
        this.filteredWorkGroups.push(element)
      }
      this.dialogVisible = true
    },
    addWorkGroup () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          this.transformWorkTimes()
          this.transformUsers()
          if (this.isEdit) {
            this.updateWorkgroup(this.form)
          } else {
            this.createWorkgroup(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleQueryChange (value) {
      // console.log(value)
      if (value.trim().length === 0) {
        this.loading = true
        let payload = { page: 0, size: 20 }
        this.getWorkGroups(payload)
      }
    },
    handleQuery () {
      // console.log(this.nickname)
      if (this.nickname === '') {
        let payload = { page: 0, size: 20 }
        this.getWorkGroups(payload)
      } else {
        let payload = {page: 0, size: 20, nickname: this.nickname}
        this.filterWorkGroups(payload)
      }
    },
    handleView (index, row) {
      // console.log(row)
      if (this.isIp) {
        window.open(constants.CHAT_BASE_URL + '?uid=' + this.userInfo.uid + '&wid=' + row.wid + '&type=workGroup&aid=&ph=ph')
      } else {
        window.open(constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/chat?uid=' + this.userInfo.uid + '&wid=' + row.wid + '&type=workGroup&aid=&ph=ph')
      }
    },
    handleCode (index, row) {
      // console.log(row)
      bus.$emit(constants.EVENT_BUS_WORKGROUP_CODE, row)
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.isEdit = true
      this.form.id = row.id
      this.form.wid = row.wid
      this.form.nickname = row.nickname
      this.form.avatar = row.avatar
      this.form.defaultRobot = row.defaultRobot
      this.form.offlineRobot = row.offlineRobot
      this.form.forceRate = row.forceRate
      this.form.routeType = row.routeType
      this.form.welcomeTip = row.welcomeTip
      this.form.acceptTip = row.acceptTip
      this.form.offlineTip = row.offlineTip
      this.form.onDutyWorkGroupWid = row.onDutyWorkGroup != null ? row.onDutyWorkGroup.wid : ''
      //
      this.workTimes = []
      for (let i = 0; i < row.workTimes.length; i++) {
        const workTime = row.workTimes[i]
        // console.log(workTime.startTime, workTime.endTime)
        this.workTimeIndex = i
        this.workTimes.push({id: i, value: [ new Date('2018-08-10 ' + workTime.startTime), new Date('2018-08-10 ' + workTime.endTime) ]})
      }
      //
      this.form.users = []
      for (let i = 0; i < row.users.length; i++) {
        const element = row.users[i]
        if (!element.robot) {
          this.form.users.push(element)
        }
      }
      this.form.admin = row.admin !== null ? row.admin.uid : ''
      //
      this.filteredWorkGroups = [{ id: 0, wid: '', nickname: '不设置' }]
      for (let i = 0; i < this.allWorkGroups.length; i++) {
        const element = this.allWorkGroups[i]
        if (row.wid !== element.wid) {
          this.filteredWorkGroups.push(element)
        }
      }
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteWorkgroup(row.id)
        //
        for (let i = this.allWorkGroups.length - 1; i >= 0; i--) {
          const element = this.allWorkGroups[i]
          if (element.id === row.id) {
            this.allWorkGroups.splice(i, 1)
          }
        }
      }).catch(() => {
        //
      })
    }
  },
  mounted () {
    https.getAllWorkGroups(this.accessToken, response => {
      console.log(response)
      for (let i = 0; i < response.data.length; i++) {
        const element = response.data[i]
        this.allWorkGroups.push(element)
      }
    }, error => {
      console.log(error)
    })
    https.getContacts(this.accessToken, response => {
      console.log(response)
      for (let i = 0; i < response.data.length; i++) {
        const element = response.data[i]
        if (!element.robot) {
          this.allAgents.push({ 'value': element.realName, 'object': element })
        }
      }
    }, error => {
      console.log(error)
    })
    //
    bus.$on(constants.EVENT_BUS_WORKGROUP_LOAD_FINISHIED, message => {
      this.loading = false
    })
  }
}
</script>

<style scoped>
#bd-workGroup-admin {
  height: 100%;
  text-align: left;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-radio+.el-radio {
  margin-left: 1px;
}
</style>



