<template>
  <!-- TODO: 增加搜索、多语言选择 -->
  <!-- TODO: 登录之后同步服务器自动回复、接待状态，包括：安卓+ios -->
  <!-- TODO: 增加机器人接管在线状态 -->
  <!-- TODO: 增加迁入状态：默认登录之后，只有设置迁入状态之后才能够接入访客 -->
  <div id='bd-header'>
    <el-menu style="padding-left:  25px; margin-left: -20px; margin-right: -20px; padding-right:30px;"
        :default-active="activeIndex"
        class="el-menu-demo"
        mode="horizontal"
        @select="handleSelect"
        :background-color="headerColor"
        text-color="#fff"
        active-text-color="#ffd04b">
        <el-menu-item index="home">
          {{ title }}
        </el-menu-item>
        <el-menu-item index="1">
          <i class="el-icon-menu"></i>
        </el-menu-item>
        <el-menu-item index="2" style="float: right;">
          <el-color-picker
            v-model="headerColor"
            size="mini"
            show-alpha
            @change="onChange"
            @active-change="onActiveChange"
            :predefine="predefineColors">
          </el-color-picker>
        </el-menu-item>
        <el-submenu index="2-1" style="float: right;" v-if="showAuthority.debug">
            <template slot="title">
              <span>中文</span>
            </template>
            <el-menu-item index="zh_cn">中文</el-menu-item>
            <el-menu-item index="en">English</el-menu-item>
        </el-submenu>
        <el-submenu index="3" style="float: right;">
            <template slot="title">
              <span>{{ userInfo.realName }}</span>
            </template>
            <el-menu-item index="profile">个人资料</el-menu-item>
            <el-menu-item index="password">修改密码</el-menu-item>
            <el-menu-item index="logout">退出登录</el-menu-item>
        </el-submenu>
        <!-- TODO: 同步连接状态 -->
        <el-submenu index="4" style="float: right;">
            <template slot="title">{{ onlineStatus }}</template>
            <el-menu-item index="online">上线</el-menu-item>
            <el-menu-item index="busy">忙线</el-menu-item>
            <el-menu-item index="rest">小休</el-menu-item>
        </el-submenu>
        <el-submenu index="5" style="float: right;">
            <template slot="title">{{ userInfo.autoReplyContent }}</template>
            <el-menu-item index="noreply">{{ noAuto }}</el-menu-item>
            <el-menu-item index="eat">{{ autoReplyEat }}</el-menu-item>
            <el-menu-item index="leave">{{ autoReplyLeave }}</el-menu-item>
            <el-menu-item index="back">{{ autoReplyBack }}</el-menu-item>
            <el-menu-item index="phone">{{ autoReplyPhone }}</el-menu-item>
            <el-menu-item index="self">{{ autoReplySelf }}</el-menu-item>
        </el-submenu>
        <el-submenu index="6" style="float: right;" v-if="showAuthority.debug">
            <template slot="title">费用</template>
            <el-menu-item index="recharge">充值</el-menu-item>
            <el-menu-item index="detail">明细</el-menu-item>
        </el-submenu>
        <el-menu-item index="7" style="float: right;" v-if="showAuthority.debug">
          <el-button type="success" size="mini" @click="onPayVip">成为VIP用户</el-button>
        </el-menu-item>
        <el-menu-item index="8" style="float: right;" v-if="showAuthority.free">
          <el-button type="warning" size="mini" @click="onApplyTry">申请30天试用</el-button>
        </el-menu-item>
        <el-menu-item index="9" v-if="showAuthority.debug">
          <el-input size="mini" placeholder="请输入内容" prefix-icon="el-icon-search" v-model="searchContent"></el-input>
        </el-menu-item>
    </el-menu>

    <el-dialog
      title="自定义"
      :visible.sync="autoReplyDialogVisible"
      width="30%"
      center>
      <el-input type="textarea"
        :autosize="{ minRows: 4, maxRows: 8}"
        placeholder="请输入自动回复内容"
        v-model="autoReplySelfContent">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="autoReplyDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doSelfAutoReply">保 存</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改密码"
      :visible.sync="changePasswordDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :rules="rules" :model="form" label-width="80px" style="margin-top: 20px;">
        <el-form-item label="原密码" prop="old">
          <el-input type="password" v-model="form.old" auto-complete="off" placeholder="原密码" style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="password">
          <el-input type="password" v-model="form.password" auto-complete="off" placeholder="新密码" style="width: 250px;"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="rePassword">
          <el-input type="password" v-model="form.rePassword" auto-complete="off" placeholder="确认密码" style="width: 250px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changePasswordDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doChangePassword">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import * as https from '@/apis/http'
import bus from '@/util/eventbus'

export default {
  name: 'bd-header',
  data () {
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.form.rePassword !== '') {
          this.$refs.form.validateField('rePassword')
        }
        callback()
      }
    }
    var validateRePassword = (rule, value, callback) => {
      if (value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      activeIndex: '0',
      isCollapse: true,
      changePasswordDialogVisible: false,
      searchContent: '',
      form: {
        old: '',
        password: '',
        rePassword: ''
      },
      rules: {
        old: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符之间', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符之间', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          { validator: validateRePassword, trigger: 'blur' }
        ]
      },
      autoReplyContent: constants.AUTO_REPLY_NO,
      noAuto: constants.AUTO_REPLY_NO,
      autoReplyEat: constants.AUTO_REPLY_EAT,
      autoReplyLeave: constants.AUTO_REPLY_LEAVE,
      autoReplyBack: constants.AUTO_REPLY_BACK,
      autoReplyPhone: constants.AUTO_REPLY_PHONE,
      autoReplySelf: constants.AUTO_REPLY_SELF,
      autoReplyDialogVisible: false,
      autoReplySelfContent: '',
      title: constants.DEFAULT_TITLE,
      // rgba(0, 123, 255, 1)
      headerColor: '#007bff',
      predefineColors: [
        '#ffd700',
        'rgb(255, 120, 0)',
        '#ff4500',
        '#c71585',
        '#90ee90',
        '#00ced1',
        '#1e90ff',
        'rgba(0, 123, 255, 1)'
      ]
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'accessToken', 'status', 'showAuthority' ]),
    onlineStatus () {
      if (this.status === constants.USER_STATUS_ONLINE) {
        return '在线'
      } else if (this.status === constants.USER_STATUS_BUSY) {
        return '忙线'
      } else {
        return '小休'
      }
    }
  },
  methods: {
    ...mapActions([ 'setAcceptStatus', 'applyTry', 'logout', 'updateAutoReply' ]),
    onApplyTry () {
      // console.log('apply try')
      // TODO: 首先检查手机号是否为空，否则需要提示填写手机号
      this.$prompt('请输入手机号码', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        let mobile = value
        this.applyTry(mobile)
      }).catch(() => {
      })
    },
    onPayVip () {
      console.log('pay vip')
    },
    onChange (value) {
      // 点击确定按钮之后触发
      // console.log('onChange:', value)
      localStorage.headerColor = value
    },
    onActiveChange (value) {
      // 每次选择颜色之后触发
      // console.log('onActiveChange:', value)
    },
    doSelfAutoReply () {
      this.autoReplyDialogVisible = false
      //
      let payload = {
        isAutoReply: true,
        content: this.autoReplySelfContent
      }
      this.updateAutoReply(payload)
    },
    doChangePassword () {
      //
      this.$refs['form'].validate((valid) => {
        if (valid) {
          let password = this.form.password
          https.changePassword2(password, this.accessToken, response => {
            console.log(response)
            this.changePasswordDialogVisible = false
          }, error => {
            console.log(error)
          })
        }
      })
    },
    handleSelect (key, keyPath) {
      // console.log(key, keyPath)
      if (key === 'home') {
        this.$router.replace('/admin')
      } else if (key === '1') {
        this.isCollapse = !this.isCollapse
        bus.$emit('collapse', this.isCollapse)
      } else if (key === 'profile') {
        this.$router.replace('/admin/setting/profile')
      } else if (key === 'password') {
        this.changePasswordDialogVisible = true
      } else if (key === 'logout') {
        this.logout()
      } else if (key === 'online') {
        this.setAcceptStatus(constants.USER_STATUS_ONLINE)
      } else if (key === 'busy') {
        this.setAcceptStatus(constants.USER_STATUS_BUSY)
      } else if (key === 'rest') {
        this.setAcceptStatus(constants.USER_STATUS_REST)
      } else if (key === 'recharge') {
        this.$router.replace('/admin/setting/pay')
      } else if (key === 'detail') {
        this.$router.replace('/admin/setting/pay')
      } else if (key === 'noreply') {
        this.autoReplyContent = this.noAuto
        let payload = {
          isAutoReply: false,
          content: this.noAuto
        }
        this.updateAutoReply(payload)
      } else if (key === 'eat') {
        this.autoReplyContent = this.autoReplyEat
        let payload = {
          isAutoReply: true,
          content: this.autoReplyEat
        }
        this.updateAutoReply(payload)
      } else if (key === 'leave') {
        this.autoReplyContent = this.autoReplyLeave
        let payload = {
          isAutoReply: true,
          content: this.autoReplyLeave
        }
        this.updateAutoReply(payload)
      } else if (key === 'back') {
        this.autoReplyContent = this.autoReplyBack
        let payload = {
          isAutoReply: true,
          content: this.autoReplyBack
        }
        this.updateAutoReply(payload)
      } else if (key === 'phone') {
        this.autoReplyContent = this.autoReplyPhone
        let payload = {
          isAutoReply: true,
          content: this.autoReplyPhone
        }
        this.updateAutoReply(payload)
      } else if (key === 'self') {
        this.autoReplyContent = this.autoReplySelf
        this.autoReplyDialogVisible = true
      }
    }
  },
  created () {
    let color = localStorage.headerColor
    // console.log('headerColor:', color)
    if (color !== null && color !== undefined) {
      this.headerColor = color
    }
  }
}
</script>

<style scoped>
#bd-header {
  background-color: #B3C0D1;
  color: #333;
  text-align: center;
  line-height: 60px;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>



