<template>
  <div id='bd-new'>
    <!-- TODO: 增加标签 -->
    <el-form ref="form" :model="form" :rules="rules" label-width="100px" style="width: 80%;">
      <el-form-item label="标题" prop="title">
        <el-input v-model="form.title" style="width: 450px; float: left;" placeholder="标题"></el-input>
      </el-form-item>
      <el-form-item label="推荐首页" prop="recommend">
        <el-radio-group v-model="form.recommend" style="float: left; margin-top: 13px;">
          <el-radio :label="true">是</el-radio>
          <el-radio :label="false">否</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="所属分类" prop="categories">
        <el-checkbox-group v-model="form.categories" style="float: left;">
          <el-checkbox v-if="category.id > 0" v-for="category in supportCategories" :key="category.id" :label="category.id">{{ category.name }}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="立即发布" prop="published">
        <el-radio-group v-model="form.published" style="float: left; margin-top: 13px;">
          <el-radio :label="true">是</el-radio>
          <el-radio :label="false">否</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="概要">
        <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 10}" v-model="form.summary"></el-input>
      </el-form-item>
      <el-form-item label="编辑器">
        <el-radio-group v-model="form.markdown" style="float: left; margin-top: 13px;">
          <el-radio :label="false">HTML</el-radio>
          <el-radio :label="true">MarkDown</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="内容" prop="content">
        <bd-quill-editor v-if="!form.markdown" :value="form.content" @change="onEditorChange" style="height: 400px;"></bd-quill-editor>
        <!-- https://github.com/hinesboy/mavonEditor -->
        <mavon-editor v-if="form.markdown" v-model="form.content"/>
        <!-- <quill-editor ref="myTextEditor" style="height: 400px;"
                    v-model="form.content"
                    :options="editorOption"
                    @blur="onEditorBlur($event)"
                    @focus="onEditorFocus($event)"
                    @ready="onEditorReady($event)">
        </quill-editor> -->
      </el-form-item>
      <el-form-item style="margin-top: 60px; float:left;">
        <el-button type="primary" @click="saveArticle">立即创建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import hljs from 'highlight.js'
import { mapActions, mapGetters } from 'vuex'
import BdQuillEditor from '@/components/dashboard/main/support/children/BdQuillEditor'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-new',
  components: { BdQuillEditor },
  data () {
    return {
      form: {
        title: '',
        content: '',
        recommend: false,
        published: false,
        markdown: false,
        summary: '',
        categories: []
      },
      rules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请输入内容', trigger: 'blur' }
        ],
        categories: [
          { type: 'array', required: true, message: '请至少选择一个类别', trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'supportCategories', 'supportArticles' ]),
    editor () {
      return this.$refs.myTextEditor.quill
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'deleteCategory',
      'getSupportArticles', 'createSupportArticle', 'updateSupportArticle', 'deleteSupportArticle' ]),
    onEditorChange (content) {
      // console.log(content)
      this.form.content = content
    },
    onEditorBlur (editor) {
      // console.log('editor blur!', editor)
    },
    onEditorFocus (editor) {
      // console.log('editor focus!', editor)
    },
    onEditorReady (editor) {
      // console.log('editor ready!', editor)
    },
    saveArticle () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.createSupportArticle(this.form)
        } else {
          this.$message.error('缺少必填字段')
          return false
        }
      })
    }
  },
  created () {
    bus.$on(constants.EVENT_BUS_SUPPORT_NEW_ARTICLE_SUCCESS, message => {
      this.$message({ message: '创建成功', type: 'success' })
    })
    bus.$on(constants.EVENT_BUS_SUPPORT_NEW_ARTICLE_ERROR, message => {
      this.$message.error('创建失败')
    })
  }

}
</script>

<style scoped>
#bd-new {
  height: 100%;
  overflow-y: auto;
}
</style>



