<template>
  <div id='dashboard'>
    <el-header>
      <bd-header></bd-header>
    </el-header>
    <el-container style="height: calc(100% - 60px);">
      <el-aside :width="leftSideWidth" style="height: 100%;">
        <bd-side></bd-side>
      </el-aside>
      <el-container>
        <el-main style="padding-left: 5px; padding-top: 5px; padding-bottom: 0px; padding-right: 5px;">
          <transition mode="out-in" enter-active-class="fadeIn" leave-active-class="fadeOut" appear>
            <router-view class="animated"></router-view>
          </transition>
        </el-main>
        <el-footer style="height: 40px;">
          <bd-footer></bd-footer>
        </el-footer>
      </el-container>
    </el-container>
    <audio id="audioPlay" :src="soundUrl" hidden="true"/>
    <el-dialog
      title="创建群组"
      :visible.sync="createGroupDialogVisible"
      width="550px"
      center>
      <el-transfer
        filterable
        filter-placeholder="请输入姓名"
        v-model="selectedContacts"
        :data="contactData"
        :titles="['联系人', '群成员']">
      </el-transfer>
      <span slot="footer" class="dialog-footer">
        <el-button @click="createGroupDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doCreateGroup">确 定</el-button>
      </span>
    </el-dialog>
    <!-- <el-dialog
      title="聊天记录"
      :visible.sync="messageDialogVisible"
      width="30%"
      center>
      <span>聊天记录</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="messageDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="messageDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog> -->
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
import BdHeader from '@/components/dashboard/header/BdHeader'
import BdSide from '@/components/dashboard/aside/BdSide'
import BdFooter from '@/components/dashboard/footer/BdFooter'
import BdMsgBox from '@/components/dashboard/layim/BdMsgBox'
import * as constants from '@/util/constants'
import * as https from '@/apis/http'
import bus from '@/util/eventbus'

export default {
  name: 'dashboard',
  components: { BdHeader, BdSide, BdFooter, BdMsgBox },
  data () {
    return {
      leftSideWidth: constants.LEFT_SIDEBAR_WIDTH,
      soundUrl: constants.SOUND_DINGDONG_URL,
      iconUrl: constants.ICON_URL,
      homeUrl: constants.HOME_URL,
      electron: constants.IS_ELECTRON,
      daxuezhang: constants.IS_DAXUEZHANG,
      showIm: constants.SHOW_IM,
      //
      layer: null,
      layim: null,
      layimCurrentChatType: 'friend',
      layimCurrentChatId: '',
      //
      createGroupDialogVisible: false,
      messageDialogVisible: false,
      //
      selectedContacts: []
    }
  },
  computed: {
    ...mapGetters([ 'accessToken', 'userInfo', 'contacts', 'layimMine', 'layimFriends', 'layimGroups' ]),
    ...mapState({
      'currentMainView': 'current_main_view'
    }),
    contactData () {
      const data = []
      this.contacts.forEach((contact, index) => {
        data.push({
          label: contact.realName,
          key: contact.uid
        })
      })
      return data
    }
  },
  methods: {
    ...mapActions(['init', 'transferAccept', 'transferAccepted', 'inviteAccept',
      'sendContactTextMessage', 'sendContactImageMessage', 'sendContactFileMessage',
      'sendGroupTextMessage', 'sendGroupImageMessage', 'sendGroupFileMessage',
      'clearToken', 'logout', 'createGroup']),
    // 显示桌面通知
    showElectronMessageNotification (message) {
      // Electron 桌面客户端 右上角消息通知
      const notification = {
        title: '新消息',
        icon: this.iconUrl,
        body: message.thread.content
      }
      const myNotification = new window.Notification(notification.title, notification)
      myNotification.onclick = () => {
        // console.log('Notification clicked')
      }
    },
    // 页面标题滚动
    scrollTitle () {
      // 拿到标题栏的文本
      var title = document.title
      // 将文本字符串转换为数组
      var arr = title.split('')
      // 拿到数组的第一个元素，并从数组中删除
      var first = arr.shift()
      // 将第一个元素添加到数组的最后
      arr.push(first)
      // 将数组再组合成一个字符串
      title = arr.join('')
      document.title = title
      // 每隔一秒做一遍前6步
      setTimeout(function () {
        this.scrollTitle()
      }, 1000)
    },
    // 播放提示音
    playAudio () {
      var audio = document.getElementById('audioPlay')
      // 浏览器支持 audio
      audio.play()
    },
    // 初始化内部im
    initLayim () {
      if (this.layim !== null) {
        return
      }
      layui.use('layer', () => {
        this.layer = layui.layer
      })
      // TODO: 加载完好友和群组数据之后，执行初始化
      // TODO: 增加专属客服，SaaS用户可以直接在后台联系专属客服
      // TODO: 增加专属客服机器人，回答常见问题
      // TODO: 联系人默认分组：好友分组、黑名单、陌生人、未分组联系人
      layui.use('layim', layim => {
        layim.config({
          // 初始化接口
          init: {
            mine: this.layimMine,
            friend: this.layimFriends,
            group: this.layimGroups
          },
          // FIXME: 上传.jpeg报异常？
          // 上传图片接口（返回的数据格式见下文），若不开启图片上传，剔除该项即可
          uploadImage: {
            url: constants.UPLOAD_LAYIM_IMAGE_URL, // 接口地址
            type: 'post' // 默认post
          },
          // 上传文件接口（返回的数据格式见下文），若不开启文件上传，剔除该项即可
          uploadFile: {
            url: constants.UPLOAD_LAYIM_FILE_URL, // 接口地址
            type: 'post' // 默认post
          },
          initSkin: '2.jpg', // 1-5 设置初始背景
          min: true, // 是否始终最小化主面板，默认false
          notice: true, // 是否开启桌面消息提醒，默认false
          // TODO: 判断是否electron客户端
          msgbox: 'url', // 消息盒子页面地址，若不开启，剔除该项即可
          find: 'url', // 发现页面地址，若不开启，剔除该项即可
          chatLog: 'url', // 聊天记录页面地址，若不开启，剔除该项即可
          copyright: true // 隐藏下面的版权信息
        })
        // 组件全局看见
        this.layim = layim
        // 监听在线状态的切换事件
        layim.on('online', data => {
          console.log('online:', data)
        })
        // 创建群组
        layim.on('find', data => {
          console.log('find:', data)
          this.createGroupDialogVisible = true
        })
        // 查看消息通知
        layim.on('msgbox', data => {
          console.log('msgbox:', data)
        })
        // 查看聊天记录
        layim.on('chatLog', chat => {
          console.log('chatLog:', chat)
          // this.messageDialogVisible = true
          // chat.data.id
          if (chat.data.type === 'friend') {
            //
          } else if (chat.data.type === 'group') {
            //
          }
        })
        // 查看群组成员
        layim.on('groupMembers', chat => {
          console.log('groupMembers:', chat)
        })
        // 监听签名修改
        layim.on('sign', value => {
          console.log('sign: ', value)
        })
        // 设置皮肤
        layim.on('setSkin', (filename, src) => {
          console.log(filename) // 获得文件名，如：1.jpg
          console.log(src) // 获得背景路径，如：http://res.layui.com/layui/src/css/modules/layim/skin/1.jpg
        })
        // 监听layim建立就绪
        layim.on('ready', res => {
          console.log('ready: ', res)
        })
        // 监听发送消息
        layim.on('sendMessage', data => {
          console.log('sendMessage: ', data)
          //
          if (data.to.type === 'friend') {
            //
            if (data.mine.content.startsWith('img[') && data.mine.content.endsWith(']')) {
              // 图片类型: img[https://bytedesk.oss-cn-shenzhen.aliyuncs.com/images/43474809-49e5-4acf-baf7-62b8ebcf0e95]
              // TODO: 修改格式
              let content = data.mine.content.slice(4, -1)
              let payload = { uid: data.to.id, imageUrl: content }
              this.sendContactImageMessage(payload)
            } else if (data.mine.content.startsWith('file(') && data.mine.content.endsWith(']')) {
              // 文件类型: file(https://bytedesk.oss-cn-shenzhen.aliyuncs.com/files/9d4b4a89-6039-46f2-b262-71f6156698f0)[9d4b4a89-6039-46f2-b262-71f6156698f0]
              let endIndex = data.mine.content.indexOf(')')
              let content = data.mine.content.slice(5, endIndex)
              // TODO: format需要根据文件类型，填写相应后缀，如：doc/exe等
              let payload = { uid: data.to.id, fileUrl: content, format: 'doc' }
              this.sendContactFileMessage(payload)
            } else {
              let payload = { uid: data.to.id, content: data.mine.content }
              this.sendContactTextMessage(payload)
            }
          } else if (data.to.type === 'group') {
            //
            if (data.mine.content.startsWith('img[') && data.mine.content.endsWith(']')) {
              // 图片类型: img[https://bytedesk.oss-cn-shenzhen.aliyuncs.com/images/43474809-49e5-4acf-baf7-62b8ebcf0e95]
              let content = data.mine.content.slice(4, -1)
              let payload = { uid: data.to.id, imageUrl: content }
              this.sendGroupImageMessage(payload)
            } else if (data.mine.content.startsWith('file(') && data.mine.content.endsWith(']')) {
              // 文件类型: file(https://bytedesk.oss-cn-shenzhen.aliyuncs.com/files/9d4b4a89-6039-46f2-b262-71f6156698f0)[9d4b4a89-6039-46f2-b262-71f6156698f0]
              let endIndex = data.mine.content.indexOf(')')
              let content = data.mine.content.slice(5, endIndex)
              // TODO: format需要根据文件类型，填写相应后缀，如：doc/exe等
              let payload = { uid: data.to.id, fileUrl: content, format: 'doc' }
              this.sendGroupFileMessage(payload)
            } else {
              let payload = { uid: data.to.id, content: data.mine.content }
              this.sendGroupTextMessage(payload)
            }
          }
        })
        // 监听聊天窗口的切换
        layim.on('chatChange', res => {
          console.log('chatChange:', res)
          this.layimCurrentChatType = res.data.type
          this.layimCurrentChatId = res.data.id
        })
      })
    },
    // 创建群组, 昵称组成：最多三个被选择人的realName
    doCreateGroup () {
      this.createGroupDialogVisible = false
      if (this.selectedContacts.length === 0) {
        return
      }
      let n = 0
      let nickname = this.userInfo.realName
      for (let i = 0; i < this.selectedContacts.length; i++) {
        const uid = this.selectedContacts[i]
        for (let j = 0; j < this.contacts.length; j++) {
          const contact = this.contacts[j]
          if (uid === contact.uid) {
            nickname = nickname + ',' + contact.realName
            n++
            if (n > 2) {
              break
            }
          }
        }
      }
      let payload = { nickname: nickname, selectedContacts: this.selectedContacts }
      this.createGroup(payload)
      this.selectedContacts = []
    }
  },
  created () {
    // 初始化数据+连接stomp
    this.init()
    // 验证私有部署服务有效性
    if (constants.IS_PRIVATE_SERVER) {
      https.serverValidate(constants.PRIVATE_SERVER_UID, response => {
        console.log(response)
        if (!response.data.valid) {
          // 无效用户
          this.$alert(response.data.message, '提示', {
            confirmButtonText: '确定',
            callback: action => {
              this.logout()
            }
          })
        }
      }, error => {
        console.log(error)
      })
    }
    // TODO: 验证当前登录坐席数量，是否超过购买数量
  },
  mounted () {
    document.title = constants.DEFAULT_TITLE
    bus.$on('collapse', isCollapse => {
      if (isCollapse) {
        this.leftSideWidth = constants.LEFT_SIDEBAR_WIDTH
      } else {
        this.leftSideWidth = '200px'
      }
    })
    // 显示通知
    bus.$on(constants.EVENT_BUS_NOTIFICATION_SUCCESS, message => {
      this.$notify({ title: '提示', message: message, type: 'success' })
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_WARNING, message => {
      this.$notify({ title: '提示', message: message, type: 'warning' })
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_INFO, message => {
      // 播放声音
      this.playAudio()
      // 界面显示提示窗
      if (this.currentMainView !== 'chat') {
        // TODO: 收到新消息通知, 判断是否弹出提示框，只有主窗口被关闭的情况才会
        if (this.electron) {
          // Mac右上角通知提示框
          this.showElectronMessageNotification(message)
          // TODO: Windows桌面右下角弹窗提醒：定义一个Window
          if (message.type === constants.MESSAGE_TYPE_NOTIFICATION_THREAD) {
            // 区分新访客进入
            ipcRenderer.send('new-visitor', message)
          } else {
            // 收到新消息
            ipcRenderer.send('new-message', message)
          }
        } else {
          // TODO: 显示浏览器通知
        }
        // 对话窗口右上角提示框
        if (message.thread !== null && message.thread !== undefined) {
          let vm = this
          this.$notify({ title: '收到新消息',
            message: message.thread.content,
            type: 'success',
            onClick: function () {
              vm.$router.replace('/admin/chat')
            }})
        }
      }
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_ERROR, message => {
      this.$notify({ title: '提示', message: message, type: 'error' })
    })
    // 转接会话请求
    bus.$on(constants.EVENT_BUS_NOTIFICATION_TRANSFER, message => {
      let realName = message.transfer.realName // message.threadTransfer.fromUser.realName
      let ttid = message.transfer.tTid // message.threadTransfer.tTid
      this.$msgbox({
        title: '转接会话',
        message: realName + '转接给您一个会话',
        showCancelButton: true,
        confirmButtonText: '接受',
        cancelButtonText: '拒绝',
        beforeClose: (action, instance, done) => {
          console.log('beforeClose:', action)
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            instance.confirmButtonText = '接入中...'
            https.transferAccept(ttid, this.accessToken, response => {
              console.log(response)
              // TODO: 会话本地持久化
              this.transferAccept(message)
              done()
              instance.confirmButtonLoading = false
            }, error => {
              console.log(error)
              done()
              instance.confirmButtonLoading = false
            })
          } else {
            https.transferReject(ttid, this.accessToken, response => {
              console.log(response)
              done()
            }, error => {
              console.log(error)
              done()
            })
          }
        }
      }).then(action => {
        console.log('then:', action)
      })
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_TRANSFER_ACCEPT, message => {
      let realName = message.transfer.realName // message.threadTransfer.toUser.realName
      let content = realName + message.content
      this.$notify({ title: '提示', message: content, type: 'success' })
      // 转移会话被接受，删除本地相应thread
      this.transferAccepted(message)
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_TRANSFER_REJECT, message => {
      let realName = message.transfer.realName // message.threadTransfer.toUser.realName
      let content = realName + message.content
      this.$notify({ title: '提示', message: content, type: 'warning' })
    })
    // 邀请会话请求
    bus.$on(constants.EVENT_BUS_NOTIFICATION_INVITE, message => {
      let realName = message.invite.realName // message.threadInvite.fromUser.realName
      let tiid = message.invite.tIid // message.threadInvite.tTid
      this.$msgbox({
        title: '邀请会话',
        message: realName + '邀请您加入会话',
        showCancelButton: true,
        confirmButtonText: '接受',
        cancelButtonText: '拒绝',
        beforeClose: (action, instance, done) => {
          console.log('beforeClose:', action)
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            instance.confirmButtonText = '接入中...'
            https.inviteAccept(tiid, this.accessToken, response => {
              console.log(response.data)
              // 会话本地持久化
              this.inviteAccept(message)
              instance.confirmButtonLoading = false
              done()
            }, error => {
              console.log(error)
              instance.confirmButtonLoading = false
              done()
            })
          } else {
            https.inviteReject(tiid, this.accessToken, response => {
              console.log(response.data)
              done()
            }, error => {
              console.log(error)
              done()
            })
          }
        }
      }).then(action => {
        console.log('then:', action)
      })
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_INVITE_ACCEPT, message => {
      let realName = message.invite.realName // message.threadInvite.toUser.realName
      let content = realName + message.content
      this.$notify({ title: '提示', message: content, type: 'success' })
    })
    bus.$on(constants.EVENT_BUS_NOTIFICATION_INVITE_REJECT, message => {
      let realName = message.invite.realName // message.threadInvite.toUser.realName
      let content = realName + message.content
      this.$notify({ title: '提示', message: content, type: 'warning' })
    })
    // 加载全部联系人成功
    bus.$on(constants.EVENT_BUS_CONTACT_LOAD_FINISHED, message => {
      if (this.showIm) {
        this.initLayim()
      }
    })
    // 群组信息发生变化
    bus.$on(constants.EVENT_BUS_GROUP_ADDED, group => {
      console.log('群组信息发生变化', group)
      if (this.showIm) {
        // 调用接口，添加群组
        let layimGroup = {
          id: group.gid, // 群组id
          type: 'group', // 列表类型，只支持friend和group两种
          avatar: group.avatar, // 群组头像
          groupname: group.nickname, // 群组名称
          sign: group.description // 描述
        }
        this.layim.addList(layimGroup)
      }
    })
    // 联系人消息
    bus.$on(constants.EVENT_BUS_NOTIFICATION_CONTACT_MESSAGE, message => {
      // 不含自己发送的消息
      if (this.userInfo.uid !== message.user.uid) {
        let content = ''
        if (message.type === constants.MESSAGE_TYPE_TEXT) {
          content = message.content
        } else if (message.type === constants.MESSAGE_TYPE_IMAGE) {
          content = 'img[' + message.imageUrl + ']'
          // content = '<img src="' + message.imageUrl + '"/>'
          // content = message.imageUrl
        }
        this.layim.getMessage({
          id: message.user.uid, // 接收者：消息的来源ID（如果是私聊，则是用户id，如果是群聊，则是群组id）
          username: message.user.realName, // 消息来源用户名
          avatar: message.user.avatar, // 消息来源用户头像
          type: 'friend', // 聊天窗口来源类型，从发送消息传递的to里面获取
          content: content, // 消息内容
          cid: 0, // 消息id，可不传。除非你要对消息进行一些操作（如撤回）
          mine: false, // 是否我发送的消息，如果为true，则会显示在右方
          fromid: message.user.uid, // 消息的发送者id（比如群组中的某个消息发送者），可用于自动解决浏览器多窗口时的一些问题
          timestamp: moment(message.createdAt).valueOf() // 服务端时间戳毫秒数。注意：如果你返回的是标准的 unix 时间戳，记得要 *1000
        })
      }
    })
    // 群组消息
    bus.$on(constants.EVENT_BUS_NOTIFICATION_GROUP_MESSAGE, message => {
      // console.log('群组消息:', this.userInfo.uid, message.user.uid)
      if (this.userInfo.uid !== message.user.uid) {
        let content = ''
        if (message.type === constants.MESSAGE_TYPE_TEXT) {
          content = message.content
        } else if (message.type === constants.MESSAGE_TYPE_IMAGE) {
          content = 'img[' + message.imageUrl + ']'
        }
        this.layim.getMessage({
          id: message.gid, // 接收者：消息的来源ID（如果是私聊，则是用户id，如果是群聊，则是群组id）
          username: message.user.realName, // 消息来源用户名
          avatar: message.user.avatar, // 消息来源用户头像
          type: 'group', // 聊天窗口来源类型，从发送消息传递的to里面获取
          content: content, // 消息内容
          cid: 0, // 消息id，可不传。除非你要对消息进行一些操作（如撤回）
          mine: false, // 是否我发送的消息，如果为true，则会显示在右方
          fromid: message.user.uid, // 消息的发送者id（比如群组中的某个消息发送者），可用于自动解决浏览器多窗口时的一些问题
          timestamp: moment(message.createdAt).valueOf() // 服务端时间戳毫秒数。注意：如果你返回的是标准的 unix 时间戳，记得要 *1000
        })
      }
    })
    // stomp连接
    bus.$on(constants.EVENT_BUS_STOMP_CONNECTION_STATUS, message => {
      let connectionStatus = message
      if (this.electron) {
        ipcRenderer.send(connectionStatus, '')
      }
    })
    // 退出登录，跳转到登录页面
    bus.$on(constants.EVENT_BUS_LOGOUT, message => {
      // 401、403的情况下，需要清空下storage
      this.clearToken()
      this.$router.replace('/login')
      window.location.reload()
      // Electron 隐藏tray图标
      if (constants.IS_ELECTRON) {
        ipcRenderer.send('hide-tray', '')
      }
    })
    // 监听浏览器切换页面
    document.addEventListener('visibilitychange', function () {
      if (!document.hidden) {
        // console.log('页面可见')
      } else {
        // console.log('页面不可见')
      }
    })
    // 刷新或关闭窗口之前
    // FIXME: 影响手动退出登录
    // window.onbeforeunload = () => {
