<template>
  <div id='bd-web-admin'>
    <el-row>
      <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="showAddDialog">添加网站</el-button>
    </el-row>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="webs.content" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="aid" label="唯一ID"></el-table-column>
      <el-table-column prop="name" label="网站名"></el-table-column>
      <el-table-column prop="url" label="网址"></el-table-column>
      <!-- <el-table-column prop="status" label="状态"></el-table-column> -->
      <el-table-column prop="description" label="描述"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="添加应用"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="网站名称" prop="name">
          <el-input v-model="form.name" style="width: 350px;"></el-input>
        </el-form-item>
        <!--
          TODO: 1. 严格要求填写URL(https/http + 端口号)，可以填写多个URL
          TODO: 2. url匹配填写错误的，无法访问客服
         -->
        <el-form-item label="网站网址" prop="url">
          <el-input v-model="form.url" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="网址描述" prop="description">
          <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 10}" v-model="form.description" style="width: 350px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addApp">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-web-admin',
  data () {
    return {
      dialogVisible: false,
      isEdit: false,
      form: {
        aid: '',
        name: '',
        url: '',
        description: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入网站名称', trigger: 'blur' }
        ],
        url: [
          { required: true, message: '请输入网址', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '请输入网站简介', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'webs' ])
  },
  methods: {
    ...mapActions([ 'getWebs', 'createWeb', 'updateWeb', 'deleteWeb' ]),
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201808252119041')
    },
    showAddDialog () {
      this.dialogVisible = true
      this.isEdit = false
      //
      this.form.aid = ''
      this.form.name = ''
      this.form.url = ''
      this.form.description = ''
    },
    addApp () {
      this.dialogVisible = false
      if (this.isEdit) {
        this.updateWeb(this.form)
      } else {
        this.createWeb(this.form)
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.form.aid = row.aid
      this.form.name = row.name
      this.form.url = row.url
      this.form.description = row.description
      //
      this.isEdit = true
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteWeb(row.aid)
      }).catch(() => {
        //
      })
    }
  },
  created () {
    let payload = { page: 0, size: 20 }
    this.getWebs(payload)
  }

}
</script>

<style scoped>
#bd-web-admin {
  height: 100%;
  text-align: left;
}
</style>



