<template>
  <div id='bd-preference-admin'>
    <el-form ref="form" :model="form" label-width="80px" style="margin-top: 20px;">
      <el-form-item label="消息通知" v-if="!electron">
        <el-switch v-model="form.notification" active-text="开启" inactive-text="关闭" @change="onChange"></el-switch>
        <span style="font-size: 10px; color: gray; margin-left: 20px;">提示：窗口左上角会弹出提示框，请点击允许</span>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-preference-admin',
  data () {
    return {
      form: {
        notification: false
      },
      electron: constants.IS_ELECTRON
    }
  },
  computed: {
    ...mapGetters([])
  },
  methods: {
    ...mapActions([]),
    //
    onChange (status) {
      console.log('开启', status)
      if (status) {
        this.requestNotification()
      }
    },
    //
    handleNotificationSwitchChange (result) {
      console.log(result)
      // 浏览器支持通知
      if (window.Notification) {
        if (Notification.permission === 'granted') {
          // granted： 已经授权
          this.showNotification(result)
        } else if (Notification.permission === 'denied') {
          // denied： 之前用户拒绝授权，重新请求用户授权
          this.requestNotification()
        } else {
          // default：用户还没有做出任何许可，因此需要请求用户授权
          this.requestNotification()
        }
      } else {
        // 浏览器不支持通知
        alert('浏览器不支持消息通知')
      }
    },
    // 显示通知
    // http://javascript.ruanyifeng.com/bom/notification.html
    showNotification (result) {
      // this.notificationGranted(result)
      var content = ''
      if (result) {
        content = '开启通知'
      } else {
        content = '关闭通知'
      }
      var notification = new Notification('通知', {
        // dir: 'auto',
        // lang: 'zh-CN',
        body: content,
        // tag: 'tag',
        icon: constants.ICON_URL
      })
      // notification.title // '收到新邮件'
      // notification.body // '您总共有3封未读邮件。'
      //
      notification.onshow = function () {
        console.log('Notification shown')
        // 自动关闭
        // setTimeout(notification.close.bind(notification), 5000)
      }
      //
      notification.onclick = function () {
        console.log('notification click')
        // notification.close() //不需要手动关闭，点击之后自会关闭
      }
      //
      notification.onclose = function () {
        console.log('notification close')
      }
      //
      notification.onerror = function () {
        console.log('notification error')
      }
    },
    // 请求授权
    requestNotification () {
      // 如果浏览器支持通知，而且用户之前没有授权过通知或者拒绝过通知
      if (window.Notification && (Notification.permission !== 'granted')) {
        // var vm = this
        Notification.requestPermission(function (status) {
          if (status === 'granted') {
            // vm.notificationGranted(true)
            // var notification = new Notification('通知', {
            //   body: '消息开启成功'
            // })
          } else {
            // alert('/(ㄒoㄒ)/~~，请务必开启消息通知！')
          }
        })
      }
    }
  }

}
</script>

<style scoped>
#bd-preference-admin {
  height: 100%;
  overflow: auto;
  text-align: left;
}
</style>



