<template>
  <div id='bd-top'>
    <img class="avatar" width="35" height="35" :alt="userCurrentThread.visitor.nickname" :src="userCurrentThread.visitor.avatar">
    <p class="name">{{ userCurrentThread.visitor.nickname }}</p>
    <span class="wrapper">
      <el-button @click="showInviteRate()" type="primary" size="mini" :disabled="disabled">查看公告</el-button>
    </span>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as https from '@/apis/http'

export default {
  name: 'bd-top',
  data () {
    return {
      transferDialogVisible: false,
      inviteDialogVisible: false,
      // 当前已经加入会话的客服username
      invitedContactUserids: []
    }
  },
  computed: {
    ...mapGetters([ 'accessToken', 'userCurrentThread', 'userInfo' ]),
    disabled () {
      return this.userCurrentThread.id === 0
    }
  },
  methods: {
    ...mapActions([ 'inviteRate', 'closeThread' ]),
    // 转接会话
    showTransferChat () {
      this.transferDialogVisible = true
    },
    isTransferButtonDisabled (status) {
      return false
    },
    doTransferChat (index, row) {
      // console.log('do transfer:', index, row)
      let uid = row.uid
      let tid = this.userCurrentThread.tid
      https.transfer(uid, tid, this.accessToken, response => {
        console.log(response)
        if (response.status_code === 200) {
          this.$message({ message: '转接会话成功', type: 'success' })
          this.transferDialogVisible = false
        } else {
          this.$message.error(response.message)
        }
      }, error => {
        console.log(error)
        this.$message.error('转接会话错误')
      })
    },
    // 邀请会话
    showInviteChat () {
      this.inviteDialogVisible = true
    },
    doInviteChat () {
      // console.log('do invite')
      let tid = this.userCurrentThread.tid
      https.invite(this.invitedContactUserids, tid, this.accessToken, response => {
        console.log(response)
        if (response.status_code === 200) {
          this.$message({ message: '邀请会话成功', type: 'success' })
          this.inviteDialogVisible = false
        } else {
          this.$message.error(response.message)
        }
      }, error => {
        console.log(error)
        this.$message.error('转接会话错误')
      })
    },
    // 邀请评价
    // TODO: 收到访客评价之后，禁止重复邀请
    showInviteRate () {
      this.inviteRate(this.userCurrentThread.tid)
    },
    showCloseChat () {
      this.$confirm('确定要关闭会话?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // TODO: 首先判断是否是邀请加入的会话，如果是，需要调用退出会话接口，否则调用关闭会话接口
        //
        // 关闭会话
        this.closeThread(this.userCurrentThread.tid)
      }).catch(() => {
      })
    },
    addWorkorder () {
      console.log('addWorkorder')
    },
    handleClose (done) {
      done()
    }
  }

}
</script>

<style scoped>
#bd-top {
  width: 100%;
  height: 50px;
  border-bottom:solid 0.1px #dddddd;
}
#bd-top .avatar {
    margin-top: 7px;
    margin-left: 10px;
    float: left;
}
#bd-top .name {
    width: 100px;
    margin-left: 5px;
    margin-top: 12px;
    float: left;
    font-size: 12px;
}
#bd-top .wrapper {
    float: right;
    padding: 10px 10px;
}
</style>



