<template>
  <div id='bd-profile-admin'>
    <el-form ref="form" :rules="rules" :model="form" label-width="120px" style="margin-top: 20px;">
      <el-form-item v-if="!electron" label="管理后台网址:">
          <el-input type="textarea" v-model="adminUrl" :autosize="{ minRows: 3, maxRows: 5}" style="width: 300px;"></el-input>
      </el-form-item>
      <el-form-item label="企业号" prop="subDomain">
          <el-input v-model="userInfo.subDomain" style="width: 300px;" :disabled="isSubDomainDiabled"></el-input>
      </el-form-item>
      <el-form-item label="用户名" prop="username">
          <el-input v-model="userInfo.username" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="昵称" prop="nickname">
          <el-input v-model="userInfo.nickname" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="头像" prop="avatar">
          <img :src="userInfo.avatar" width="40px" height="40px" style="float: left; margin-right: 15px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary" :disabled="true">点击上传</el-button>
          </el-upload>
      </el-form-item>
      <el-form-item label="手机" prop="mobile">
          <el-input v-model="userInfo.mobile" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
          <el-input v-model="userInfo.email" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <!-- <el-form-item label="角色" prop="email">
          <el-input v-model="form.email"></el-input>
      </el-form-item> -->
      <el-form-item label="个性签名" prop="description">
        <el-input type="textarea" :rows="2" placeholder="这个人很懒,什么也没有写" v-model="userInfo.description" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="欢迎语" prop="welcomeTip">
        <el-input type="textarea" :rows="2" placeholder="这个人很懒,什么也没有写" v-model="userInfo.welcomeTip" style="width: 300px;" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item>
          <el-button type="primary" @click="editProfile">编辑</el-button>
      </el-form-item>
    </el-form>
    <el-dialog
      title="编辑个人资料"
      :visible.sync="editProfileDialogVisible"
      width="30%"
      center>
      <el-form ref="form" :rules="rules" :model="form" label-width="80px" style="margin-top: 20px;">
      <el-form-item label="企业号" prop="subDomain">
          <el-input v-model="form.subDomain" style="width: 200px;" :disabled="isSubDomainDiabled"></el-input>
      </el-form-item>
      <!-- FIXME: 一个管理员账号修改用户名后不能登录，因此暂且禁止修改用户名 -->
      <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username" style="width: 200px;" disabled></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="password">
        <el-input v-model="form.password" auto-complete="off" placeholder="密码" style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item label="昵称" prop="nickname">
          <el-input v-model="form.nickname" style="width: 200px;"></el-input>
      </el-form-item>
      <el-form-item label="头像" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 15px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
            <el-input v-model="form.mobile" style="width: 200px;"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
            <el-input v-model="form.email" style="width: 200px;"></el-input>
        </el-form-item>
        <!-- <el-form-item label="角色" prop="email">
            <el-input v-model="form.email"></el-input>
        </el-form-item> -->
        <el-form-item label="个性签名" prop="description">
          <el-input type="textarea" :rows="2" placeholder="这个人很懒,什么也没有写" v-model="form.description" style="width: 300px;"></el-input>
        </el-form-item>
        <el-form-item label="欢迎语" prop="welcomeTip">
          <el-input type="textarea" :rows="2" placeholder="这个人很懒,什么也没有写" v-model="form.welcomeTip" style="width: 300px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editProfileDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doUpdateProfile">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
// import bus from '@/util/eventbus'

export default {
  name: 'bd-profile-admin',
  data () {
    return {
      editProfileDialogVisible: false,
      form: {
        username: '',
        password: '',
        name: '',
        avatar: '',
        mobile: '',
        email: '',
        description: '',
        welcomeTip: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ],
        avatar: [
          { required: true, message: '上传头像', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入手机号', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '请输入简介', trigger: 'blur' }
        ]
      },
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      isIp: constants.IS_IP,
      electron: constants.IS_ELECTRON
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'mainHost' ]),
    adminUrl () {
      if (this.isIp) {
        return constants.LOGIN_URL
      } else {
        return constants.HTTP_PREFIX + this.userInfo.subDomain + '.' + this.mainHost + '/admin#/login'
      }
    },
    isSaveButtonDisabled () {
      return false
    },
    uploadImageServerUrl () {
      return constants.UPLOAD_IMAGE_URL
    },
    isSubDomainDiabled () {
      return true
    }
  },
  methods: {
    ...mapActions([ 'updateProfile' ]),
    username (usernameAtSubDomain) {
      if (this.userInfo.agent) {
        return usernameAtSubDomain.split('@')[0]
      } else {
        return usernameAtSubDomain
      }
    },
    handleImageUploadSuccess (result, file) {
      console.log('upload image success result:', result, ' file:', file)
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      // console.log('before image upload file:', file)
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    doUpdateProfile () {
      console.log('updateProfile')
      if (this.userInfo.agent) {
        this.form.username = this.form.username + '@' + this.form.subDomain
      }
      this.updateProfile(this.form)
      this.editProfileDialogVisible = false
    },
    editProfile () {
      this.form.subDomain = this.userInfo.subDomain
      if (this.userInfo.agent) {
        this.form.username = this.userInfo.username.split('@')[0]
      } else {
        this.form.username = this.userInfo.username
      }
      this.form.nickname = this.userInfo.nickname
      this.form.avatar = this.userInfo.avatar
      this.form.mobile = this.userInfo.mobile
      this.form.email = this.userInfo.email
      this.form.description = this.userInfo.description
      this.form.welcomeTip = this.userInfo.welcomeTip
      //
      this.editProfileDialogVisible = true
    }
  }
}
</script>

<style scoped>
#bd-profile-admin {
  height: 100%;
  overflow: auto;
  text-align: left;
}
</style>



