import Vue from 'vue'
// import store from '../store'
import Router from 'vue-router'
import store from '@/store'

import Auth from '@/components/Auth'
import Dashboard from '@/components/Dashboard'
// import Layim from '@/components/Layim'

import BdLogin from '@/components/auth/BdLogin'
import BdRegister from '@/components/auth/BdRegister'
import BdForget from '@/components/auth/BdForget'

import Home from '@/components/dashboard/main/home/BdHome'
import Chat from '@/components/dashboard/main/chat/BdChat'
import Robot from '@/components/dashboard/main/robot/BdRobot'
import Statistic from '@/components/dashboard/main/statistic/BdStatistic'
import Feedback from '@/components/dashboard/main/feedback/BdFeedback'
import Support from '@/components/dashboard/main/support/BdSupport'
import Ticket from '@/components/dashboard/main/ticket/BdTicket'
import CallCenter from '@/components/dashboard/main/callcenter/BdCallCenter'
import Crm from '@/components/dashboard/main/crm/BdCrm'
import WenJuan from '@/components/dashboard/main/wenjuan/BdWenjuan'
import Internal from '@/components/dashboard/main/internal/BdInternal'
import ImCloud from '@/components/dashboard/main/imcloud/BdIMCloud'
import Community from '@/components/dashboard/main/community/BdCommunity'
import Everyone from '@/components/dashboard/main/everyone/BdEveryone'
import Conference from '@/components/dashboard/main/conference/BdConference'
import Knowledge from '@/components/dashboard/main/knowledge/BdKnowledge'
import Other from '@/components/dashboard/main/other/BdOther'

import Setting from '@/components/dashboard/main/setting/BdSetting'
import Profile from '@/components/dashboard/main/setting/children/BdProfile'
import Apps from '@/components/dashboard/main/setting/children/BdApps'
import Pay from '@/components/dashboard/main/setting/children/BdPay'
import Agents from '@/components/dashboard/main/setting/children/BdAgents'
import Internals from '@/components/dashboard/main/setting/children/BdInternals'
import RobotAdmin from '@/components/dashboard/main/setting/children/BdRobot'
import CuwAdmin from '@/components/dashboard/main/setting/children/BdCuw'
import Preference from '@/components/dashboard/main/setting/children/BdPreference'
import Taboo from '@/components/dashboard/main/setting/children/BdTaboo'

// import BdLayimMsgbox from '@/components/dashboard/layim/BdMsgbox'
// import BdLayimFind from '@/components/dashboard/layim/BdFind'
// import BdLayimChatlog from '@/components/dashboard/layim/BdChatlog'

import NotFound from '@/components/common/404'

import * as constants from '@/util/constants'

Vue.use(Router)

// 在管理后台页面，检查是否已经登录，如果没有登录，则跳转到/login
function requireAuth (to, from, next) {
  if (!store.getters.connected) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
}

// 在登录界面和注册界面，检查是否已经登录，如果已经登录，则直接跳转/admin
function beforeAuth (to, from, next) {
  if (!store.getters.connected) {
    next()
  } else {
    next({
      path: '/admin',
      query: { redirect: to.fullPath }
    })
  }
}

const router = new Router({
  routes: [
    {
      path: '/',
      component: Auth,
      children: [
        {
          path: '',
          component: BdLogin,
          beforeEnter: beforeAuth,
          meta: { title: '登录 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'login',
          name: 'login', // 不能注释掉，route-link中有使用
          component: BdLogin,
          beforeEnter: beforeAuth,
          meta: { title: '登录 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'register',
          name: 'register',
          component: BdRegister,
          beforeEnter: beforeAuth,
          meta: { title: '注册 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'forget',
          name: 'forget',
          component: BdForget,
          beforeEnter: beforeAuth,
          meta: { title: '找回密码 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: '/logout',
          name: 'logout',
          beforeEnter (to, from, next) {
            store.dispatch('logout').then(() => {
              console.log('go to login page')
              next('/login')
            })
          }
        }
      ]
    },
    // {
    //   path: '/layim',
    //   component: Layim,
    //   children: [{
    //     path: '',
    //     component: BdLayimFind,
    //     meta: { title: '查找' + constants.DEFAULT_TITLE }
    //   }, {
    //     path: 'find',
    //     component: BdLayimFind,
    //     meta: { title: '查找' + constants.DEFAULT_TITLE }
    //   }, {
    //     path: 'msgbox',
    //     component: BdLayimMsgbox,
    //     meta: { title: '消息通知' + constants.DEFAULT_TITLE }
    //   }, {
    //     path: 'chatlog',
    //     component: BdLayimChatlog,
    //     meta: { title: '聊天记录' + constants.DEFAULT_TITLE }
    //   }]
    // },
    {
      path: '/admin',
      component: Dashboard,
      beforeEnter: requireAuth,
      children: [
        {
          path: '',
          component: Chat,
          meta: { title: '人工客服 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'home',
          name: 'home',
          component: Home,
          meta: { title: '我的首页 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'statistic',
          name: 'statistic',
          component: Statistic,
          meta: { title: '绩效数据 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'chat',
          name: 'chat',
          component: Chat,
          meta: { title: '人工客服 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'robot',
          name: 'robot',
          component: Robot,
          meta: { title: '智能客服 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'support',
          name: 'support',
          component: Support,
          meta: { title: '知识库-帮助中心 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'feedback',
          name: 'feedback',
          component: Feedback,
          meta: { title: '意见反馈 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'ticket',
          name: 'ticket',
          component: Ticket,
          meta: { title: '工单系统 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'callcenter',
          name: 'callcenter',
          component: CallCenter,
          meta: { title: '呼叫中心 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'crm',
          name: 'crm',
          component: Crm,
          meta: { title: '客户管理 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'community',
          name: 'community',
          component: Community,
          meta: { title: '讨论社区 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'wenjuan',
          name: 'wenjuan',
          component: WenJuan,
          meta: { title: '调查问卷 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'internal',
          name: 'internal',
          component: Internal,
          meta: { title: '内部协同 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'imcloud',
          name: 'imcloud',
          component: ImCloud,
          meta: { title: '云通讯 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'conference',
          name: 'conference',
          component: Conference,
          meta: { title: '视频会议 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'everyone',
          name: 'everyone',
          component: Everyone,
          meta: { title: '人人客服 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'knowledge',
          name: 'knowledge',
          component: Knowledge,
          meta: { title: '知识库 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'other',
          name: 'other',
          component: Other,
          meta: { title: '其他功能 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'setting',
          component: Setting,
          children: [{
            path: '',
            component: Profile,
            meta: { title: '个人资料 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'app',
            component: Apps,
            meta: { title: '应用管理 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'agent',
            component: Agents,
            meta: { title: '客服管理 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'internal',
            component: Internals,
            meta: { title: '协同管理 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'robot',
            component: RobotAdmin,
            meta: { title: '智能客服 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'cuw',
            component: CuwAdmin,
            meta: { title: ' 常用语管理 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'taboo',
            component: Taboo,
            meta: { title: ' 敏感词管理 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'profile',
            component: Profile,
            meta: { title: '个人资料 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'pay',
            component: Pay,
            meta: { title: '费用明细 - ' + constants.DEFAULT_TITLE }
          },
          {
            path: 'preference',
            component: Preference,
            meta: { title: '常用设置 - ' + constants.DEFAULT_TITLE }
          }]
        }]
    },
    {
      path: '*',
      component: NotFound
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    // https://router.vuejs.org/zh/guide/advanced/scroll-behavior.html
    // return 期望滚动到哪个的位置
  }
})

router.beforeEach((to, from, next) => {
  // console.log('beforeEach to', to)
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router



