<template>
  <div id='bd-register'>
    <el-main>
      <el-row v-if="firstStep">
        <el-col :span="8"><div class="grid-content"></div></el-col>
        <el-col :span="6">
          <el-form :model="emailForm" status-icon :rules="registerRoles" ref="emailForm" label-width="100px">
            <el-form-item label="邮箱" prop="email">
              <el-input type="email" placeholder="请输入邮箱" v-model="emailForm.email" clearable style="width: 300px;"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="emailForm.password" auto-complete="off" style="width: 300px;" clearable></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="rePassword">
              <el-input type="password" v-model="emailForm.rePassword" auto-complete="off" style="width: 300px;" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="routeLogin">登录</el-button>
              <el-button type="primary" @click="register" :loading="logining">注册</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="8"><div class="grid-content"></div></el-col>
      </el-row>
      <!-- TODO: 增加公司名、公司网站、公司人数 联系电话 等注册信息 -->
      <el-row v-if="secondStep">
        <el-col :span="10"><div class="grid-content"></div></el-col>
        <el-col :span="4">
          <el-form label-width="150px">
            <el-form-item label="请牢记您的企业号：">{{ this.subDomainForm.sub_domain }}</el-form-item>
            <el-form-item>
              <el-button @click="routeLogin">去登录</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="10"><div class="grid-content"></div></el-col>
      </el-row>
    </el-main>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as https from '@/apis/http'
import * as constants from '@/util/constants'

export default {
  name: 'bd-register',
  data () {
    var validateEmail = (rule, value, callback) => {
      // 验证邮箱唯一性
      https.emailValidate(this.emailForm.email,
        response => {
          console.log(response)
          if (response.status_code === 200) {
            callback()
          } else {
            // 邮箱已经存在，提示用户重新填写
            callback(new Error('邮箱已经存在，请重新填写'))
          }
        }, error => {
          console.log(error)
        })
    }
    var validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.emailForm.rePassword !== '') {
          this.$refs.emailForm.validateField('rePassword')
        }
        callback()
      }
    }
    var validateRePassword = (rule, value, callback) => {
      if (value !== this.emailForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      // 是否登录中
      logining: false,
      // models
      emailForm: {
        email: '',
        password: '',
        rePassword: ''
      },
      subDomainForm: {
        sub_domain: 'vip'
      },
      // 验证规则
      registerRoles: {
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'change' },
          { validator: validateEmail, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 16, message: '长度在 6 到 16 个字符之间', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        rePassword: [
          { required: true, message: '请输入确认密码', trigger: 'blur' },
          { validator: validateRePassword, trigger: 'blur' }
        ]
      },
      // 验证邮箱
      firstStep: true,
      // 显示企业号
      secondStep: false
    }
  },
  computed: {
    ...mapGetters([ 'mainHost' ])
  },
  methods: {
    ...mapActions([
      'updatePassportToken'
    ]),
    register () {
      // 首先验证form是否通过，然后调用注册接口
      this.$refs['emailForm'].validate((valid) => {
        if (valid) {
          this.logining = true
          https.register(this.emailForm.email.toLowerCase(), this.emailForm.password,
            response => {
              console.log('register success:', response)
              this.subDomainForm.sub_domain = response.data.subDomain
              this.$message({message: '注册成功', type: 'success'})
              this.firstStep = false
              this.secondStep = true
              //
              this.logining = false
            }, error => {
              console.log('register error:', error)
              this.logining = false
            })
        } else {
          return false
        }
      })
    },
    routeLogin () {
      // 跳转登录页面
      if (constants.IS_ELECTRON) {
        this.$router.replace('/login')
      } else {
        window.location.href = constants.HTTP_PREFIX + this.subDomainForm.sub_domain + '.' + this.mainHost + '/login'
      }
    }
  }

}
</script>

<style scoped>
#bd-register {
  margin-top: 150px;
}
.grid-content {
  min-height: 36px;
}
</style>



