<template>
  <div id='bd-statistic-thread'>
    <!-- TODO: 日报、周报、月报 -->
    <!-- TODO: 维度：客服账号、工作组 -->
    <!-- TODO: 数据搜索过滤 -->
    <!-- TODO: 饼图、变化曲线图展示 -->
    <!-- TODO: 增加导出 -->
    <div style="float: left;">
      <el-date-picker size="mini" style="width: 155px; margin-right: 10px;"
        v-model="filterDate"
        align="right"
        type="date"
        placeholder="选择日期"
        :picker-options="filterDatePickerOptions">
      </el-date-picker>
      <el-input size="mini" v-model="filterRealName" placeholder="客服真名" style="width: 155px; margin-right: 10px;" clearable></el-input>
      <el-button size="mini" type="primary" icon="el-icon-search" @click="handleQuery">查询</el-button>
      <el-button size="mini" type="primary" icon="el-icon-download" @click="handleExport">导出</el-button>
    </div>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="statisticThreads.content"
      style="width: 100%; margin-top: 10px;">
      <el-table-column type="index" width="40"></el-table-column>
      <el-table-column prop="date" label="日期" sortable>
        <template slot-scope="scope">
          <span>{{ scope.row.date }} {{ scope.row.hour }}点</span>
        </template>
      </el-table-column>
      <el-table-column prop="user.realName" label="客服真名" sortable></el-table-column>
      <!-- <el-table-column prop="name" label="会话总数" sortable></el-table-column> -->
      <el-table-column prop="totalMessageCount" label="总消息数" sortable></el-table-column>
      <el-table-column prop="visitorMessageCount" label="访客消息数" sortable></el-table-column>
      <el-table-column prop="agentMessageCount" label="客服消息数" sortable></el-table-column>
      <el-table-column prop="agentWordCount" label="客服字数" sortable></el-table-column>
      <!-- <el-table-column prop="name" label="平均等待时间" sortable></el-table-column> -->
      <el-table-column prop="acceptRateLt1m" label="1分钟接通率" sortable></el-table-column>
      <el-table-column prop="acceptRateLt3m" label="3分钟接通率" sortable></el-table-column>
      <el-table-column prop="acceptRateLt5m" label="5分钟接通率" sortable></el-table-column>
      <el-table-column prop="acceptRateGt5m" label="超时接通率" sortable></el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'bd-statistic-thread',
  data () {
    return {
      //
      filterDate: '',
      filterRealName: '',
      filterDatePickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      }
    }
  },
  computed: {
    ...mapGetters([ 'statisticThreads' ])
  },
  methods: {
    handleQuery () {
      //
    },
    handleExport () {
      //
    }
  }

}
</script>

<style scoped>
#bd-statistic-thread {
  height: 100%;
  overflow-y: auto;
  text-align: left;
}
</style>



