<template>
  <div id='bd-monitor'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-right: 10px;" plain>帮助文档</el-button>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="monitors.content" style="width: 100%">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="visitor.nickname" label="昵称">
        <template slot-scope="scope">
          <span>{{ scope.row.visitor.client }}</span>
          <span>{{ scope.row.visitor.nickname }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="workGroup.nickname" label="工作组" sortable></el-table-column>
      <el-table-column prop="content" label="最后消息内容" sortable></el-table-column>
      <el-table-column prop="timestamp" label="最后消息时间" sortable></el-table-column>
      <el-table-column prop="timestamp" label="消息条数" sortable></el-table-column>
      <el-table-column prop="createdAt" label="进入时间" sortable></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'bd-monitor',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters([ 'monitors' ])
  },
  methods: {
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
    }
  }

}
</script>

<style scoped>
#bd-monitor {
  height: 100%;
  text-align: left;
}
</style>



