<template>
  <div id='bd-top'>
    <img class="avatar" width="35" height="35" :alt="userCurrentThread.visitor.nickname" :src="userCurrentThread.visitor.avatar">
    <span class="nickname">{{ userCurrentThread.visitor.nickname }}</span>
    <span class="wrapper">
      <!-- TODO: 增加一键机器人接管 -->
      <!-- TODO: 宽度不够的情况下，按钮不能一行安排，有待优化处理 -->
      <!-- <el-button v-if="showAuthority.debug" @click="addCrm()" type="success" size="mini" :disabled="disabled"><i class="iconfont icon-crm" style="font-size: 10px;"></i> CRM</el-button> -->
      <el-button v-if="showAuthority.debug" @click="addWorkorder()" type="warning" size="mini" :disabled="disabled"><i class="iconfont icon-workorder" style="font-size: 10px;"></i> 工单</el-button>
      <!-- <el-button v-if="showAuthority.debug" @click="showInviteChat()" type="warning" size="mini" :disabled="disabled"><i class="iconfont icon-invite" style="font-size: 10px;"></i> 邀请</el-button> -->
      <el-button @click="showTransferChat()" type="success" size="mini" icon="el-icon-sort" :disabled="disabled">转接</el-button>
      <el-button @click="showNoteDialog()" type="info" size="mini" icon="el-icon-document" :disabled="disabled">备注</el-button>
      <!-- 迁移到输入框上方 -->
      <!-- <el-button @click="showInviteRate()" type="warning" size="mini" icon="el-icon-star-off" :disabled="disabled">邀评</el-button> -->
      <el-button @click="showCloseChat()" type="danger" size="mini" icon="el-icon-close" :disabled="disabled">关闭</el-button>
    </span>

    <el-dialog
      title="备注"
      :visible.sync="noteDialogVisible"
      width="30%"
      :before-close="handleClose"
      center>
      <el-input type="textarea"
        :autosize="{ minRows: 4, maxRows: 8}"
        placeholder="请输入备注内容"
        v-model="noteContent">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="noteDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doSaveNote">确 定</el-button>
      </span>
    </el-dialog>

    <!-- TODO: 过滤掉离线坐席，增加翻页 -->
    <el-dialog style="text-align: left;"
        title="转接会话给当前在线客服人员"
        width="30%"
        :visible.sync="transferDialogVisible"
        :before-close="handleClose" center>
        <el-table :data="contactTransferTableData" empty-text="无客服在线">
          <el-table-column type="index"></el-table-column>
          <!-- <el-table-column property="avatar" label="头像"></el-table-column> -->
          <el-table-column property="realName" label="客服名"></el-table-column>
          <!-- <el-table-column property="status" label="在线状态"></el-table-column> -->
          <!-- <el-table-column property="name" label="忙闲程度"></el-table-column> -->
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button type="primary" size="small"
              :disabled="isTransferButtonDisabled(scope.row.status)"
              @click="doTransferChat(scope.$index, scope.row)">
                转接
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="transferDialogVisible = false">取 消</el-button>
        </span>
    </el-dialog>

    <!-- TODO: 过滤掉离线坐席, 增加翻页 -->
    <el-dialog style="text-align: left;"
        title="邀请其他在线客服人员加入会话"
        width="30%"
        :visible.sync="inviteDialogVisible"
        :before-close="handleClose" center>
        <div class="invite-dialog-body">
          <el-transfer
            v-model="invitedContactUserids"
            :titles="['当前在线客服', '被邀请客服']"
            :data="contactInviteData">
          </el-transfer>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="inviteDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="doInviteChat()">确 定</el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
// import * as constants from '@/util/constants'
import * as https from '@/apis/http'

export default {
  name: 'bd-top',
  data () {
    return {
      noteDialogVisible: false,
      noteContent: '',
      transferDialogVisible: false,
      inviteDialogVisible: false,
      // 当前已经加入会话的客服username
      invitedContactUserids: []
    }
  },
  computed: {
    ...mapGetters([ 'accessToken', 'userCurrentChatType', 'userCurrentThread',
      'userCurrentContact', 'userCurrentGroup', 'userInfo', 'onlineAgents', 'showAuthority' ]),
    disabled () {
      return this.userCurrentThread.id === 0
    },
    // 转接客服数据
    contactTransferTableData () {
      // 非在线状态的客服不能转接
      var transferContact = []
      for (var i = this.onlineAgents.content.length - 1; i >= 0; i--) {
        let uid = this.onlineAgents.content[i].uid
        let robot = this.onlineAgents.content[i].robot
        // 过滤掉自己和机器人
        if (this.userInfo.uid === uid || robot) {
          continue
        }
        let realName = this.onlineAgents.content[i].realName
        let status = this.onlineAgents.content[i].acceptStatus
        let contact = {uid, realName, status}
        transferContact.push(contact)
      }
      return transferContact
    },
    // 邀请客服数据
    contactInviteData () {
      // 非在线状态的客服不能邀请
      var inviteContact = []
      for (var i = this.onlineAgents.content.length - 1; i >= 0; i--) {
        let key = this.onlineAgents.content[i].uid
        let robot = this.onlineAgents.content[i].robot
        if (this.userInfo.uid === key || robot) {
          continue
        }
        let label = this.onlineAgents.content[i].realName
        let disabled = false
        let contact = {key, label, disabled}
        inviteContact.push(contact)
      }
      return inviteContact
    }
  },
  methods: {
    ...mapActions([ 'inviteRate', 'closeThread', 'setNote', 'getOnlineAgents' ]),
    // 转接会话
    showTransferChat () {
      this.transferDialogVisible = true
      //
      let payload = { page: 0, size: 100 }
      this.getOnlineAgents(payload)
    },
    isTransferButtonDisabled (status) {
      return false
    },
    doTransferChat (index, row) {
      // console.log('do transfer:', index, row)
      let uid = row.uid
      let tid = this.userCurrentThread.tid
      https.transfer(uid, tid, this.accessToken, response => {
        console.log(response)
        if (response.status_code === 200) {
          this.$message({ message: '转接会话成功', type: 'success' })
          this.transferDialogVisible = false
        } else {
          this.$message.error(response.message)
        }
      }, error => {
        console.log(error)
        this.$message.error('转接会话错误')
      })
    },
    showNoteDialog () {
      console.log('备注')
      this.noteDialogVisible = true
    },
    doSaveNote () {
      console.log('save note')
      let payload = { uid: this.userCurrentThread.visitor.uid, value: this.noteContent }
      this.setNote(payload)
      this.noteDialogVisible = false
    },
    // 邀请会话
    showInviteChat () {
      this.inviteDialogVisible = true
      //
      let payload = { page: 0, size: 100 }
      this.getOnlineAgents(payload)
    },
    doInviteChat () {
      // console.log('do invite')
      let tid = this.userCurrentThread.tid
      https.invite(this.invitedContactUserids, tid, this.accessToken, response => {
        console.log(response)
        if (response.status_code === 200) {
          this.$message({ message: '邀请会话成功', type: 'success' })
          this.inviteDialogVisible = false
        } else {
          this.$message.error(response.message)
        }
      }, error => {
        console.log(error)
        this.$message.error('转接会话错误')
      })
    },
    // 邀请评价
    // TODO: 收到访客评价之后，禁止重复邀请
    showInviteRate () {
      this.inviteRate(this.userCurrentThread.tid)
    },
    showCloseChat () {
      this.$confirm('确定要关闭会话?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // TODO: 首先判断是否是邀请加入的会话，如果是，需要调用退出会话接口，否则调用关闭会话接口
        //
        // 关闭会话
        this.closeThread(this.userCurrentThread.tid)
      }).catch(() => {
      })
    },
    addWorkorder () {
      console.log('addWorkorder')
    },
    addCrm () {
      console.log('add crm')
    },
    handleClose (done) {
      done()
    }
  }

}
</script>

<style scoped>
#bd-top {
  width: 100%;
  height: 50px;
  border-bottom:solid 0.1px #dddddd;
}
#bd-top .avatar {
  margin-top: 7px;
  margin-left: 10px;
  float: left;
}
#bd-top .nickname {
  max-width: 100px;
  margin-left: 5px;
  margin-top: 12px;
  float: left;
  font-size: 12px;
}
#bd-top .wrapper {
  float: right;
  margin-right: 10px;
  margin-top: 10px;
  overflow: hidden;
  height: 40px;
}
</style>



