<template>
  <div id='bd-feedback'>
    <!-- TODO: 注册账号时，后台自动生成一个默认模板 -->
    <!-- TODO: 回放还原技术处理用户反馈问题：https://mp.weixin.qq.com/s/mljdJ18u16sFYtoOvxCHfg -->
    <el-tabs v-model="activeName" style="height: 100%;">
      <el-tab-pane label="意见反馈" name="all" style="height: 100%;">
        <bd-all></bd-all>
      </el-tab-pane>
      <!-- <el-tab-pane label="反馈记录" name="record" style="height: 100%;">
        <bd-record></bd-record>
      </el-tab-pane> -->
      <!-- <el-tab-pane label="嵌入代码" name="code" style="height: 100%;">
        <bd-code></bd-code>
      </el-tab-pane> -->
    </el-tabs>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import BdAll from '@/components/dashboard/main/feedback/children/BdAll'
import BdCode from '@/components/dashboard/main/feedback/children/BdCode'
import BdRecord from '@/components/dashboard/main/feedback/children/BdRecord'

export default {
  name: 'bd-feedback',
  components: { BdAll, BdCode, BdRecord },
  data () {
    return {
      activeName: 'all'
    }
  },
  computed: {
    ...mapGetters([])
  },
  methods: {
    ...mapActions([ 'changeCurrentMainView' ])
  },
  created () {
  },
  mounted () {
    this.changeCurrentMainView('feedback')
  }
}
</script>

<style scoped>
#bd-feedback {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
}
#right-col{
  height: 100%;
}
</style>



