<template>
  <!-- TODO: 增加离线消息推送，上传pem文件页面 -->
  <div id='bd-app-admin'>
    <el-row>
      <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="showAddDialog">添加应用</el-button>
    </el-row>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="apps.content" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="aid" label="唯一ID(aid)"></el-table-column>
      <el-table-column prop="name" label="应用名"></el-table-column>
      <el-table-column prop="key" label="AppKey"></el-table-column>
      <el-table-column prop="version" label="版本号"></el-table-column>
      <el-table-column prop="tip" label="升级内容"></el-table-column>
      <el-table-column prop="url" label="下载网址"></el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <span>{{ formatStatus(scope.row.status) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="platform" label="平台">
        <template slot-scope="scope">
          <span>{{ formatPlatform(scope.row.platform) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="description" label="描述"></el-table-column>
      <!-- <el-table-column prop="updatedAt" label="更新时间"></el-table-column> -->
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title="添加应用"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="80px">
        <el-form-item label="应用名称" prop="name">
          <el-input v-model="form.name" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="头像" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="状态" prop="status">
          <el-radio v-model="form.status" v-for="status in statuses" :key="status.id" :label="status.value">{{ status.name }}</el-radio>
        </el-form-item>
        <el-form-item label="平台" prop="platform">
          <el-radio v-model="form.platform" v-for="platform in platforms" :key="platform.id" :label="platform.value">{{ platform.name }}</el-radio>
        </el-form-item>
        <el-form-item label="版本号" prop="version">
          <el-input v-model="form.version" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="升级内容" prop="tip">
          <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 5}" v-model="form.tip" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="下载网址" prop="url">
          <el-input v-model="form.url" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="应用描述" prop="description">
          <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 10}" v-model="form.description" style="width: 350px;"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addApp">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-app-admin',
  data () {
    return {
      dialogVisible: false,
      isEdit: false,
      form: {
        aid: '',
        name: '',
        avatar: constants.DEFAULT_IOS_AVATAR_URL,
        version: '',
        tip: '',
        url: '',
        status: 'debug',
        platform: 'both',
        description: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入网站名称', trigger: 'blur' }
        ],
        // url: [
        //   { required: true, message: '请输入网址', trigger: 'blur' }
        // ],
        description: [
          { required: true, message: '请输入网站简介', trigger: 'blur' }
        ]
      },
      statuses: [
        { id: 1, value: 'debug', name: '测试中' },
        { id: 2, value: 'release', name: '已发布' },
        { id: 3, value: 'version', name: '新版本' }
      ],
      platforms: [
        { id: 1, value: 'android', name: '安卓' },
        { id: 2, value: 'ios', name: '苹果' },
        { id: 3, value: 'both', name: '安卓+苹果' }
      ],
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'apps' ])
  },
  methods: {
    ...mapActions([ 'getApps', 'createApp', 'updateApp', 'deleteApp' ]),
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201808252118181')
    },
    formatStatus (status) {
      return status === 'debug' ? '测试中' : (status === 'release' ? '已发布' : '新版本')
    },
    formatPlatform (platform) {
      return platform === 'both' ? '安卓+苹果' : (platform === 'android' ? '安卓' : '苹果')
    },
    showAddDialog () {
      this.dialogVisible = true
      this.isEdit = false
      //
      this.form.aid = ''
      this.form.name = ''
      this.form.avatar = constants.DEFAULT_IOS_AVATAR_URL
      this.form.version = ''
      this.form.tip = ''
      this.form.url = ''
      this.form.status = 'debug'
      this.form.platform = 'both'
      this.form.description = ''
    },
    addApp () {
      this.dialogVisible = false
      if (this.isEdit) {
        this.updateApp(this.form)
      } else {
        this.createApp(this.form)
      }
    },
    handleEdit (index, row) {
      console.log(index, row)
      //
      this.form.aid = row.aid
      this.form.name = row.name
      this.form.avatar = row.avatar
      this.form.version = row.version
      this.form.tip = row.tip
      this.form.url = row.url
      this.form.status = row.status
      this.form.platform = row.platform
      this.form.description = row.description
      //
      this.isEdit = true
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteApp(row.aid)
      }).catch(() => {
        //
      })
    }
  }

}
</script>

<style scoped>
#bd-app-admin {
  height: 100%;
  text-align: left;
}
</style>



