<template>
  <!-- TODO: 增加设置登录状态，支持登录之后默认不接待状态 -->
  <div id='bd-login'>
    <el-main>
      <el-row>
        <el-col :span="8"><div class="grid-content"></div></el-col>
        <el-col :span="6">
          <el-form :model="loginForm" status-icon :rules="rules" ref="loginForm" label-width="100px">
            <el-form-item label="企业号" prop="sub_domain">
              <el-input v-model="loginForm.sub_domain" placeholder="请填写企业号" style="width: 250px;" clearable></el-input>
            </el-form-item>
            <el-form-item label="用户名/邮箱" prop="email">
              <el-input v-model="loginForm.email" placeholder="用户名/邮箱" style="width: 250px;" clearable></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input type="password" v-model="loginForm.password" @keyup.enter.native="onEnter" auto-complete="off" placeholder="密码" style="width: 250px;" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="login" :loading="logining">登录</el-button>
              <el-button @click="register">注册</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="8"><div class="grid-content"></div></el-col>
      </el-row>
    </el-main>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as https from '@/apis/http'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-login',
  data () {
    return {
      activeIndex: '0',
      errorCode: 0,
      is400: false,
      isIp: constants.IS_IP,
      // 是否登录中
      logining: false,
      loginForm: {
        sub_domain: location.hostname.split('.').length > 2 ? location.hostname.split('.')[0] : '',
        email: '',
        password: ''
      },
      rules: {
        sub_domain: [
          { required: true, message: '请输入企业号', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入用户名/邮箱', trigger: 'blur' },
          // { type: 'email', message: '请输入正确的邮箱地址', trigger: 'change' },
          { min: 5, message: '长度不能少于5个字符', trigger: 'blur' }
          // TODO: 此处增加验证用户账号是否已经被禁用，如已禁用，则禁止登录
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters([ 'mainHost' ]),
    subDomain () {
      // 如果包含二级域名，那么返回二级域名，否则返回空
      return location.hostname.split('.').length > 2 ? location.hostname.split('.')[0] : ''
    }
  },
  methods: {
    ...mapActions([
      'updatePassportToken'
    ]),
    onEnter () {
      this.login()
    },
    login () {
      this.$refs['loginForm'].validate((valid) => {
        if (valid) {
          let username = this.loginForm.email.toLowerCase()
          // 判断是否是email，如果不是email，则添加后缀 ‘@subDomain’
          if (username.indexOf('@') < 0) {
            username = username + '@' + this.loginForm.sub_domain
          }
          this.logining = true
          https.login(username, this.loginForm.password,
            response => {
              console.log('login success:', response)
              this.logining = false
              if (response === null || response === undefined) {
                if (this.errorCode === 500) {
                  return
                }
                if (!this.is400) {
                  // 非400错误的情况下，才会显示超时
                  bus.$emit(constants.EVENT_BUS_LOGIN_TIMEOUT, '')
                }
                return
              }
              // Electron 显示tray图标
              if (constants.IS_ELECTRON) {
                ipcRenderer.send('show-tray', '')
              }
              // 务必设置一下
              localStorage.access_token = response.access_token
              this.updatePassportToken(response)
              // TODO: 跳转二级域名
              if (constants.IS_ELECTRON) {
                this.$router.replace('/admin')
              } else if (this.isIp) {
                // 管理后台为ip地址
                window.location.href = constants.ADMIN_URL
              } else {
                // 管理后台需要使用二级域名
                window.location.href = constants.HTTP_PREFIX + this.loginForm.sub_domain + '.' + this.mainHost + '/admin'
              }
            }, error => {
              console.log('login error:', error)
              this.logining = false
            })
        } else {
          return false
        }
      })
    },
    register () {
      this.$router.replace('/register')
    }
  },
  mounted () {
    // 用户名或密码错误
    bus.$on(constants.EVENT_BUS_LOGIN_ERROR_400, message => {
      this.is400 = true
      this.errorCode = 400
      this.$message.error('用户名或密码错误')
    })
    //
    bus.$on(constants.EVENT_BUS_LOGIN_TIMEOUT, message => {
      this.$message.error('登录超时，请检查网络或稍后重试')
    })
    //
    bus.$on(constants.EVENT_BUS_SERVER_ERROR_500, message => {
      this.errorCode = 500
      this.$message.error('服务器错误')
    })
  }
}
</script>

<style scoped>
#bd-login {
  margin-top: 150px;
}
.grid-content {
  min-height: 36px;
}
</style>



