<template>
  <div id='bd-member'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" plain>帮助文档</el-button>
    <el-button size="mini" type="primary" style="float: left;" @click="addAgentDialog()">添加账号</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="agents.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="agents.content" style="width: 100%">
      <el-table-column type="index" width="20"></el-table-column>
      <el-table-column prop="subDomain" label="企业号" width="100"></el-table-column>
      <el-table-column prop="username" label="用户名(用于登录)">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" height="15px" width="15px"/>
          <span>{{ username(scope.row.username) }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="realName" label="真名"></el-table-column>
      <el-table-column prop="workGroups" label="部门">
        <template slot-scope="scope">
          <el-tag v-for="workGroup in scope.row.workGroups" :key="workGroup.id" size="mini" style="margin-right:5px;">{{ workGroup.nickname }}</el-tag>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="name" label="在线状态" sortable></el-table-column> -->
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="!isSelf(scope.row.username)" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      layout="prev, pager, next"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :total="agents.totalElements">
    </el-pagination>
    <el-dialog
      title="添加客服账号"
      :visible.sync="dialogVisible"
      width="30%"
      center>
      <el-form ref="form" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="登录用户名" prop="username">
          <el-input v-model="form.username" placeholder="用于登录客服后台"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="form.password" auto-complete="off" placeholder="密码"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="form.email" placeholder="邮箱"></el-input>
        </el-form-item>
        <el-form-item label="头像" prop="avatar">
          <img :src="form.avatar" width="40px" height="40px" style="float: left; margin-right: 10px;"/>
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="真名" prop="realName">
          <el-input v-model="form.realName"></el-input>
        </el-form-item>
        <el-form-item label="所属部门" prop="workGroups">
          <el-radio-group v-model="form.workGroups">
            <el-radio v-for="workGroup in workGroups.content" :key="workGroup.id" :label="workGroup.wid">{{ workGroup.nickname }}</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addAgent()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
// import * as https from '@/apis/http'

export default {
  name: 'bd-member',
  data () {
    // var validateUsername = (rule, value, callback) => {
    //   https.agentnameValidate(this.form.username, this.accessToken, response => {
    //     console.log(response)
    //     if (response.status_code === 200) {
    //       callback()
    //     } else {
    //       callback(new Error('用户名已经存在，请重新填写'))
    //     }
    //   }, error => {
    //     console.log(error)
    //   })
    // }
    return {
      currentPage: 1,
      isEdit: false,
      dialogVisible: false,
      form: {
        id: 0,
        username: '',
        password: '',
        email: '',
        avatar: constants.DEFAULT_AVATAR,
        realName: '',
        name: '',
        maxThreadCount: 10,
        workGroups: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 5, message: '长度最少5个字符', trigger: 'blur' }
          // { validator: validateUsername, trigger: 'blur' }
        ],
        email: [
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'change' }
        ],
        realName: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        workGroups: [
          { required: true, message: '请至少选择一个工作组', trigger: 'change' }
        ]
      },
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'accessToken', 'agents', 'workGroups', 'roles' ])
  },
  methods: {
    ...mapActions([ 'createAgent', 'updateAgent', 'deleteAgent', 'getAgents' ]),
    username (usernameAtSubDomain) {
      return usernameAtSubDomain.split('@')[0]
    },
    isSelf (username) {
      return username === this.userInfo.username
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    handleMaxThreadCountChange (value) {
      // console.log(value)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = {
        page: this.currentPage - 1,
        size: 20
      }
      this.getAgents(payload)
    },
    addAgentDialog () {
      this.isEdit = false
      //
      this.form.id = ''
      this.form.username = ''
      this.form.password = ''
      this.form.email = ''
      this.form.avatar = constants.DEFAULT_AVATAR
      this.form.realName = ''
      this.form.nickname = ''
      this.form.maxThreadCount = 10
      this.form.workGroups = []
      this.form.role = ''
      //
      this.dialogVisible = true
    },
    addAgent () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogVisible = false
          if (this.isEdit) {
            this.updateAgent(this.form)
          } else {
            this.createAgent(this.form)
          }
        } else {
          return false
        }
      })
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.isEdit = true
      this.form.id = row.id
      this.form.username = row.username.split('@')[0]
      this.form.password = ''
      this.form.email = row.email
      this.form.avatar = row.avatar
      this.form.realName = row.realName
      this.form.nickname = row.nickname
      this.form.maxThreadCount = row.maxThreadCount
      this.form.email = row.email
      this.form.mobile = row.mobile
      //
      this.form.workGroups = []
      for (let i = 0; i < row.workGroups.length; i++) {
        const workGroup = row.workGroups[i]
        this.form.workGroups.push(workGroup.wid)
      }
      // TODO: 是否应该使用数组？
      let roleValue = ''
      for (let i = 0; i < row.roles.length; i++) {
        const role = row.roles[i]
        if (role.value !== constants.ROLE_FREE) {
          roleValue = (role.value)
        }
      }
      this.form.role = roleValue
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteAgent(row.id)
      }).catch(() => {
        //
      })
    }
  }

}
</script>

<style scoped>
#bd-member {
  height: 100%;
  text-align: left;
}
</style>



