<template>
  <div id='bd-mini-admin'>
    <!--
      TODO:提供三种方式对接:
      1.授权开放平台
      2.提供后台推送URL
      3.提供转接URL，即公众号或小程序已经对接第三方的情况下，供第三方开发者转发消息到此
    -->
    <el-row>
      <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="gotoOAuth" disabled>方法一：授权绑定(待上线)</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="showAddDialog">方法二：配置服务器</el-button>
      <el-button size="mini" type="primary" style="float: left;" @click="getApi" disabled>方法三：消息接口(待上线)</el-button>
    </el-row>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="minis.content" style="width: 100%;">
      <el-table-column type="index" width="50"></el-table-column>
      <!-- <el-table-column prop="wid" label="唯一ID"></el-table-column> -->
      <el-table-column prop="nickname" label="小程序"></el-table-column>
      <el-table-column prop="url" label="服务器地址(URL)"></el-table-column>
      <el-table-column prop="token" label="令牌(Token)"></el-table-column>
      <el-table-column prop="userName" label="原始Id"></el-table-column>
      <el-table-column prop="appId" label="AppId"></el-table-column>
      <el-table-column prop="workGroup" label="工作组">
        <template slot-scope="scope">
          <el-tag size="mini" style="margin-right:5px;">{{ scope.row.workGroup.nickname }}</el-tag>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="description" label="描述"></el-table-column> -->
      <el-table-column prop="updatedAt" label="更新时间"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button v-if="!scope.row.defaulted" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="添加公众号" :visible.sync="dialogVisible" width="38%" center>
      <p>小程序后台->设置->基本设置</p>
      <el-form ref="form" :model="form" :rules="rules" label-width="120px">
        <el-form-item label="名称" prop="nickname">
          <el-input v-model="form.nickname" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="介绍" prop="description">
          <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 10}" v-model="form.description" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="主体信息" prop="company">
          <el-input v-model="form.company" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="原始Id" prop="original_id">
          <el-input v-model="form.original_id" style="width: 350px;"></el-input>
        </el-form-item>
        <p>小程序后台->设置->开发设置</p>
        <el-form-item label="AppId" prop="app_id">
          <el-input v-model="form.app_id" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="AppSecret" prop="app_secret">
          <el-input v-model="form.app_secret" style="width: 350px;"></el-input>
        </el-form-item>
        <el-form-item label="EncodingAESKey" prop="aes_key">
          <el-input v-model="form.aes_key" style="width: 400px;"></el-input>
        </el-form-item>
        <p>请在小程序后台设置为'安全模式'</p>
        <el-form-item label="消息加解密方式" prop="encode_type">
          <el-radio v-model="form.encode_type" v-for="encode_type in encodeTypes" :key="encode_type.id" :label="encode_type.value" disabled>{{ encode_type.name }}</el-radio>
        </el-form-item>
        <p>请在小程序后台设置数据格式为'XML'</p>
        <el-form-item label="数据格式" prop="encode_type">
          <el-radio v-model="form.data_type" v-for="data_type in dataTypes" :key="data_type.id" :label="data_type.value" disabled>{{ data_type.name }}</el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addMini">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-mini-admin',
  data () {
    return {
      dialogVisible: false,
      isEdit: false,
      form: {
        wid: '',
        nickname: '',
        description: '',
        company: '',
        original_id: '',
        app_id: '',
        app_secret: '',
        aes_key: 'YLOfuQ2DfNTx58PqdJoO4cceZOeT8Dv1NYBoZ1ftSjJ',
        encode_type: '3',
        data_type: '2'
      },
      rules: {
        nickname: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '请输入简介', trigger: 'blur' }
        ],
        company: [
          { required: true, message: '请输入公司名称', trigger: 'blur' }
        ],
        original_id: [
          { required: true, message: '请输入原始ID', trigger: 'blur' }
        ],
        app_id: [
          { required: true, message: '请输入AppId', trigger: 'blur' }
        ],
        app_secret: [
          { required: true, message: '请输入AppSecret', trigger: 'blur' }
        ],
        aes_key: [
          { required: true, message: '请输入AesKey', trigger: 'blur' }
        ]
      },
      encodeTypes: [
        { id: 1, value: '1', name: '明文模式' },
        { id: 2, value: '2', name: '兼容模式' },
        { id: 3, value: '3', name: '安全模式' }
      ],
      dataTypes: [
        { id: 1, value: '1', name: 'JSON' },
        { id: 2, value: '2', name: 'XML' }
      ],
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL
    }
  },
  computed: {
    ...mapGetters([ 'minis', 'userInfo', 'accessToken' ])
  },
  methods: {
    ...mapActions([ 'getMinis', 'createMini', 'updateMini', 'deleteMini' ]),
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201808252119421')
    },
    handleImageUploadSuccess (result, file) {
      if (result.status_code === 200) {
        this.form.avatar = result.data
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    gotoOAuth () {
      let url = constants.WECHAT_OPEN_PLATFORM_REDIRECT_URI + this.userInfo.uid
      window.open(url)
    },
    showAddDialog () {
      this.dialogVisible = true
      this.isEdit = false
      //
      this.form.wid = ''
      this.form.nickname = ''
      this.form.description = ''
      this.form.company = ''
      this.form.original_id = ''
      this.form.app_id = ''
      this.form.app_secret = ''
    },
    addMini () {
      this.dialogVisible = false
      if (this.isEdit) {
        this.updateMini(this.form)
      } else {
        this.createMini(this.form)
      }
    },
    handleEdit (index, row) {
      // console.log(index, row)
      //
      this.form.wid = row.wid
      this.form.nickname = row.nickname
      this.form.description = row.description
      this.form.company = row.company
      this.form.original_id = row.userName
      this.form.app_id = row.appId
      this.form.app_secret = row.appSecret
      this.form.aes_key = row.aesKey
      this.form.encode_type = '3'
      this.form.data_type = '2'
      //
      this.isEdit = true
      this.dialogVisible = true
    },
    handleDelete (index, row) {
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteMini(row.wid)
      }).catch(() => {
        //
      })
    },
    getApi () {}
  }

}
</script>

<style scoped>
#bd-mini-admin {
  height: 100%;
  text-align: left;
}
</style>



