<template>
  <div id='bd-leave-msg'>
    <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-right: 10px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
    <!-- TODO: 清空输入框 和 时间范围 触发自动重新查询 -->
    <el-input size="mini" v-model="nickname" placeholder="访客昵称" style="width: 155px; margin-right: 10px;" clearable></el-input>
    <span style="font-size: 12px;">进入时间：</span>
    <el-date-picker style="width: 345px;"
      size="mini"
      v-model="createdAtInterval"
      type="datetimerange"
      :picker-options="pickerOptions"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      align="right">
    </el-date-picker>
    <el-dropdown @command="handleWorkGroupCommand">
      <span class="el-dropdown-link" style="margin-left: 10px; font-size: 12px;">
        {{ currentWorkGroup }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="all">全部工作组</el-dropdown-item>
        <el-dropdown-item v-for="workGroup in filterWorkGroups" :key="workGroup.id" :command="workGroup.nickname">{{ workGroup.nickname }}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dropdown @command="handleAgentCommand" v-if="showAuthority.admin">
      <span class="el-dropdown-link" style="margin-left: 10px; font-size: 12px;">
        {{ currentAgent }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="all">全部客服</el-dropdown-item>
        <el-dropdown-item v-for="agent in filterWorkGroupMembers" :key="agent.id" :command="agent.realName">{{ agent.realName }}</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-dropdown @command="handleClientCommand">
      <span class="el-dropdown-link" style="margin-left: 10px; font-size: 12px;">
        {{ currentClient }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item command="all">全部来源</el-dropdown-item>
        <el-dropdown-item command="web">网站</el-dropdown-item>
        <el-dropdown-item command="android">安卓</el-dropdown-item>
        <el-dropdown-item command="ios">苹果</el-dropdown-item>
        <el-dropdown-item command="mp">公众号</el-dropdown-item>
        <el-dropdown-item command="mini">小程序</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-button size="mini" type="primary" icon="el-icon-search" style="margin-left: 10px;" @click="handleQuery">查询</el-button>
    <el-button size="mini" type="primary" icon="el-icon-download" style="margin-left: 10px;" @click="handleExport" v-if="showAuthority.debug">导出</el-button>
    <el-pagination style="float: right;"
      layout="prev, pager, next"
      small
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="pageSize"
      :total="leaveMessages.totalElements">
    </el-pagination>
    <el-table stripe size="mini" height="calc(100% - 80px)" :data="leaveMessages.content"
      style="width: 100%; margin-top: 10px;">
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="visitor.nickname" label="访客昵称"></el-table-column>
      <el-table-column prop="mobile" label="手机"></el-table-column>
      <el-table-column prop="email" label="邮箱"></el-table-column>
      <el-table-column prop="content" label="留言"></el-table-column>
      <el-table-column prop="createdAt" label="时间"></el-table-column>
      <el-table-column prop="replied" label="是否回复">
        <template slot-scope="scope">
          <span v-if="scope.row.replied">是</span>
          <span v-if="!scope.row.replied">否</span>
        </template>
      </el-table-column>
      <el-table-column prop="reply" label="回复内容"></el-table-column>
      <el-table-column prop="visitor.client" label="来源" width="80">
        <template slot-scope="scope">
          <span>{{ formatClient(scope.row.visitor.client) }}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="80">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">标记</el-button>
          <!-- <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="float: right; margin-top: 10px;"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 200, 300, 400]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="leaveMessages.totalElements">
    </el-pagination>
    <el-dialog
      title="提示"
      :visible.sync="noteDialogVisible"
      width="30%"
      center>
      <span>仅作为备注使用, 您需要主动联系回复</span>
      <el-form ref="noteForm" :model="noteForm" label-width="80px" style="margin-top: 20px;">
        <el-form-item label="标记回复">
          <el-radio-group v-model="noteForm.replied">
            <el-radio :label="true">是</el-radio>
            <el-radio :label="false">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="回复备注">
          <el-input type="textarea" v-model="noteForm.reply"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="noteDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="leaveMessage()">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'

export default {
  name: 'bd-leave-msg',
  data () {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      createdAtInterval: [ '', '' ],
      nickname: '',
      pageSize: 20,
      currentPage: 1,
      dialogVisible: false,
      isEdit: false,
      currentWorkGroup: '全部工作组',
      currentAgent: '全部客服',
      currentClient: '全部来源',
      currentClientCommand: '',
      noteDialogVisible: false,
      noteForm: {
        lid: '',
        replied: false,
        reply: ''
      },
      electron: constants.IS_ELECTRON
    }
  },
  computed: {
    ...mapGetters([ 'leaveMessages', 'userInfo', 'agents', 'workGroups',
      'filterWorkGroups', 'filterWorkGroupMembers', 'showAuthority' ])
  },
  methods: {
    ...mapActions([ 'getLeaveMessages', 'replyLeaveMessage', 'filterLeaveMessage' ]),
    timeInterval (start, end) {
      return moment(start, 'YYYY-MM-DD HH:mm:ss').diff(end, 'YYYY-MM-DD HH:mm:ss')
    },
    formatClient (client) {
      // TODO: 区分电脑、手机网站
      if (client === constants.CLIENT_WEB) {
        return '网站'
      } else if (client === constants.CLIENT_WAP) {
        return '手机网站'
      } else if (client === constants.CLIENT_ANDROID) {
        return '安卓App'
      } else if (client === constants.CLIENT_IOS) {
        return '苹果App'
      } else if (client === constants.CLIENT_WECHAT_MP) {
        return '公众号'
      } else if (client === constants.CLIENT_WECHAT_MINI) {
        return '小程序'
      }
    },
    gotoHelpdoc () {
      let helpUrl = constants.HELP_BASE_URL + '201809042103521'
      if (this.electron) {
        ipcRenderer.send('open-external-url', helpUrl)
      } else {
        window.open(helpUrl)
      }
    },
    handleWorkGroupCommand (command) {
      console.log(command)
      if (command === 'all') {
        this.currentWorkGroup = '全部工作组'
      } else {
        this.currentWorkGroup = command
      }
    },
    handleAgentCommand (command) {
      console.log(command)
      if (command === 'all') {
        this.currentAgent = '全部客服'
      } else {
        this.currentAgent = command
      }
    },
    handleClientCommand (command) {
      console.log(command)
      this.currentClientCommand = command
      if (command === 'all') {
        this.currentClient = '全部来源'
      } else if (command === 'web') {
        this.currentClient = '网站'
      } else if (command === 'android') {
        this.currentClient = '安卓'
      } else if (command === 'ios') {
        this.currentClient = '苹果'
      } else if (command === 'mp') {
        this.currentClient = '公众号'
      } else if (command === 'mini') {
        this.currentClient = '小程序'
      }
    },
    handleQuery () {
      // 如果所有选项都是默认的，那么直接调用分页获取接口，不需要调用搜索接口
      if (this.createdAtInterval[0] === '' && this.createdAtInterval[1] === '' && this.nickname === '' &&
        this.pageSize === 20 && this.currentPage === 1 && this.currentWorkGroup === '全部工作组' &&
        this.currentAgent === '全部客服' && this.currentClient === '全部来源') {
        let payload = { page: 0, size: 20 }
        this.getLeaveMessages(payload)
        return
      }
      // 非管理员只能查询自己的历史记录
      // TODO: 客服组长可以查询组内数据
      if (!this.showAuthority.admin) {
        console.log('not admin')
        this.currentAgent = this.userInfo.realName
      } else {
        console.log('admin')
      }
      //
      if (this.createdAtInterval === null) {
        this.createdAtInterval = [ '', '' ]
      }
      let payload = {
        page: this.currentPage - 1,
        size: this.pageSize,
        nickname: this.nickname,
        createdAtStart: moment(this.createdAtInterval[0]).format('YYYY-MM-DD HH:mm:ss'),
        createdAtEnd: moment(this.createdAtInterval[1]).format('YYYY-MM-DD HH:mm:ss'),
        workGroupNickname: this.currentWorkGroup === '全部工作组' ? 'all' : this.currentWorkGroup,
        agentRealName: this.currentAgent === '全部客服' ? 'all' : this.currentAgent,
        client: this.currentClientCommand
      }
      console.log('query', payload)
      this.filterLeaveMessage(payload)
    },
    handleExport () {
      console.log('export')
    },
    handleSizeChange (val) {
      console.log(val)
      this.pageSize = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getLeaveMessages(payload)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getLeaveMessages(payload)
    },
    leaveMessage () {
      this.replyLeaveMessage(this.noteForm)
      this.noteDialogVisible = false
    },
    handleEdit (index, row) {
      // console.log(index, row)
      this.noteForm.lid = row.lid
      this.noteForm.replied = row.replied
      this.noteForm.reply = row.reply
      this.noteDialogVisible = true
    },
    handleDelete (index, row) {
      // console.log(index, row)
    }
  },
  created () {
    // console.log('created histories')
  },
  mounted () {
    // console.log('mounted histories')
  }

}
</script>

<style scoped>
#bd-leave-msg {
  height: 100%;
  text-align: left;
}
</style>



