<template>
  <div id='bd-all'>
    <el-row>
      <el-col :span="4" id="left-col">
        <!-- TODO: 支持设置多个意见反馈, 目前仅支持一个配置 -->
        <el-button icon="el-icon-question" size="mini" type="info" style="float: left; margin-bottom: 15px;" @click="gotoHelpdoc()" plain>帮助文档</el-button>
        <el-button size="mini" type="success" style="float: left; margin-bottom: 15px;" @click="addArticles">添加问题分类</el-button>
        <el-input size="mini" placeholder="搜索问题分类" v-model="filterText"></el-input>
        <el-tree class="filter-tree"
          ref="cateTree"
          default-expand-all
          :data="supportCategories"
          :props="defaultProps"
          :filter-node-method="filterNode"
          @node-click="handleNodeClick">
        </el-tree>
        <el-button size="mini" type="success" @click="addCategory">添加</el-button>
        <el-button size="mini" type="primary" @click="editCategory">编辑</el-button>
        <el-button size="mini" type="danger" @click="deleteCategory">删除</el-button>
      </el-col>
      <el-col :span="20" id="right-col">
        <el-pagination style="float: right;"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :total="supportArticles.totalElements">
        </el-pagination>
        <el-table stripe size="mini" height="calc(100% - 80px)" :data="supportArticles.content" style="width: 100%; text-align: left;">
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="title" label="内容"></el-table-column>
          <el-table-column prop="title" label="图片"></el-table-column>
          <el-table-column prop="title" label="手机"></el-table-column>
          <el-table-column prop="title" label="邮箱"></el-table-column>
          <el-table-column prop="createdAt" label="反馈时间"></el-table-column>
          <el-table-column fixed="right" label="操作" width="220">
            <template slot-scope="scope">
              <el-button size="mini" type="success" @click="handleView(scope.$index, scope.row)">预览</el-button>
              <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination style="float: right; margin-top: 10px;"
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :total="supportArticles.totalElements">
        </el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-all',
  data () {
    return {
      currentCategoryId: 0,
      currentPage: 1,
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  computed: {
    ...mapGetters([ 'supportCategories', 'supportArticles' ])
  },
  watch: {
    filterText (val) {
      this.$refs.cateTree.filter(val)
    }
  },
  methods: {
    ...mapActions([ 'getCategories', 'createCategory', 'deleteCategory',
      'getSupportArticles', 'createSupportArticle', 'updateSupportArticle', 'deleteSupportArticle' ]),
    gotoHelpdoc () {
      window.open(constants.HELP_BASE_URL + '201810252124471')
    },
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    },
    handleNodeClick (data) {
      console.log(data)
      this.currentCategoryId = data.id
      let payload = {
        categoryId: this.currentCategoryId,
        page: 0,
        size: 20
      }
      this.getSupportArticles(payload)
    },
    handleNodeContextMenuClick (event, data, node) {
      console.log(event, data, node)
    },
    handleCurrentChange (val) {
      console.log(val)
      this.currentPage = val
      //
      let payload = {
        categoryId: this.currentCategoryId,
        page: this.currentPage - 1,
        size: 20
      }
      this.getSupportArticles(payload)
    },
    addArticles () {
      console.log('settings')
    },
    addCategory () {
      this.$prompt('请输入类别', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        console.log(value)
        if (value !== undefined && value !== null && value.length > 0) {
          let payload = {
            parent_id: this.currentCategoryId,
            name: value
          }
          this.createCategory(payload)
        }
      }).catch(() => {
        // 取消
      })
    },
    editCategory () {},
    deleteCategory () {},
    handleView (index, row) {
    },
    handleEdit (index, row) {
      // console.log('bd-all:', index, row)
      bus.$emit(constants.EVENT_BUS_SUPPORT_EDIT_ARTICLE, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$confirm('确定要删除?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteSupportArticle(row.id)
      }).catch(() => {
        //
      })
    }
  },
  created () {
  }
}
</script>

<style scoped>
#bd-all {
  height: 100%;
}
.el-row {
  height: 100%;
}
.el-col {
  border-radius: 4px;
}
#left-col {
  height: 100%;
}
#right-col{
  height: 100%;
}
.filter-tree {
  margin-top: 10px;
  margin-bottom: 10px;
  height: calc(100% - 120px);
  overflow-y: auto;
}
</style>



