<template>
    <div :id="id" :style="style" style="margin-top: 10px;"></div>
</template>

<script>
// 教程说明: https://segmentfault.com/a/1190000013903264
// TODO: 优化复用

export default {
  name: 'bd-chart',
  data () {
    return {
      // echarts实例
      chart: ''
    }
  },
  props: {
    // 父组件需要传递的参数：id，width，height，option
    id: {
      type: String
    },
    title: {
      type: String
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '400px'
    },
    totalThreadCounts: {
      type: Array
    },
    totalQueueCounts: {
      type: Array
    },
    option: {
      type: Object,
      // Object类型的prop值一定要用函数return出来，不然会报错。原理和data是一样的，
      // 使用闭包保证一个vue实例拥有自己的一份props
      default () {
        return {
          title: {
            text: this.title
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['接待人次', '排队人次', '消息条数']
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          toolbox: {
            show: true,
            feature: {
              mark: { show: true },
              dataView: { show: true, readOnly: false },
              magicType: { show: true, type: ['line', 'bar'] },
              restore: { show: true },
              saveAsImage: { show: true }
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: ['00:00', '01:00', '02:00', '03:00', '04:00', '05:00', '06:00',
              '07:00', '08:00', '09:00', '10:00', '11:00', '12:00',
              '13:00', '14:00', '15:00', '16:00', '17:00', '18:00',
              '19:00', '20:00', '21:00', '22:00', '23:00']
          },
          yAxis: {
            type: 'value'
          },
          series: [
            {
              name: '接待人次',
              type: 'line',
              data: this.totalThreadCounts
            },
            {
              name: '排队人次',
              type: 'line',
              data: this.totalQueueCounts
            },
            {
              name: '消息条数',
              type: 'line',
              data: this.totalQueueCounts
            }
          ]
        }
      }
    }
  },
  watch: {
    // 观察option的变化
    option: {
      handler (newVal, oldVal) {
        if (this.chart) {
          if (newVal) {
            this.chart.setOption(newVal)
          } else {
            this.chart.setOption(oldVal)
          }
        } else {
          this.init()
        }
      },
      deep: true // 对象内部属性的监听，关键
    }
  },
  computed: {
    style () {
      return {
        height: this.height,
        width: this.width
      }
    }
  },
  methods: {
    init () {
      this.chart = this.$echarts.init(document.getElementById(this.id))
      this.chart.setOption(this.option)
      window.addEventListener('resize', this.chart.resize)
    }
  },
  mounted () {
    this.init()
  }
}
</script>



