<template>
    <div id='left-side'>
      <el-menu class="el-menu-vertical-demo"
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b"
              @open="handleOpen"
              @close="handleClose"
              :collapse="isCollapse"
              :router=true>
          <el-menu-item index="/admin/home" v-if="showAuthority.debug">
            <i class="iconfont icon-home"></i>
            <span slot="title" style="margin-left: 5px;">我的首页</span>
          </el-menu-item>
          <el-menu-item index="/admin/statistic" v-if="showStatistic">
            <i class="iconfont icon-data"></i>
            <span slot="title" style="margin-left: 5px;">绩效数据</span>
          </el-menu-item>
          <el-menu-item index="/admin/chat">
            <!-- TODO: 添加badge -->
            <!-- <el-badge is-dot><i class="iconfont icon-agent"></i></el-badge> -->
            <i class="iconfont icon-agent"></i>
            <span slot="title" style="margin-left: 5px;">人工客服</span>
          </el-menu-item>
          <el-menu-item index="/admin/robot" v-if="showAuthority.admin">
            <i class="iconfont icon-robot"></i>
            <span slot="title" style="margin-left: 5px;">智能客服</span>
          </el-menu-item>
          <el-menu-item index="/admin/ticket" v-if="showAuthority.debug">
            <i class="iconfont icon-workorder"></i>
            <span slot="title" style="margin-left: 5px;">工单系统</span>
          </el-menu-item>
          <!-- <el-menu-item index="/admin/feedback" v-if="showAuthority.debug">
            <i class="iconfont icon-feedback"></i>
            <span slot="title" style="margin-left: 5px;">意见反馈</span>
          </el-menu-item> -->
          <el-menu-item index="/admin/support" v-if="showSupport">
            <i class="iconfont icon-help"></i>
            <span slot="title" style="margin-left: 5px;">知识库</span>
          </el-menu-item>
          <el-menu-item index="/admin/crm" v-if="showAuthority.debug">
            <i class="iconfont icon-crm"></i>
            <span slot="title" style="margin-left: 5px;">客户管理</span>
          </el-menu-item>
          <el-menu-item index="/admin/setting" v-if="showAuthority.setting">
            <i class="iconfont icon-setting"></i>
            <span slot="title" style="margin-left: 5px;">所有设置</span>
          </el-menu-item>
          <el-submenu index="" v-if="showAuthority.debug">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">二期开发</span>
            </template>
            <el-menu-item index="/admin/wenjuan" v-if="showAuthority.debug">
              <i class="iconfont icon-wenjuan"></i>
              <span slot="title" style="margin-left: 5px;">调查问卷</span>
            </el-menu-item>
            <el-menu-item index="/admin/callcenter" v-if="showAuthority.debug">
              <i class="iconfont icon-callcenter"></i>
              <span slot="title" style="margin-left: 5px;">呼叫中心</span>
            </el-menu-item>
            <el-menu-item index="/admin/community" v-if="showAuthority.debug">
              <i class="iconfont icon-community"></i>
              <span slot="title" style="margin-left: 5px;">讨论社区</span>
            </el-menu-item>
            <!-- <el-menu-item index="/admin/internal" v-if="showAuthority.debug">
              <i class="iconfont icon-internal"></i>
              <span slot="title" style="margin-left: 5px;">内部协同</span>
            </el-menu-item> -->
            <el-menu-item index="/admin/imcloud" v-if="showAuthority.debug">
              <i class="iconfont icon-imcloud"></i>
              <span slot="title" style="margin-left: 5px;">云通讯IM</span>
            </el-menu-item>
            <el-menu-item index="/admin/conference" v-if="showAuthority.debug">
              <i class="iconfont icon-camera"></i>
              <span slot="title" style="margin-left: 5px;">视频会议</span>
            </el-menu-item>
            <el-menu-item index="/admin/everyone" v-if="showAuthority.debug">
              <i class="iconfont icon-world"></i>
              <span slot="title" style="margin-left: 5px;">人人客服</span>
            </el-menu-item>
            <!-- <el-menu-item index="/admin/knowledge" v-if="showAuthority.debug">
              <i class="iconfont icon-world"></i>
              <span slot="title" style="margin-left: 5px;">知识库</span>
            </el-menu-item> -->
            <el-menu-item index="/admin/other" v-if="showAuthority.debug">
              <i class="iconfont icon-other"></i>
              <span slot="title" style="margin-left: 5px;">其他功能</span>
            </el-menu-item>
          </el-submenu>
      </el-menu>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-aside',
  data () {
    return {
      isCollapse: true,
      showSupport: constants.SHOW_SUPPORT
    }
  },
  computed: {
    ...mapGetters([ 'showAuthority' ]),
    showStatistic () {
      return this.showAuthority.admin && constants.SHOW_STATISTIC
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      // console.log('open', key, keyPath)
    },
    handleClose (key, keyPath) {
      // console.log('close', key, keyPath)
    }
  },
  mounted () {
    bus.$on('collapse', collapse => {
      this.isCollapse = collapse
    })
  }

}
</script>

<style scoped>
#left-side {
 height: 100%;
 overflow: hidden;
}
.el-menu-vertical-demo {
  height: 100%;
  padding-top: 40px;
  overflow-y: auto;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-badge__content .is-fixed .is-dot {
  margin-top: 10px;
  margin-right: 40px;
}
</style>



