<template>
  <div id='bd-input'>
    <div id='bd-toolbar'>
      <li id='bd-emoji' class='iconfont icon-emoji' @click="switchEmoji"></li>
      <transition name="showbox">
        <div class="bd-emoji-box" v-show="showEmoji">
          <li v-for="item in emojis" :key="item.file">
            <img :src="emotionUrl(item.file)" :data="item.title" @click="emotionClicked(item.title)">
          </li>
        </div>
      </transition>
      <!-- TODO: 匹配关键词高亮显示 -->
      <transition name="showbox">
        <div class="bd-input-tip" v-show="showInputTip">
          <ul>
            <li v-for="tip in inputTips" :key="tip" @click="inputTipClicked(tip)">{{ tip }}</li>
          </ul>
        </div>
      </transition>
      <el-upload class='bd-upload-image'
        :on-preview='handlePreview'
        :on-remove='handleRemove'
        :before-remove='beforeRemove'
        :on-exceed='handleExceed'
        :file-list='uploadedImageList'
        :data='upload_data'
        :name='upload_name'
        :action='uploadImageServerUrl'
        :show-file-list='false'
        :on-success='handleImageUploadSuccess'
        :before-upload='beforeImageUpload'
        :disabled="disabled">
          <li class='iconfont icon-image'></li>
      </el-upload>
      <el-upload class='bd-upload-file'
        :on-preview='handlePreview'
        :on-remove='handleRemove'
        :before-remove='beforeRemove'
        :on-exceed='handleExceed'
        :file-list='uploadedImageList'
        :data='upload_data'
        :name='upload_name'
        :action='uploadFileServerUrl'
        :show-file-list='false'
        :on-success='handleFileUploadSuccess'
        :before-upload='beforeFileUpload'
        :disabled="disabled">
          <li class='iconfont icon-file'></li>
      </el-upload>
      <li id='bd-rate' class='iconfont icon-rate' @click="showInviteRate" :disabled="disabled"></li>
      <!-- TODO: qq邮箱截图插件 https://snip.qq.com/install_plugin.html -->
      <li id='bd-jietu' class='iconfont icon-jietu' @click="showCaptureDialog" v-if="showCaptureFunction" :disabled="disabled"></li>
      <li id='bd-video' class='iconfont icon-video' @click="showRtcDialog" v-if="showAuthority.debug" :disabled="disabled"></li>
      <li id='bd-phone' class='iconfont icon-phone' @click="showPhoneDialog" v-if="showAuthority.debug" :disabled="disabled"></li>
      <li id='bd-block' class='iconfont icon-block' @click="showBlockDialog" v-if="showAuthority.debug" :disabled="disabled"></li>
      <!-- FIXME: currentThread变化之后，消息预知未变化 -->
      <li id='bd-tip' v-if="inputTipVisible">{{ previewContent }}</li>
      <el-button size="mini" type="text" @click="showHistoryMessages" :disabled="disabled" style="float: right; margin-top: 3px; margin-right: 15px;">历史消息</el-button>
    </div>
    <div id='bd-inputbox'>
      <el-input v-focus type='textarea' :rows='4' placeholder='请输入内容' v-model='inputContent' @input="onInputChange" @keyup.enter.native="onKeyUp"></el-input>
      <el-button class="sendbutton" @click="sendMessage" :disabled="sendButtonDisabled">发 送</el-button>
    </div>
    <!-- 拉黑对话框 -->
    <el-dialog
      title="拉入黑名单"
      :visible.sync="blockDialogVisible"
      width="30%"
      center>
      <!-- TODO: 添加拉黑时长到日期之后自动取消拉黑，或者永久拉黑 -->
      <el-input type="textarea" :rows="4" placeholder="请输入拉黑理由" v-model="blockReason"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="blockDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="doBlock">拉 黑</el-button>
      </span>
    </el-dialog>
    <!-- 实时视频WebRTC窗口 -->
    <el-dialog
      title="提示"
      :visible.sync="rtcDialogVisible"
      width="40%"
      :before-close="handleRtcDialogClose" center>
      <div id="videos">
        <video id="localVideo" autoplay muted playsinline style="width: 320px; height: 240px;"></video>
        <video id="remoteVideo" autoplay playsinline style="width: 320px; height: 240px;"></video>
      </div>
      <div id="videoButtons" style="text-align: center;">
        <!-- 主动 -->
        <el-button v-if="isRtcInitiator && !isRtcStarted" @click="startInviteVideoCall">邀请视频会话</el-button>
        <!-- <el-button @click="startAudioCall">音频会话</el-button> -->
        <el-button v-if="isRtcStarted" @click="hangUpCall">结束会话</el-button>
        <!-- 被动 -->
        <el-button v-if="!isRtcInitiator && !isRtcStarted" @click="acceptVideoCall">接受视频会话</el-button>
        <el-button v-if="!isRtcInitiator && !isRtcStarted" @click="rejectVideoCall">拒绝视频会话</el-button>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="closeRtcDialog">关闭</el-button>
      </span>
    </el-dialog>
    <!-- 聊天记录窗口 -->
    <el-dialog
      title="历史聊天记录"
      :visible.sync="messageDialogVisible"
      width="50%"
      center>
      <!-- TODO: 添加搜索 -->
      <!-- <div class="message-search">
        <el-input placeholder="请输入内容" v-model="searchMessage" clearable>
          <el-button slot="append" icon="el-icon-search" @click="handleSearch()"></el-button>
        </el-input>
      </div> -->
      <bd-message></bd-message>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" type="primary" @click="messageDialogVisible = false">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// import html2canvas from 'html2canvas'
import { mapActions, mapGetters, mapState } from 'vuex'
import BdMessage from '@/components/dashboard/main/chat/children/thread/middle/BdMessage'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-input',
  data () {
    return {
      show_emoji: false,
      inputContent: '',
      previewContent: '',
      blockReason: '',
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file',
      // 接收图片服务器地址
      uploadImageServerUrl: constants.UPLOAD_IMAGE_URL,
      uploadFileServerUrl: constants.UPLOAD_FILE_URL,
      // 上传图片结果集, 格式：{name: '', url: ''}
      uploadedImageList: [],
      uploadFileType: '',
      //
      inputTipVisible: false,
      //
      messageDialogVisible: false,
      searchMessage: '',
      //
      blockDialogVisible: false,
      // webrtc会话相关
      mediaStreamConstraints: {
        audio: true,
        video: true
      },
      videoOfferSdpConstraints: {
        offerToReceiveAudio: true,
        offerToReceiveVideo: true
      },
      audioOfferSdpConstraints: {
        offerToReceiveAudio: true,
        offerToReceiveVideo: false
      },
      // 为计算接通时长,记录开始情况会话时间戳
      rtcStartCallTimestamp: null,
      // 是否rtc会话发起者
      isRtcInitiator: false,
      // rtc会话是否成功开始
      isRtcStarted: false,
      // 会话窗口是否显示
      rtcDialogVisible: false,
      rtcPeerConnection: null,
      // 视频窗口
      rtcLocalVideo: null,
      rtcRemoteVideo: null,
      // 视频流
      rtcLocalStream: null,
      rtcRemoteStream: null,
      // 本地candidate信息
      rtcIceCandidate: null
    }
  },
  components: { BdMessage },
  computed: {
    ...mapGetters([ 'userInfo', 'userThreads', 'userCurrentThread', 'userCurrentContact',
      'userCurrentGroup', 'userCurrentChatType', 'showAuthority', 'allCuws' ]),
    ...mapState([
      'emojis'
    ]),
    showEmoji () {
      return !this.disabled && this.show_emoji
    },
    showInputTip () {
      return !this.disabled &&
      this.inputContent.length > 0 &&
      this.inputTips.length > 0
    },
    inputTips () {
      let tips = []
      for (let z = 0; z < this.allCuws.length; z++) {
        const cate = this.allCuws[z]
        for (let i = 0; i < cate.cuwChildren.length; i++) {
          const category = cate.cuwChildren[i]
          for (let j = 0; j < category.cuwChildren.length; j++) {
            const cuw = category.cuwChildren[j]
            if (this.inputContent.length > 0 && cuw.name.indexOf(this.inputContent) !== -1) {
              tips.push(cuw.name)
            }
          }
        }
      }
      return tips
    },
    disabled () {
      return this.userCurrentThread.id === 0
    },
    sendButtonDisabled () {
      return this.disabled || this.inputContent.trim().length === 0
    },
    prePageButtonDisabled () {
      return this.userCurrentThread.first
    },
    nextPageButtonDisabled () {
      return this.userCurrentThread.last
    },
    showCaptureFunction () {
      return constants.IS_ELECTRON && this.showAuthority.debug
    }
  },
  methods: {
    ...mapActions([ 'updateCurrentThread', 'inviteRate', 'sendTextMessage', 'sendImageMessage', 'sendFileMessage', 'addBlock',
      'getMessages', 'sendOtherMessage', 'sendPreviewMessage', 'sendIceCandidateMessage', 'sendSessionDescriptionMessage',
      'sendThreadTextMessageSync', 'sendThreadImageMessageSync' ]),
    switchEmoji () {
      if (!this.disabled) {
        this.show_emoji = !this.show_emoji
      }
    },
    emotionUrl (file) {
      return constants.EMOJI_BASE_URL + file
    },
    emotionClicked (emotion) {
      this.inputContent += emotion
      this.show_emoji = false
    },
    inputTipClicked (tip) {
      // 注意：末尾加空格，方便点击之后隐藏tip框
      // TODO: 增加统计CUW使用频次
      this.inputContent = tip + ' '
    },
    /**
     * 上传图片相关函数
     */
    handleRemove (file, uploadedImageList) {
      console.log(file, uploadedImageList)
      // 删除
      for (var i = 0; i < this.uploadedImageList.length; i++) {
        if (this.uploadedImageList[i].url === file.url) {
          this.uploadedImageList.splice(i, 1)
        }
      }
    },
    handlePreview (file) {
      console.log(file)
    },
    handleExceed (files, uploadedImageList) {
      this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + uploadedImageList.length} 个文件`)
    },
    beforeRemove (file, uploadedImageList) {
      return this.$confirm(`确定移除 ${file.name}？`)
    },
    handleImageUploadSuccess (result, file) {
      console.log('upload image success result:', result, ' file:', file)
      if (result.status_code === 200) {
        // 发送消息
        let url = result.data
        // this.sendImageMessage(url)
        this.sendThreadImageMessageSync(url)
      } else {
        this.$message.error('上传图片错误，请稍后重试...')
      }
    },
    beforeImageUpload (file) {
      // console.log('before image upload file:', file)
      const isJPGorPNG = (file.type === 'image/jpeg' || file.type === 'image/png')
      const isLt2M = file.size / 1024 / 1024 < 2
      // 验证图片格式
      if (!isJPGorPNG) {
        this.$message.error('上传头像图片只能是 jpg 或 png 格式!')
      }
      // 验证图片大小
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      // 设置文件名
      let filename = moment(new Date()).format('YYYYMMDDHHmmss')
      if (file.type === 'image/jpeg') {
        this.upload_data.file_name = filename + '.jpg'
      } else if (file.type === 'image/png') {
        this.upload_data.file_name = filename + '.png'
      }
      // 返回
      return isJPGorPNG && isLt2M
    },
    beforeFileUpload (file) {
      // file.name包含文件后缀
      this.upload_data.file_name = file.name
      // 文件大小控制在2m之内
      this.uploadFileType = file.type
      const isLt2M = file.size / 1024 / 1024 < 2
      return isLt2M
    },
    handleFileUploadSuccess (result, file) {
      console.log('upload file success result:', result, ' file:', file)
      if (result.status_code === 200) {
        let url = result.data
        const isJPGorPNG = (this.uploadFileType.type === 'image/jpeg' || this.uploadFileType.type === 'image/png')
        if (isJPGorPNG) {
          this.sendImageMessage(url)
        } else {
          let payload = { fileUrl: url, format: this.uploadFileType }
          this.sendFileMessage(payload)
        }
      } else {
        this.$message.error('上传文件错误，请稍后重试...')
      }
    },
    // 邀请评价
    // TODO: 收到访客评价之后，禁止重复邀请
    showInviteRate () {
      this.inviteRate(this.userCurrentThread.tid)
    },
    // 截图
    showCaptureDialog () {
      console.log('show capture')
      // 浏览器截图: http://html2canvas.hertzen.com/getting-started
      // html2canvas(document.body).then(function (canvas) {
      //   // document.body.appendChild(canvas)
      // })
    },
    // 电话
    showPhoneDialog () {
      console.log('show phone')
    },
    // 黑名单
    showBlockDialog () {
      this.blockDialogVisible = true
    },
    // 拉黑
    doBlock () {
      if (this.blockReason.trim().length > 0) {
        let payload = { uid: this.userCurrentThread.visitor.uid, type: '默认类型', note: this.blockReason }
        this.addBlock(payload)
        this.blockDialogVisible = false
      } else {
        this.$message.error('拉黑理由不能为空')
      }
    },
    /**
     * 视频窗口
     */
    showRtcDialog () {
      if (this.disabled) {
        return
      }
      this.rtcDialogVisible = true
      this.isRtcInitiator = true
    },
    handleRtcDialogClose (done) {
      if (this.isRtcStarted) {
        this.$confirm('确认关闭？')
          .then(_ => {
            this.hangUpCall()
            done()
          })
          .catch(_ => {
            this.hangUpCall()
            done()
          })
      } else {
        this.isRtcInitiator = false
        this.rtcDialogVisible = false
        this.isRtcStarted = false
      }
    },
    closeRtcDialog () {
      if (this.isRtcStarted) {
        this.$confirm('确认关闭？')
          .then(_ => {
            this.hangUpCall()
          })
          .catch(_ => {
            this.hangUpCall()
          })
      } else {
        this.isRtcInitiator = false
        this.rtcDialogVisible = false
        this.isRtcStarted = false
      }
    },
    /**
     * 参考网址:
     * https://codelabs.developers.google.com/codelabs/webrtc-web/#4
     * https://cloud.tencent.com/developer/article/1004661
     * 开始视频会话：
     * 1. A初始化本地摄像头
     * 2. (自定义协议步骤)发送消息给对方B，其收到消息之后也开始初始化设备信息, 并创建peerConnection
     * 3. A创建peerConnection
     * 4. A发送candidate信息给对方B，即：自己的ip等信息
     * 5. 对方B收到candidate信息后，执行addIcecandidate
     * 6. A发起offer: 首先调用setLocalDescription设置自己的SDP，然后将SDP发送给B
     * 7. B收到offer之后，首先将A的SDP调用setRemoteDescription保存
     * 8. B执行answer：首先调用setLocalDescription设置自己的SDP，然后将SDP发送给A
     */
    startInviteVideoCall () {
      // this.rtcStartCallTimestamp = window.performance.now()
      this.isRtcInitiator = true
      this.sendInviteMessage()
      this.initLocalStream()
    },
    startAudioCall () {
      //
    },
    acceptVideoCall () {
      // this.rtcStartCallTimestamp = window.performance.now()
      this.sendAcceptMessage()
      this.initLocalStream()
    },
    rejectVideoCall () {
      this.sendRejectMessage()
    },
    // 本地设备初始化
    initLocalStream () {
      //
      let app = this
      this.rtcLocalVideo = document.getElementById('localVideo')
      this.rtcLocalVideo.addEventListener('loadedmetadata', event => {
        const video = event.target
        console.log(`本地视频窗口 ${video.id} videoWidth: ${video.videoWidth}px, ` + `videoHeight: ${video.videoHeight}px.`)
      })
      this.rtcRemoteVideo = document.getElementById('remoteVideo')
      this.rtcRemoteVideo.addEventListener('loadedmetadata', event => {
        const video = event.target
        console.log(`远程视频窗口 ${video.id} videoWidth: ${video.videoWidth}px, ` + `videoHeight: ${video.videoHeight}px.`)
      })
      this.rtcRemoteVideo.addEventListener('onresize', event => {
        // This event is fired when video begins streaming.
        const video = event.target
        console.log(`接通远程视频 remote resize ${video.id} videoWidth: ${video.videoWidth}px, ` + `videoHeight: ${video.videoHeight}px.`)
        // 计算从请求会话到接通时长, TODO: 服务器保存
        // if (app.rtcStartCallTimestamp) {
        //   const elapsedTime = window.performance.now() - app.rtcStartCallTimestamp
        //   app.rtcStartCallTimestamp = null
        //   console.log(`接通时长: ${elapsedTime.toFixed(3)}ms.`)
        // }
      })
      //
      navigator.mediaDevices.getUserMedia(this.mediaStreamConstraints).then(mediaStream => {
        app.rtcLocalStream = mediaStream
        app.rtcLocalVideo.srcObject = mediaStream
        // 创建连接，并发送ice candidate信息
        app.createPeerConnection()
        // Added local stream to localPeerConnection
        app.rtcPeerConnection.addStream(mediaStream)
      }).catch(error => {
        console.log('getUserMedia() error: ' + error)
      })
      // 测试信息：Get local media stream tracks. TODO: 服务器保存
      // const videoTracks = this.rtcLocalStream.getVideoTracks()
      // const audioTracks = this.rtcLocalStream.getAudioTracks()
      // if (videoTracks.length > 0) {
      //   console.log(`视频设备: ${videoTracks[0].label}.`)
      // }
      // if (audioTracks.length > 0) {
      //   console.log(`音频设备: ${audioTracks[0].label}.`)
      // }
    },
    /**
     *  TODO: 暂时没有使用STUN/TURN, 有待后期添加
     * 创建连接
     */
    createPeerConnection () {
      try {
        let app = this
        const servers = null // Allows for RTC server configuration.
        this.rtcPeerConnection = new RTCPeerConnection(servers)
        // 调用createOffer，设置setLocalDescription之后才会调用onicecandidate
        // The PeerConnection won't start gathering candidates until you call setLocalDescription();
        this.rtcPeerConnection.addEventListener('onicecandidate', event => {
          console.log('icecandidate event: ', event)
          // const peerConnection = event.target
          // const iceCandidate = event.candidate
          // app.rtcIceCandidate = event.candidate
          app.isRtcStarted = true
          // 发送自己candidate信息给对方
          if (event.candidate !== null && event.candidate !== undefined) {
            app.sendCandidateMessage(event.candidate)
          }
        })
        this.rtcPeerConnection.iceconnectionstatechange = function (event) {
          console.log('iceconnectionstatechange: ', event)
        }
        this.rtcPeerConnection.onaddstream = function (event) {
          console.log('Remote stream added.')
          app.rtcRemoteStream = event.stream
          app.rtcRemoteVideo.srcObject = event.stream
        }
        this.rtcPeerConnection.onremovestream = function (event) {
          console.log('Remote stream removed. Event: ', event)
        }
        console.log('Created RTCPeerConnnection')
        // 视频连接就绪之后，通知对方
        this.sendReadyMessage()
      } catch (error) {
        console.log('Failed to create PeerConnection, exception: ' + error.message)
      }
    },
    // 发送webrtc Offer
    createRtcOffer (offerSdpConstraints) {
      let app = this
      this.rtcPeerConnection.createOffer(offerSdpConstraints).then(sessionDescription => {
        // 将SDP保存本地
        app.rtcPeerConnection.setLocalDescription(sessionDescription).then(() => {
          console.log('offer set localDescription success')
        }, error => {
          console.log('offer set localDescription error: ', error)
        })
        // 将SDP发送给对方
        app.sendOfferMessage(sessionDescription)
      }, error => {
        console.log('createOffer error: ', error)
      })
    },
    // 发送webrtc answer
    createRtcAnswer () {
      let app = this
      this.rtcPeerConnection.createAnswer().then(sessionDescription => {
        // 将SDP保存本地
        app.rtcPeerConnection.setLocalDescription(sessionDescription).then(() => {
          console.log('answer set localDescription success')
        }, error => {
          console.log('answer set localDescription error: ', error)
        })
        // 将SDP发送给对方
        app.sendAnswerMessage(sessionDescription)
      }, error => {
        console.log(error)
      })
    },
    // 结束会话
    hangUpCall () {
      console.log('hangUpCall')
      if (this.rtcPeerConnection !== null && this.rtcPeerConnection !== undefined) {
        console.log('do hangUpCall')
        // 结束视频流
        this.rtcPeerConnection.close()
        this.rtcPeerConnection = null
        // 关闭摄像头
        this.rtcLocalStream.getAudioTracks()[0].stop()
        this.rtcLocalStream.getVideoTracks()[0].stop()
        // 关闭对话框
        this.rtcDialogVisible = false
      }
      // 标记会话结束
      this.isRtcStarted = false
      // 发送结束会话信息
      this.sendCloseMessage()
    },
    // 通知对方初始化视频设备
    sendInviteMessage () {
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_INVITE, content: '视频会话邀请' }
      this.sendOtherMessage(payload)
    },
    // 将本地ice candidate信息发送给对方
    sendCandidateMessage (candidate) {
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_CANDIDATE, content: candidate }
      this.sendIceCandidateMessage(payload)
    },
    // 将SDP发送给对方
    sendOfferMessage (sessionDescription) {
      console.log('send offer sessionDescription')
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_OFFER_VIDEO, content: sessionDescription }
      this.sendSessionDescriptionMessage(payload)
    },
    // 将SDP发送给对方
    sendAnswerMessage (sessionDescription) {
      console.log('send answer sessionDescription:', sessionDescription)
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_ANSWER, content: sessionDescription }
      this.sendSessionDescriptionMessage(payload)
    },
    // 发送接受视频邀请信息
    sendAcceptMessage () {
      console.log('send accept rtc')
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_ACCEPT, content: '接受视频会话' }
      this.sendOtherMessage(payload)
    },
    // 发送拒绝视频邀请信息
    sendRejectMessage () {
      console.log('send reject rtc')
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_REJECT, content: '拒绝视频会话' }
      this.sendOtherMessage(payload)
    },
    // 被邀请方视频设备 + peeConnection已经就绪
    sendReadyMessage () {
      console.log('send ready rtc')
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_READY, content: '视频就绪' }
      this.sendOtherMessage(payload)
    },
    // 视频会话忙信息
    sendBusyMessage () {
      console.log('send busy rtc')
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_BUSY, content: '对方忙' }
      this.sendOtherMessage(payload)
    },
    // 关闭视频会话信息
    sendCloseMessage () {
      console.log('send close rtc')
      let payload = { type: constants.MESSAGE_TYPE_NOTIFICATION_WEBRTC_CLOSE, content: '关闭视频会话' }
      this.sendOtherMessage(payload)
    },
    /**
     * 历史消息
     */
    showHistoryMessages () {
      this.messageDialogVisible = true
      // bus.$emit(constants.EVENT_BUS_MESSAGE_HISTORY)
    },
    /**
     * 输入框内容发生变化
     */
    onInputChange (content) {
      // 判断是否disable状态
      if (this.disabled) {
        return
      }
      // 发送消息预知
      let contentTrim = content.trim()
      if (contentTrim.length > 0) {
        this.sendPreviewMessage(contentTrim)
      }
    },
    /**
     * 发送消息
     */
    onKeyUp (e) {
      if (e.keyCode === 13 && this.inputContent.trim().length > 0) {
        this.sendMessage()
      }
    },
    sendMessage () {
      // TODO: 本地首先显示，不需要等待服务器回执
      if (this.disabled) {
        return
      }
      this.show_emoji = false
      let content = this.inputContent.trim()
      if (content.length > 0) {
        // this.sendTextMessage(content)
        this.sendThreadTextMessageSync(content)
        this.inputContent = ''
      }
    },
    handleSearch () {
      console.log('searchMessage:', this.searchMessage)
    },
    prePage () {},
    nextPage () {
      let size = 20
      this.getMessages(size)
    }
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  mounted () {
    // 点击常用语
    bus.$on(constants.EVENT_BUS_SELECT_CUW, cuw => {
      if (this.disabled) {
        return
      }
      this.inputContent += cuw
    })
    // 消息预知
    bus.$on(constants.EVENT_BUS_MESSAGE_PREVIEW, message => {
      // console.log(message.content)
      if (message.thread.tid === this.userCurrentThread.tid) {
        this.inputTipVisible = true
        if (message.content.trim().length > 0) {
          this.previewContent = '对方正在输入:' + message.content
        } else {
          this.previewContent = ''
        }
        // 5s 定期清空状态
        setTimeout(function () {
          this.inputTipVisible = false
        }, 5000)
      }
    })
    // 通知初始化localStream
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_INVITE, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc invite')
      } else {
        console.log('自己：event webrtc invite')
      }
      // 被邀请者收到邀请信息之后，弹出提示窗口
      if (!this.isRtcInitiator) {
        this.rtcDialogVisible = true
      }
    })
    // webrtc取消邀请
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_CANCEL, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc cancel')
      } else {
        console.log('自己：event webrtc cancel')
      }
      this.rtcDialogVisible = false
    })
    // webrtc邀请视频会话
    // TODO: 弹出提示窗，客服点击接受之后，才能够 setRemoteDescription 和 answer
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_OFFER_VIDEO, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc offer video')
      } else {
        console.log('自己：event webrtc offer video')
      }
      // 邀请者发送offer，被邀请者接收offer并发送answer
      if (!this.isRtcInitiator) {
        let sessionDescription = message.sessionDescription
        this.rtcPeerConnection.setRemoteDescription(new RTCSessionDescription(sessionDescription))
        this.createRtcAnswer()
      }
    })
    // webrtc邀请音频会话
    // TODO: 弹出提示窗，客服点击接受之后，才能够 setRemoteDescription 和 answer
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_OFFER_AUDIO, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc offer audio')
      } else {
        console.log('自己：event webrtc offer audio')
      }
      // 邀请者发送offer，被邀请者接收offer并发送answer
      if (!this.isRtcInitiator) {
        let sessionDescription = message.sessionDescription
        if (sessionDescription !== null && sessionDescription !== undefined) {
          this.rtcPeerConnection.setRemoteDescription(new RTCSessionDescription(sessionDescription))
          this.createRtcAnswer()
        }
      }
    })
    // 接受webrtc邀请
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_ANSWER, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc answer')
      } else {
        console.log('自己：event webrtc answer')
      }
      // 邀请者处理收到并处理被邀请者发送过来的answer
      if (this.isRtcInitiator) {
        let sessionDescription = message.sessionDescription
        if (sessionDescription !== null && sessionDescription !== undefined) {
          this.rtcPeerConnection.setRemoteDescription(new RTCSessionDescription(sessionDescription))
        }
      }
    })
    // webrtc candidate信息
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_CANDIDATE, message => {
      // 接收并处理对方的candidate信息
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc candidate')
        let candidate = message.candidate
        if (candidate !== null && candidate !== undefined) {
          this.rtcPeerConnection.addIceCandidate(candidate).then(() => {
            console.log('addIceCandidate success')
          }).catch((error) => {
            console.log('addIceCandidate:', error)
          })
        }
      } else {
        console.log('自己：event webrtc candidate')
      }
    })
    // 接受webrtc邀请
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_ACCEPT, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc accept')
      } else {
        console.log('自己：event webrtc accept')
      }
      // 对方接受视频邀请
      if (this.isRtcInitiator) {
        //
      }
    })
    // 拒绝webrtc邀请
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_REJECT, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc reject')
      } else {
        console.log('自己：event webrtc reject')
      }
      this.hangUpCall()
      this.isRtcInitiator = false
    })
    // 对方视频设备 + peeConnection已经就绪
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_READY, message => {
      // 被邀请者设备连接ready
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc ready')
        // 邀请者收到被邀请者的视频连接已经就绪的消息之后，开始发送sdp信息
        if (this.isRtcInitiator) {
          this.createRtcOffer(this.videoOfferSdpConstraints)
        }
      } else {
        console.log('自己：event webrtc ready')
      }
    })
    // webrtc忙线
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_BUSY, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc busy')
      } else {
        console.log('自己：event webrtc busy')
      }
      this.hangUpCall()
      this.isRtcInitiator = false
    })
    // 结束webrtc会话
    bus.$on(constants.EVENT_BUS_MESSAGE_WEBRTC_CLOSE, message => {
      if (this.userInfo.uid !== message.user.uid) {
        console.log('对方：event webrtc close')
      } else {
        console.log('自己：event webrtc close')
      }
      this.hangUpCall()
      this.isRtcInitiator = false
    })
  }
}
</script>

<style scoped>
#bd-input {
  width: 100%;
  height: 130px;
  background: #fff;
  border-top:solid 0.1px #dddddd;
}
#bd-emoji {
  float: left;
  margin-top: 11px;
  margin-left: 20px;
  cursor: pointer;
  list-style: none;
}
.bd-emoji-box {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  margin-top: -295px;
  width: 280px;
  height: 280px;
  padding: 3px 6px 6px 14px;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  box-shadow:0 1px 2px 1px #d1d1d1;
}
.bd-emoji-box li {
  list-style: none;
  width: 24px;
  margin-top: 15px;
  margin-left: 5px;
  border: none;
  margin-bottom: 0px;
}
.bd-emoji-box.showbox-enter-active, .bd-emoji-box.showbox-leave-active {
  transition: all .5s;
}
.bd-emoji-box.showbox-enter, .bd-emoji-box.showbox-leave-active {
  opacity: 0;
}
.bd-input-tip {
  position: absolute;
  bottom: 142px;
  width: 300px;
  padding: 3px 6px 6px 10px;
  background-color: #fff;
  border: 1px solid #d1d1d1;
  text-align: left;
}
.bd-input-tip li {
  list-style: none;
  margin-top: 5px;
  border: none;
  cursor: pointer;
}
.bd-input-tip.showbox-enter-active, .bd-input-tip.showbox-leave-active {
  transition: all .5s;
}
.bd-input-tip.showbox-enter, .bd-input-tip.showbox-leave-active {
  opacity: 0;
}
.bd-upload-image{
  font-size: 25px;
  color: gray;
  margin-left: 20px;
  cursor: pointer;
  float: left;
  list-style: none;
}
.bd-upload-file {
  font-size: 25px;
  color: gray;
  margin-left: 20px;
  cursor: pointer;
  float: left;
  list-style: none;
}
#bd-rate {
  float: left;
  margin-top: 11px;
  margin-left: 18px;
  color: gray;
  cursor: pointer;
  list-style: none;
  font-size: 15px;
}
#bd-jietu {
  float: left;
  margin-top: 9px;
  margin-left: 20px;
  cursor: pointer;
  list-style: none;
  font-size: 18px;
}
#bd-video {
  float: left;
  margin-top: 9px;
  margin-left: 20px;
  cursor: pointer;
  list-style: none;
  font-size: 18px;
}
#bd-phone {
  float: left;
  margin-top: 7px;
  margin-left: 15px;
  cursor: pointer;
  list-style: none;
  font-size: 20px;
}
#bd-block {
  float: left;
  margin-top: 8px;
  margin-left: 15px;
  cursor: pointer;
  list-style: none;
  font-size: 19px;
}
#bd-tip {
  float: left;
  margin-top: 9px;
  margin-left: 15px;
  cursor: pointer;
  list-style: none;
  font-size: 10px;
}
.sendbutton {
  position: absolute;
  bottom: 35px;
  right: 30%;
}
</style>



