<template>
  <div id='bd-ticket-new'>
    <el-form id="bd-ticket-form" ref="form" :model="form" label-width="80px">
      <el-form-item label="名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="类型">
        <el-radio-group v-model="form.resource">
          <el-radio label="工作组"></el-radio>
          <el-radio label="指定人"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="等级">
        <el-radio-group v-model="form.resource">
          <el-radio label="高"></el-radio>
          <el-radio label="中"></el-radio>
          <el-radio label="底"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="内容">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item label="附件">
          <!-- <img :src="form.files" width="40px" height="40px" style="float: left; margin-right: 15px;"/> -->
          <el-upload
            class="upload-demo"
            :data="upload_data"
            :name="upload_name"
            :action="uploadImageServerUrl"
            :on-success="handleImageUploadSuccess"
            :before-upload="beforeImageUpload">
            <el-button size="small" type="primary" :disabled="true">点击上传</el-button>
          </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">立即创建</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import * as constants from '@/util/constants'

export default {
  name: 'bd-ticket-new',
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      // 上传图片相关参数
      upload_headers: {
        'X-CSRF-TOKEN': '',
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json',
        'Authorization': 'Bearer '
      },
      upload_data: {
        file_name: 'test.png',
        username: ''
      },
      upload_name: 'file'
    }
  },
  computed: {
    uploadImageServerUrl () {
      return constants.UPLOAD_IMAGE_URL
    }
  },
  methods: {
    handleImageUploadSuccess (result, file) {
      console.log('upload image success result:', result, ' file:', file)
    },
    beforeImageUpload (file) {},
    onSubmit () {
      console.log('submit!')
    }
  }
}
</script>

<style scoped>
#bd-ticket-new {
  width: 30%;
  height: 100%;
  /* overflow: auto; */
  text-align: left;
}
#bd-ticket-form {
  height: 100%;
}
</style>



