<script>
import { mapActions, mapGetters } from 'vuex'
import BdChart from '@/components/dashboard/main/statistic/children/BdChart'
import * as constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-all',
  data () {
    return {
      loading: false,
      pageSize: 20,
      currentPage: 1,
      //
      type: '1',
      title: '24小时在线服务趋势图',
      //
      filterDate: '',
      filterRealName: '',
      filterDatePickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      }
    }
  },
  components: { BdChart },
  computed: {
    ...mapGetters([ 'statistic', 'totalThreadCounts', 'totalQueueCounts' ])
  },
  methods: {
    ...mapActions([ 'getStatisticDetails', 'filterStatisticDetail' ]),
    handleQuery () {
      console.log('filter:', this.filterDate, this.filterRealName)
      if (this.filterDate === '' && this.filterRealName === '') {
        let payload = { page: 0, size: 20 }
        this.getStatisticDetails(payload)
      } else {
        let date = moment(this.filterDate).format('YYYY-MM-DD')
        let payload = { page: 0, size: 20, date: date, realName: this.filterRealName }
        this.filterStatisticDetail(payload)
      }
    },
    handleSizeChange (val) {
      // console.log(val)
      this.pageSize = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getStatisticDetails(payload)
    },
    onTypeChanged (value) {
      console.log(value)
      if (value === 1) {
        // TODO:加载24小时数据
        this.title = '24小时在线服务趋势图'
      } else if (value === 2) {
        // TODO:加载一周数据
        this.title = '一周在线服务趋势图'
      } else if (value === 3) {
        // TODO:加载一月数据
        this.title = '一月在线服务趋势图'
      }
    },
    handleCurrentChange (val) {
      // console.log(val)
      this.currentPage = val
      //
      this.loading = true
      let payload = { page: this.currentPage - 1, size: this.pageSize }
      this.getStatisticDetails(payload)
    },
    handleViewDetail (val) {
      console.log('view detail:', val)
      bus.$emit(constants.EVENT_BUS_STATISTIC_DETAIL, val)
    }
  }
}
</script>

<style scoped>
#bd-all {
  height: 100%;
  overflow-y: auto;
  padding-left: 10px;
  padding-right: 10px;
  /* text-align: left; */
}
