  margin-top: 30px;
}
.bd-number {
  margin-top: 5px;
  font-size: 40px;
}
.bd-link-detail {
  font-size: 10px;
  color: gray;
  margin-top: 5px;
  cursor: pointer;
}
.bd-charts {
  /* height: 300px; */
  /* width: 100%; */
  margin-top: 50px;
  margin-bottom: 50px;
}
</style>



