/**
 * 重新加载页面初始化
 * TODO: web/mac/windows，同一个账号仅允许登录一个端
 */
export const init = ({ commit, state }) => {
  // 1. 加载本地token
  commit(types.INIT)
  // 2. 通知服务器，并加载相关信息
  let accessToken = state.passport.token.access_token
  https.init(accessToken, response => {
    console.log('init response: ', response.data)
    //
    if (response.status_code === 200) {
      // 持久化用户信息
      let info = response.data.info
      commit(types.PERSIST_USER_INFO, { info })
      // 持久化用户工作组
      let workGroups = response.data.workGroups
      commit(types.PERSIST_USER_WORKGROUPS, { workGroups })
      // 持久化用户会话
      let threads = response.data.agentThreads
      commit(types.PERSIST_USER_THREADS, { threads })
      // // 联系人会话
      // let contactThreads = response.data.contactThreads
      // for (let i = 0; i < contactThreads.length; i++) {
      //   const thread = contactThreads[i]
      //   commit(types.ADD_USER_THREAD, { thread })
      // }
      // // 群组会话
      // let groupThreads = response.data.groupThreads
      // for (let i = 0; i < groupThreads.length; i++) {
      //   const thread = groupThreads[i]
      //   commit(types.ADD_USER_THREAD, { thread })
      // }
      // 持久化排队
      let queues = response.data.queues
      commit(types.PERSIST_USER_QUEUES, { queues })
      // 持久化群组
      let groups = response.data.groups
      commit(types.PERSIST_USER_GROUPS, { groups })
      // 持久化全部联系人
      let contacts = response.data.contacts
      commit(types.PERSIST_USER_CONTACTS, { contacts })
      // 根据角色权限显示隐藏div
      commit(types.UPDATE_SHOW_AUTHORITY)
      // 连接rabbitmq
      stompConnect({ commit, state })
      // 联系人加载完毕，通知layim可以初始化了
      bus.$emit(constants.EVENT_BUS_CONTACT_LOAD_FINISHED, '')
      // TODO: 加载通知消息
    } else {
      console.log('init error:', response.message)
      bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, response.message)
    }
  }, error => {
    console.log(error)
    bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, '初始化错误')
  })
}

/**
 * 更新个人资料
 */
export const updateProfile = ({ commit, state }, payload) => {
  let username = payload.username
  let password = payload.password
  let nickname = payload.nickname
  let avatar = payload.avatar
  let mobile = payload.mobile
  let email = payload.email
  let description = payload.description
  let welcomeTip = payload.welcomeTip
  let accessToken = state.passport.token.access_token
  https.updateProfile(username, password, nickname, avatar, mobile, email,
    description, welcomeTip, accessToken, response => {
      console.log(response)
      if (response.status_code === 200) {
        // 持久化用户信息
        let info = response.data
        commit(types.PERSIST_USER_INFO, { info })
      } else {
        bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, response.message)
      }
    }, error => {
      console.log(error)
      bus.$emit(constants.EVENT_BUS_NOTIFICATION_ERROR, error)
    })
}

