/**
 * 更新当前会话thread
 * @param {*} param0 commit, state
 * @param {*} thread thread
 */
export const updateCurrentThread = ({ commit, state }, thread) => {
  let preTid = state.user.current_thread.tid
  let tid = thread.tid
  // TODO: 清除未读消息数目
  commit(types.UPDATE_CURRENT_THREAD, { thread })
  // 通知服务器
  let accessToken = state.passport.token.access_token
  https.updateCurrentThread(preTid, tid, accessToken, response => {
    console.log(response)
  }, error => {
    console.log(error)
  })
  // 加载聊天记录
  let size = 20
  getMessages({ commit, state }, size)
  // 加载用户信息
  let uid = thread.visitor.uid
  fingerPrint({ commit, state }, uid)
  // 加载用户标签
  userTag({ commit, state }, uid)
}

