/**
 * 获取当前会话
 * @param {*} param0 commit, state
 */
export const getThread = ({ commit, state }) => {
  let accessToken = state.passport.token.access_token
  https.getThread(accessToken, response => {
    console.log(response)
    // 本地持久化
    let threads = response.data.agentThreads
    commit(types.PERSIST_USER_THREADS, { threads })
    // 添加订阅
    for (let i = 0; i < threads.length; i++) {
      const thread = threads[i]
      let topic = 'thread.' + thread.tid
      subscribeTopic({ commit, state }, topic)
    }
  }, error => {
    console.log(error)
  })
}

