/**
 * 接入排队访客
 * @param {*} param0 commit, state
 */
export const acceptQueue = ({ commit, state }, qid) => {
  let accessToken = state.passport.token.access_token
  https.acceptQueue(qid, accessToken, response => {
    console.log(response)
    // 1.本地持久化
    commit(types.ACCEPT_USER_QUEUE, { qid })
    // 2.持久化 接入会话
    let message = response.data
    let thread = message.thread
    commit(types.ADD_USER_THREAD, { thread })
    // 3.添加订阅
    let topic = 'thread.' + thread.tid
    subscribeTopic({ commit, state }, topic)
  }, error => {
    console.log(error)
  })
}

