/**
 * stomp长连接
 * TODO: 加载所属工作组，添加订阅
 */
export const stompConnect = ({ commit, state }) => {
  // 更新连接状态: 连接中...
  let connectionStatus = constants.STOMP_CONNECTION_STATUS_CONNECTING
  commit(types.UPDATE_USER_CONNECTION, { connectionStatus })
  bus.$emit(constants.EVENT_BUS_STOMP_CONNECTION_STATUS, connectionStatus)
  // https://stomp-js.github.io/stomp-websocket/codo/extra/docs-src/Usage.md.html
  let socket = new SockJS(constants.SOCKJS_WEBSOCKET_ENDPOINT + state.passport.token.access_token)
  // v5.0.2, FIXME: 重连没有走回调？
  // stompClient = StompJS.Stomp.over(socket)
  // v4.0.8
  stompClient = StompJS.over(socket)
  // Add the following if you need automatic reconnect (delay is in milli seconds)
  stompClient.reconnect_delay = 1000
  // client will send heartbeats every 10000ms, default 10000ms
  stompClient.heartbeat.outgoing = 15000
  // client does not want to receive heartbeats from the server, default 10000ms
  stompClient.heartbeat.incoming = 15000
  // to disable logging, set it to an empty function:
  if (constants.IS_PRODUCTION) {
    // stompClient.debug = function (value) {}
  }
  stompClient.connect({}, function (frame) {
    console.log('Connected: ' + frame)
    // TODO: 断开重连成功之后，需要重新从服务器请求thread，然后添加sub订阅
    // 登录成功之后，尝试登录次数清零
    stompReconnectTimes = 0
    // 更新连接状态：连接成功
    let connectionStatus = constants.STOMP_CONNECTION_STATUS_CONNECTED
    commit(types.UPDATE_USER_CONNECTION, { connectionStatus })
    bus.$emit(constants.EVENT_BUS_STOMP_CONNECTION_STATUS, connectionStatus)
    // TODO: 订阅公司信息
    let subDomainTopic = 'subDomain.' + state.user.info.subDomain
    subscribeTopic({ commit, state }, subDomainTopic)
    // 订阅工作组消息
    let workGroups = state.user.workGroups
    for (var i = 0; i < workGroups.length; i++) {
      let workGroup = workGroups[i]
      // 工作组会话
      let topic = 'workGroup.' + workGroup.wid
      subscribeTopic({ commit, state }, topic)
      // 订阅群组信息，群组聊天
      // let topicGroup = 'group.' + workGroup.wid
      // subscribeTopic({ commit, state }, topicGroup)
    }
    // 添加订阅群组会话topic
    let groups = state.user.groups
    for (let i = 0; i < groups.length; i++) {
      const group = groups[i]
      let topicGroup = 'group.' + group.gid
      subscribeTopic({ commit, state }, topicGroup)
    }
    // 订阅会话thread
    let threads = state.user.threads
    for (var j = 0; j < threads.length; j++) {
      let thread = threads[j]
      // 默认仅订阅未关闭会话
      // if (!thread.closed) {
      let topic = 'thread.' + thread.tid
      subscribeTopic({ commit, state }, topic)
      // }
    }
    // 订阅同事消息
    let topic = 'contact.' + state.user.info.uid
    subscribeTopic({ commit, state }, topic)
    // 订阅个人消息, 因为可能同时登录多个客户端，所以也是订阅topic...
    topic = 'user.' + state.user.info.uid
    subscribeTopic({ commit, state }, topic)
  }, function (error) {
    console.log('连接断开【' + error + '】')
    // 清空订阅topic，以便于重连后添加订阅
    commit(types.CLEAR_TOPIC)
    // 统计连接次数，如果短时间内重试连接次数过多，则说明有可能token过期，需要刷新页面重新登录一下
    if (stompReconnectTimes >= 10) {
      // bus.$emit(constants.EVENT_BUS_LOGOUT, 'logout')
    }
    // 更新连接状态: 断开
    let connectionStatus = constants.STOMP_CONNECTION_STATUS_DISCONNECTED
    commit(types.UPDATE_USER_CONNECTION, { connectionStatus })
    bus.$emit(constants.EVENT_BUS_STOMP_CONNECTION_STATUS, connectionStatus)
    // 10秒后重新连接，实际效果：每10秒重连一次，直到连接成功
    setTimeout(function () {
      console.log('reconnecting...')
      // 增加尝试连接次数
      stompReconnectTimes++
      // 重新连接
      stompConnect({ commit, state })
    }, 10000)
  })
}

